import { derived } from 'svelte/store';
import { language } from '../store';
import { dictionary } from '../i18n/dictionary';

/**
 * Helper function to get translated text
 * @param {string} key - Translation key (e.g., 'nav.home')
 * @param {string} lang - Language code ('it' or 'en')
 * @returns {string} Translated text or key if not found
 */
export function t(key, lang) {
    const entry = dictionary[key];
    if (!entry) return key; // fallback to key if not found
    return entry[lang] || entry['it'] || key; // fallback chain
}

/**
 * Svelte store that provides translation function reactive to language changes
 * Usage in components: $t('nav.home')
 */
export const translate = derived(language, ($language) => {
    return (key) => t(key, $language);
});
