<script>
  import { onMount, onDestroy, tick } from "svelte";
  import { currentTheme, language } from "../../store";
  import { translate } from "../../lib/i18n";
  import { blogPosts as blogPostsRaw } from "../../data/blogPostData";

  // Transform blog posts to current language
  $: blogPosts = blogPostsRaw.map(post => ({
    ...post,
    title: typeof post.title === 'object' ? (post.title[$language] || post.title.it) : post.title,
    excerpt: typeof post.excerpt === 'object' ? (post.excerpt[$language] || post.excerpt.it) : post.excerpt,
    content: typeof post.content === 'object' ? (post.content[$language] || post.content.it) : post.content,
  }));

  let selectedCategory = 'all';
  let searchTerm = '';


  $: categories = [
    { id: 'all', name: $translate('blog.filter.all'), icon: 'fas fa-glass-cheers' },
    { id: 'happy-hour', name: 'Happy Hour', icon: 'fas fa-cocktail' },
    { id: 'progetti', name: $translate('blog.filter.projects'), icon: 'fas fa-film' },
    { id: 'diretta', name: 'Live', icon: 'fas fa-podcast' },
    { id: 'tech-talk', name: 'Tech Talk', icon: 'fas fa-microphone-alt' }
  ];

  $: filteredPosts = blogPosts.filter(post => {
    const matchesCategory = selectedCategory === 'all' || post.category === selectedCategory;
    const matchesSearch = post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         post.excerpt.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         post.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()));
    return matchesCategory && matchesSearch;
  });

  $: featuredPosts = blogPosts.filter(p => p.featured);

  let selectedPost = null;

  async function openPost(post) {
    selectedPost = post;
    post.views += 1; // incremento simulato
    await tick();
    window.scrollTo({ top: 0, behavior: 'instant' });
    const el = document.querySelector('.cinema-screen');
    if (el) el.focus();
  }

  function closePost() {
    selectedPost = null;
  }

  function likePost(post) {
    post.likes += 1;
    // In un'app reale: API call
  }

  function formatDate(dateString) {
    const date = new Date(dateString);
    return date.toLocaleDateString('it-IT', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  }

  function getCategoryStyle(category) {
    const styles = {
      'happy-hour': 'linear-gradient(45deg, #f39c12, #e67e22)',
      'proiezione': 'linear-gradient(45deg, #e74c3c, #c0392b)',
      'diretta': 'linear-gradient(45deg, #9b59b6, #8e44ad)',
      'tech-talk': 'linear-gradient(45deg, #3498db, #2980b9)'
    };
    return styles[category] || 'linear-gradient(45deg, #95a5a6, #7f8c8d)';
  }

  onMount(() => {
    // Animazioni per le card del blog
    const cards = document.querySelectorAll('.story-card');
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry, index) => {
        if (entry.isIntersecting) {
          setTimeout(() => {
            entry.target.style.opacity = '1';
            entry.target.style.transform = 'translateY(0)';
          }, index * 100);
        }
      });
    });

    cards.forEach(card => {
      card.style.opacity = '0';
      card.style.transform = 'translateY(30px)';
      card.style.transition = 'all 0.6s ease';
      observer.observe(card);
    });

    window.addEventListener('keydown', handleKeyDown);
  });

  function handleKeyDown(e) {
    if (e.key === 'Escape' && selectedPost) {
      closePost();
    }
    if (e.key === 'Enter' && e.target?.classList?.contains('story-activator')) {
      const id = e.target.getAttribute('data-post-id');
      const post = blogPosts.find(p => String(p.id) === id);
      if (post) openPost(post);
    }
  }

  onDestroy(() => {
    window.removeEventListener('keydown', handleKeyDown);
  });
</script>

<div class="pub-container" class:dark-theme={$currentTheme === 'dark'}>
  <!-- Hero rimosso -->
  <header class="page-header" role="banner" aria-label="Intestazione Il Bancone delle Storie">
    <div class="ph-inner">
      <div class="ph-titles">
        <h1 class="ph-title">{$translate('blog.hero.title')}</h1>
        <p class="ph-sub">{$translate('blog.hero.subtitle')}</p>
      </div>
      <div class="ph-stats">
        <div class="ph-stat" title="Serate"><span class="label">{$language === 'it' ? 'Articoli' : 'Articles'}</span><span class="val">{blogPosts.length}</span></div>
        <div class="ph-sep"></div>
        <div class="ph-stat" title="Avventori"><span class="label">{$language === 'it' ? 'Visualizzazioni' : 'Views'}</span><span class="val">{blogPosts.reduce((s,p)=>s+p.views,0).toLocaleString()}</span></div>
        <div class="ph-sep"></div>
        <div class="ph-stat" title="Brindisi"><span class="label">Likes</span><span class="val">{blogPosts.reduce((s,p)=>s+p.likes,0)}</span></div>
      </div>
    </div>
  </header>

  <!-- Bar Menu - Filtri -->
  <section class="bar-menu">
    <div class="container">
      <div class="menu-header">
        <h2 class="menu-title"> Articoli</h2>
        <div class="search-bar">
          <i class="fas fa-search"></i>
          <input 
            type="text" 
            placeholder="Cosa ti va di sentire stasera?" 
            bind:value={searchTerm}
          />
        </div>
      </div>
      <div class="menu-categories">
        {#each categories as category}
          <button 
            class="menu-item"
            class:active={selectedCategory === category.id}
            on:click={() => selectedCategory = category.id}
          >
            <div class="item-icon">
              <i class={category.icon}></i>
            </div>
            <span class="item-name">{category.name}</span>
            <span class="item-count">
              {category.id === 'all' ? blogPosts.length : blogPosts.filter(p => p.category === category.id).length}
            </span>
          </button>
        {/each}
      </div>
    </div>
  </section>

  <!-- Stories Timeline - Timeline Verticale -->
  <section class="stories-timeline">
    <div class="container">
      <div class="timeline-wrapper">
        <div class="timeline-line"></div>
        {#each filteredPosts as post, index}
          <div class="timeline-item story-card" class:reverse={index % 2 === 1}>
            <div class="timeline-marker">
              <div class="marker-dot" style="background: {getCategoryStyle(post.category)}">
                <i class={categories.find(c => c.id === post.category)?.icon}></i>
              </div>
            </div>
            <article class="story-record story-activator" tabindex="0" role="button" aria-label={`Apri la storia: ${post.title}`} data-post-id={post.id} on:click={() => openPost(post)}>
              <div class="record-cover">
                <img src={post.image} alt={post.title} />
                <div class="vinyl-effect">
                  <div class="vinyl-center"></div>
                </div>
                <div class="record-overlay">
                  <div class="episode-info">
                    S{post.season}E{post.episode}
                  </div>
                </div>
              </div>
              <div class="record-info">
                <div class="record-header">
                  <div class="category-tag" style="background: {getCategoryStyle(post.category)}">
                    {categories.find(c => c.id === post.category)?.name}
                  </div>
                  <div class="record-date">{formatDate(post.date)}</div>
                </div>
                <h3 class="record-title">{post.title}</h3>
                <p class="record-description">{post.excerpt}</p>
                <div class="record-tags">
                  {#each post.tags as tag}
                    <span class="story-tag">#{tag}</span>
                  {/each}
                </div>
                <div class="record-stats">
                  <div class="stat">
                    <i class="fas fa-headphones"></i>
                    <span>{post.views.toLocaleString()}</span>
                  </div>
                  <div class="stat">
                    <i class="fas fa-heart"></i>
                    <span>{post.likes}</span>
                  </div>
                  <div class="stat">
                    <i class="fas fa-clock"></i>
                    <span>{post.readTime} min</span>
                  </div>
                </div>
              </div>
            </article>
          </div>
        {:else}
          <div class="no-stories">
            <div class="empty-pub">
              <i class="fas fa-glass-whiskey"></i>
              <h3>Il pub  vuoto stasera</h3>
              <p>Prova a cambiare selezione o torna pi tardi</p>
            </div>
          </div>
        {/each}
      </div>
    </div>
  </section>
</div>

<!-- Story Modal - Sala Cinema -->
{#if selectedPost}
  <div class="cinema-backdrop" on:click={closePost}>
    <div class="cinema-screen" tabindex="-1" role="dialog" aria-modal="true" aria-label={selectedPost.title} on:click|stopPropagation>
      <button class="exit-btn" on:click={closePost}>
        <i class="fas fa-times"></i>
      </button>
      <article class="story-content">
        <div class="screen-header">
          <div class="film-strip">
            <div class="strip-holes">
              {#each Array(10) as _}
                <div class="hole"></div>
              {/each}
            </div>
          </div>
          <img src={selectedPost.image} alt={selectedPost.title} class="feature-image" />
          <div class="story-meta-overlay">
            <div class="episode-badge">
              Season {selectedPost.season}  Episode {selectedPost.episode}
            </div>
            <div class="category-badge" style="background: {getCategoryStyle(selectedPost.category)}">
              {categories.find(c => c.id === selectedPost.category)?.name}
            </div>
            <h1 class="story-title">{selectedPost.title}</h1>
            <div class="story-credits">
              <div class="author-credit">
                <div class="credit-info">
                  <strong>Raccontato da {selectedPost.author}</strong>
                  <p> {formatDate(selectedPost.date)}</p>
                </div>
              </div>
              <div class="story-info">
                <div class="info-item">
                  <i class="fas fa-clock"></i>
                  <span>{selectedPost.readTime} min</span>
                </div>
                <div class="info-item">
                  <i class="fas fa-headphones"></i>
                  <span>{selectedPost.views.toLocaleString()}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="story-body">
          <div class="story-text">
            {#each selectedPost.content.split('\n\n') as paragraph}
              <p>{paragraph}</p>
            {/each}
          </div>
          <div class="story-footer">
            <div class="story-tags-section">
              <h4> Tag della storia:</h4>
              <div class="tags-grid">
                {#each selectedPost.tags as tag}
                  <span class="story-tag">#{tag}</span>
                {/each}
              </div>
            </div>
          </div>
        </div>
      </article>
    </div>
  </div>
{/if}

<style>
  /* Palette & CSS Variables */
  :global(:root) {
    /* LIGHT / PANNA CINEMA */
    --accent: #d26b32;               /* arancio bruciato cinema */
    --accent-alt: #b55626;           /* tono pi profondo */
    --accent-gold: #c79c48;          /* oro caldo meno saturo */
    --bg-dark: #f6f3ed;              /* ora sfondo base chiaro */
    --bg-darker: #ece7df;            /* per bande o alternative */
    --surface-1: #ffffff;            /* card base */
    --surface-2: #f4eee6;            /* alt layer */
    --surface-3: #ece4da;            /* hover layer */
    --border: #ddd3c7;
    --border-soft: #e7ded3;
    --text-main: #2d2723;
    --text-dim: #6d6259;
    --text-muted: #a18f80;
    --glass-border: rgba(90,60,30,0.15);
    --radius-lg: 18px;
    --radius-md: 12px;
    --transition-fast: 0.18s cubic-bezier(.4,.0,.2,1);
    --shadow-soft: 0 4px 12px rgba(138,110,80,0.12);
    --shadow-focus: 0 0 0 1px rgba(210,107,50,0.35), 0 4px 18px -2px rgba(210,107,50,0.25);
  }

  /* DARK THEME OVERRIDES */
  :global(.dark-theme) {
    --accent: #d8a657;
    --accent-alt: #2d2a27;
    --accent-gold: #e8c27b;
    --bg-dark: #121212;
    --bg-darker: #0a0a0a;
    --surface-1: #191919;
    --surface-2: #202020;
    --surface-3: #262626;
    --border: #2f2f2f;
    --border-soft: #242424;
    --text-main: #e9e9e9;
    --text-dim: #b3b3b3;
    --text-muted: #8a8a8a;
    --glass-border: rgba(255,255,255,0.06);
    --shadow-soft: 0 4px 12px rgba(0,0,0,0.4);
    --shadow-focus: 0 0 0 1px rgba(216,166,87,0.4), 0 4px 18px -2px rgba(216,166,87,0.25);
  }

  /* Variante chiara (default quando non  dark) */
  .pub-container:not(.dark-theme) {
    --bg-dark: #f5f3ef;
    --surface-1: #f7f6f4;
    --surface-2: #ffffff;
    --surface-3: #f1ede8;
    --border: #e1dbd2;
    --border-soft: #ece6dd;
    --text-dim: #5e564d;
    --text-muted: #8b8076;
    --accent: #b2772d;
    --accent-gold: #b2772d;
    --accent-alt: #ebe4da;
    --shadow-soft: 0 4px 10px rgba(0,0,0,0.08);
    --shadow-focus: 0 0 0 1px rgba(178,119,45,0.35), 0 4px 16px -2px rgba(178,119,45,0.25);
    color: #2d2a27;
    background: linear-gradient(#faf9f7,#f3f1ed);
  }
  .pub-container:not(.dark-theme) .page-header { background: linear-gradient(140deg, rgba(255,255,255,0.82), rgba(247,243,238,0.78)); }
  .pub-container:not(.dark-theme) .ph-title { color:#3b342d; }
  .pub-container:not(.dark-theme) .ph-sub { color: var(--text-muted); }
  .pub-container:not(.dark-theme) .featured-stories,
  .pub-container:not(.dark-theme) .stories-timeline,
  .pub-container:not(.dark-theme) .bar-menu { background: var(--surface-1); }
  .pub-container:not(.dark-theme) .movie-poster,
  .pub-container:not(.dark-theme) .story-record { background: var(--surface-2); }
  .pub-container:not(.dark-theme) .movie-poster:hover,
  .pub-container:not(.dark-theme) .story-record:hover { background: var(--surface-3); }
  .pub-container:not(.dark-theme) .poster-info h3,
  .pub-container:not(.dark-theme) .record-title { color:#2f2b27; }
  .pub-container:not(.dark-theme) .story-excerpt,
  .pub-container:not(.dark-theme) .record-description { color: var(--text-dim); }
  .pub-container:not(.dark-theme) .story-meta,
  .pub-container:not(.dark-theme) .record-stats { color: var(--text-muted); }
  .pub-container:not(.dark-theme) .category-badge,
  .pub-container:not(.dark-theme) .category-tag { color: #68441b; background: rgba(178,119,45,0.08); border-color: var(--border); }
  .pub-container:not(.dark-theme) .marker-dot { background: var(--surface-2); color: var(--accent); border-color: var(--border); }
  .pub-container:not(.dark-theme) .action-btn { background: var(--surface-2); color:#3a352f; }
  .pub-container:not(.dark-theme) .action-btn:hover { background: var(--surface-3); }
  .pub-container:not(.dark-theme) .like-btn:hover { background:#c75e2b; border-color:#c75e2b; color:#fff; }
  .pub-container:not(.dark-theme) .search-bar input { background: var(--surface-2); color:#2d2a27; }
  .pub-container:not(.dark-theme) .search-bar input:focus { background: var(--surface-3); }
  .pub-container:not(.dark-theme) .menu-item { background: var(--surface-2); color:#3a352f; }
  .pub-container:not(.dark-theme) .menu-item.active { background: var(--surface-3); }
  .pub-container:not(.dark-theme) .story-tag { background: var(--surface-3); color: var(--accent); border-color: var(--border); }
  .pub-container:not(.dark-theme) .vinyl-effect { background: rgba(255,255,255,0.55); }
  .pub-container:not(.dark-theme) .vinyl-center { box-shadow:0 0 0 4px rgba(178,119,45,0.25),0 0 0 8px rgba(178,119,45,0.12); }

  .pub-container {
    padding-top: 16px;
    background: linear-gradient(to bottom, var(--bg-dark), var(--bg-darker));
    min-height: 100vh;
    color: var(--text-main);
    font-family: 'Georgia', serif;
    position: relative;
    isolation: isolate;
  }

  .pub-container::before { 
    content: ""; position:absolute; inset:0; pointer-events:none;
    background:
      linear-gradient(to bottom, rgba(0,0,0,0.04), transparent 220px),
      radial-gradient(circle at 60% 0%, rgba(210,107,50,0.15), transparent 55%),
      repeating-linear-gradient(90deg, rgba(0,0,0,0.015) 0 2px, transparent 2px 4px);
    mix-blend-mode: multiply;
  }
  /* fine grana pellicola */
  .pub-container::after {
    content:""; position:absolute; inset:0; pointer-events:none; opacity:.22; mix-blend-mode:overlay;
    background: repeating-conic-gradient(from 0deg, rgba(0,0,0,0.015) 0deg 10deg, rgba(0,0,0,0.04) 10deg 20deg);
    mask: linear-gradient(to bottom, transparent, #000 8%, #000 92%, transparent);
  }

  .dark-theme.pub-container { background: var(--bg-darker); }

  /* Hero rimosso: rimossi stili pub-hero, neon, bubbles, stats */

  /* Featured Stories - Locandine */
  .featured-stories { padding: 48px 0 56px; background: var(--surface-1); border-top:1px solid var(--border-soft); border-bottom:1px solid var(--border-soft);} 
  .dark-theme .featured-stories { background: var(--surface-1); }

  .section-header { text-align:center; margin: 0 0 2.5rem; }

  .section-title { font-size: 2rem; color: var(--accent-alt); margin:0 0 .5rem; font-weight:600; letter-spacing:.5px; }

  .section-subtitle { font-size: .8rem; text-transform:uppercase; letter-spacing:2px; color: var(--text-dim); font-style:normal; font-weight:500; }

  .featured-grid { display:grid; grid-template-columns:repeat(auto-fit,minmax(300px,1fr)); gap:1.5rem; max-width:1180px; margin:0 auto; padding:0 1.25rem; }

  .movie-poster { background: linear-gradient(145deg,var(--surface-2),var(--surface-1)); border-radius: var(--radius-lg); overflow:hidden; cursor:pointer; transition: transform .5s cubic-bezier(.3,.7,.4,1), background var(--transition-fast), border-color var(--transition-fast); border:1px solid var(--border); position:relative; will-change:transform; }

  .movie-poster:hover,
  .movie-poster:focus-visible { transform: translateY(-6px); border-color: var(--accent-alt); box-shadow: var(--shadow-focus); outline:none; background: var(--surface-3); }

  .poster-frame { position:relative; height:200px; overflow:hidden; }

  .poster-frame img { width:100%; height:100%; object-fit:cover; transition: transform .7s cubic-bezier(.3,.7,.4,1); filter:contrast(1.02) saturate(1); }

  .movie-poster:hover .poster-frame img { transform:scale(1.08); filter:saturate(1); }

  .poster-overlay { position:absolute; inset:0; background:linear-gradient(130deg, rgba(255,255,255,0), rgba(0,0,0,0.45)); display:flex; align-items:center; justify-content:center; opacity:0; transition:opacity .35s ease; }
  .movie-poster:hover .poster-overlay { opacity:1; }

  .episode-badge { position:absolute; top:12px; right:12px; background:rgba(255,255,255,.55); backdrop-filter:blur(4px); color:var(--accent-alt); padding:.35rem .75rem; border:1px solid rgba(0,0,0,0.06); border-radius:999px; font-weight:600; font-size:.65rem; letter-spacing:.5px; }

  .play-btn { background: rgba(255,255,255,.6); border:1px solid rgba(0,0,0,0.08); width:54px; height:54px; border-radius:50%; display:flex; align-items:center; justify-content:center; font-size:1.1rem; color:var(--accent-alt); cursor:pointer; transition:background var(--transition-fast), transform var(--transition-fast), color var(--transition-fast); backdrop-filter:blur(6px); }

  .play-btn:hover,
  .play-btn:focus-visible { transform:scale(1.08); background:var(--accent-alt); color:#fff; outline:none; }

  .poster-info { padding:1.1rem 1.15rem 1.25rem; }

  .category-badge { display:inline-flex; align-items:center; gap:.4ch; color:var(--accent-alt); padding:.3rem .7rem; border-radius: 999px; font-size:.6rem; font-weight:600; letter-spacing:.5px; text-transform:uppercase; background:rgba(210,107,50,0.08); border:1px solid var(--border); margin-bottom:.65rem; }

  .poster-info h3 { color:var(--text-main); font-size:1.05rem; margin:0 0 .5rem; line-height:1.35; font-weight:600; }

  .story-excerpt { color:var(--text-dim); line-height:1.45; font-size:.83rem; margin:0 0 .65rem; }

  .story-meta { display:flex; justify-content:space-between; font-size:.62rem; text-transform:uppercase; letter-spacing:1px; color:var(--text-muted); }

  /* Bar Menu - Filtri */
  .bar-menu { padding:42px 0 50px; background: var(--surface-2); border-top:1px solid var(--border-soft); border-bottom:1px solid var(--border-soft); }

  .menu-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3rem;
    flex-wrap: wrap;
    gap: 2rem;
  }

  .menu-title { font-size:1.35rem; color: var(--accent-alt); font-weight:600; letter-spacing:.5px; margin:0; }

  .search-bar {
    position: relative;
    width: 350px;
    max-width: 100%;
  }

  .search-bar i {
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    color: #999;
  }

  .search-bar input { width:100%; padding:12px 14px 12px 44px; border:1px solid var(--border); border-radius: 10px; background: var(--surface-1); color:var(--text-main); font-size:.9rem; transition: border-color var(--transition-fast), background var(--transition-fast); }

  .search-bar input:focus { outline:none; border-color: var(--accent-alt); background: var(--surface-2); }

  .menu-categories {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
  }

  .menu-item { background: var(--surface-1); border:1px solid var(--border); padding:.8rem 1.05rem; border-radius: var(--radius-md); color:var(--text-main); cursor:pointer; transition: background var(--transition-fast), border-color var(--transition-fast), transform var(--transition-fast); display:flex; align-items:center; gap:.75rem; font-weight:500; min-width:140px; justify-content:space-between; font-size:.78rem; letter-spacing:.5px; }

  .menu-item:hover,
  .menu-item:focus-visible,
  .menu-item.active { background: var(--surface-2); border-color: var(--accent-alt); transform: translateY(-2px); outline:none; box-shadow: var(--shadow-soft); }

  .item-icon {
    font-size: 1.2rem;
  }

  .item-count { background: rgba(255,255,255,0.05); padding:.25rem .55rem; border-radius: 999px; font-size:.65rem; letter-spacing:.5px; }

  /* Stories Timeline */
  .stories-timeline { background: var(--surface-1); }
  .dark-theme .stories-timeline { background: var(--surface-1); }

  .timeline-wrapper {
    position: relative;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 2rem;
  }

