import { writable, derived } from 'svelte/store';

// Routing principale
export let segment = writable('home');
export let currentTheme = writable('sunset');
export let isLoading = writable(true);

// i18n - Language store con sync query string
function createLanguageStore() {
    // Leggi dalla query string o usa default 'it'
    const params = new URLSearchParams(window.location.search);
    const initialLang = params.get('lang') || 'it';
    
    const { subscribe, set, update } = writable(initialLang);
    
    return {
        subscribe,
        set: (lang) => {
            set(lang);
            // Aggiorna query string
            const url = new URL(window.location);
            url.searchParams.set('lang', lang);
            window.history.replaceState({}, '', url);
        },
        update
    };
}

export const language = createLanguageStore();

// Nuovo: navigazione "mappa / rotte"
export const selectedRoute = writable(null);          // id della rotta scelta (es. 'grand_line')
export const routeProgress = writable(0);             // indice dell'isola corrente lungo la rotta
export const visitedIslands = writable([]);           // array di id isole visitate (ordine cronologico)
export const autoTravel = writable(false);            // se true avanza automaticamente

// Temi disponibili (immutabile)
export const themes = [
    { id: 'sunset', name: 'Sunset', icon: '', description: 'Arancione e rosa caldi' },
    { id: 'light', name: 'Daylight', icon: '', description: 'Chiaro e pulito' },
    { id: 'forest', name: 'Forest', icon: '', description: 'Verde naturale' },
    { id: 'aurora', name: 'Aurora', icon: '', description: 'Blu e viola eleganti' },
    { id: 'dark', name: 'Midnight', icon: '', description: 'Scuro elegante' },
];

// Definizione condivisa delle isole (coordinate percentuali riferite al container)
export const islands = [
    { id: 'home', label: 'Base', segment: 'home', x: 12, y: 68, icon: 'fa-anchor' },
    { id: 'about', label: 'About', segment: 'about', x: 29.11, y: 75.40, icon: 'fa-user' },
    { id: 'passions', label: 'Passions', segment: 'passions', x: 43, y: 59, icon: 'fa-heart' },
    { id: 'skills', label: 'Skills', segment: 'skills', x: 54, y: 70, icon: 'fa-dragon' },
    { id: 'cv', label: 'CV', segment: 'cv', x: 64, y: 48, icon: 'fa-file-alt' },
    { id: 'projects', label: 'Projects', segment: 'projects', x: 75, y: 60, icon: 'fa-hammer' },
    { id: 'blog', label: 'Blog', segment: 'blog', x: 83, y: 40, icon: 'fa-feather' },
    { id: 'contact', label: 'Contact', segment: 'contact', x: 91, y: 55, icon: 'fa-envelope' },
];

// Rotte predefinite (ordini diversi delle stesse isole)
export const routes = {
    grand_line: ['home', 'about', 'projects', 'blog', 'cv', 'passions', 'skills', 'contact'],
    calm_belt: ['home', 'projects', 'cv', 'about', 'blog', 'skills', 'passions', 'contact'],
    new_world: ['home', 'blog', 'projects', 'skills', 'passions', 'about', 'cv', 'contact']
};

// Derivato: elenco isole della rotta selezionata
export const activeRouteIslands = derived([selectedRoute], ([$selectedRoute]) => {
    if (!$selectedRoute) return [];
    return routes[$selectedRoute] || [];
});

// Derivato: id isola corrente se segment corrisponde
export const currentIslandId = derived([segment], ([$segment]) => {
    const found = islands.find(i => i.segment === $segment);
    return found ? found.id : null;
});

// Helper per avanzare alla prossima isola della rotta
export function advanceRoute() {
    let currentRoute; let progress;
    selectedRoute.update(r => { currentRoute = r; return r; });
    routeProgress.update(p => { progress = p; return p; });
    if (!currentRoute) return;
    const list = routes[currentRoute];
    if (!list) return;
    const nextIndex = progress + 1;
    if (nextIndex < list.length) {
        routeProgress.set(nextIndex);
        const nextId = list[nextIndex];
        const island = islands.find(i => i.id === nextId);
        if (island) {
            segment.set(island.segment);
            visitedIslands.update(v => v.includes(nextId) ? v : [...v, nextId]);
        }
    } else {
        autoTravel.set(false); // termine rotta
    }
}

export function resetRouteProgress() {
    routeProgress.set(0);
    visitedIslands.set(['home']);
}
