<script>
  import { onMount, tick } from "svelte";
  import { currentTheme, language } from "../../store";
  import { translate } from "../../lib/i18n";
  import { categoryScoresData } from "../../data/passionsData";
  import anime from "animejs/lib/anime.es.js";

  let selectedPassion = null;

  let passions = [
    {
      id: "projects",
      title: "Projects",
      icon: "",
      musicNote: "Re",
      notePosition: 2,
      color: "#27ae60",
      description:
        "Progetti personali in corso: giochi, musica e idee in lavorazione.",
      fullDescription: `Qui tengo traccia dei miei progetti in corso tra sviluppo giochi e musica. Alcuni sono quasi pronti, altri sono agli inizi, ma tutti rappresentano il mio percorso creativo.`,
      image: "/images/passions/projects.jpg",
      activities: [
        "Pirate's Dawn: The Hidden Tide",
        "Stay (canzone mia in progetto, quasi ultimata)",
        "Gdrloop (progetto di gioco mio, sviluppo quasi ultimato)",
        "Terzo gioco senza nome (in sviluppo, all'inizio)",
        "Canzoni varie (in progetto)",
      ],
    },
    {
      id: "game-dev",
      title: "Sviluppo di Videogiochi",
      icon: "",
      musicNote: "Mi",
      notePosition: 3,
      color: "#e67e22",
      description:
        "La mia vera passione: trasformare idee in esperienze interattive e mondi digitali.",
      fullDescription: `Lo sviluppo di videogiochi  il campo dove la mia creativit e le mie competenze tecniche si fondono. Sono affascinato dalla possibilit di creare mondi, personaggi e storie che possano coinvolgere ed emozionare i giocatori. Ho iniziato a sperimentare con lo sviluppo di giochi per puro interesse personale, ma  diventato rapidamente il mio principale focus creativo.

      Mi piace occuparmi di diversi aspetti del game development, dalla programmazione delle meccaniche di gioco al design dei livelli, fino all'integrazione di asset grafici e sonori. Utilizzo principalmente la libreria GTGE con Python per i miei progetti personali, ma ho anche esplorato altri motori e framework.

      Attualmente sto lavorando a un gioco di ruolo PvP per dispositivi mobili, un progetto ambizioso che mi sta permettendo di approfondire le mie conoscenze in ambito di networking, intelligenza artificiale per gli NPC e bilanciamento del gameplay.`,
      image: "/images/passions/gaming.jpg",
      activities: [
        "Sviluppo del gioco PvP di ruolo per mobile (work in progress)",
        "Studio di game design e meccaniche di gioco innovative",
        "Prototipazione rapida di idee di gioco",
        "Utilizzo di Python e della libreria GTGE per lo sviluppo",
        "Esplorazione di TensorFlow per l'IA dei nemici",
      ],
    },
    {
      id: "anime-manga",
      title: "Anime & TV Show",
      icon: "",
      musicNote: "Fa",
      notePosition: 4,
      color: "#f39c12",
      description:
        "Appassionato di storie epiche come One Piece e del mondo dell'animazione giapponese.",
      fullDescription: `Gli anime e i manga sono una fonte costante di ispirazione per la mia creativit. Sono particolarmente affascinato dalle storie epiche e dai mondi complessi creati da autori come Eiichiro Oda con One Piece. Queste opere mi insegnano l'importanza della narrazione, dello sviluppo dei personaggi e della costruzione di universi coerenti e affascinanti.

      Questo interesse si riflette nel mio approccio al game development, dove cerco sempre di creare storie coinvolgenti e personaggi memorabili. L'estetica e i temi degli anime influenzano anche i miei progetti creativi, dall'arte concettuale alla scelta delle meccaniche di gioco.`,
      image: "/images/passions/anime.jpg",
      activities: [
        "Lettura settimanale dei capitoli di One Piece",
        "Studio delle tecniche narrative degli anime",
        "Analisi del character design giapponese",
        "Ispirazione per i progetti di game development",
      ],
    },
    {
      id: "film",
      title: "Film",
      icon: "",
      musicNote: "Sol",
      notePosition: 5,
      color: "#e84393",
      description:
        "Cinema e storie che mi hanno segnato: dai cult d'azione alla fantascienza e all'avventura.",
      fullDescription: `Il cinema  una delle mie fonti principali di ispirazione visiva e narrativa. Dai film d'azione di Tarantino alle epopee fantascientifiche, ogni pellicola lascia un segno nel mio modo di pensare e creare. Mi piace analizzare regia, fotografia e sceneggiatura, e spesso traggo idee per i miei progetti personali proprio da queste opere.`,
      image: "/images/passions/film.jpg",
      activities: [
        "Visione e analisi di film iconici",
        "Discussioni su regia e sceneggiatura",
        "Collezione di colonne sonore memorabili",
        "Ispirazione per storytelling e worldbuilding",
      ],
    },
    {
      id: "music-production",
      title: "Produzione Musicale",
      icon: "",
      musicNote: "Do",
      notePosition: 1, // posizione sul pentagramma
      color: "#9b59b6",
      description:
        "Compongo e produco musica elettronica, suono il pianoforte e utilizzo software come FL Studio.",
      fullDescription: `La musica  una parte fondamentale della mia vita. Suono il pianoforte da molti anni e questa passione mi ha portato a esplorare il mondo della produzione musicale digitale. Utilizzo FL Studio per comporre e arrangiare brani, sperimentando con diversi generi che spaziano dalla musica elettronica all'ambient.

      La produzione musicale mi ha insegnato l'importanza della pazienza, della cura dei dettagli e della struttura armonica, competenze che trovo sorprendentemente utili anche nella scrittura di codice pulito e ben organizzato. Vedo molte somiglianze tra la composizione di una traccia e la progettazione di un'applicazione: entrambe richiedono una visione d'insieme, una solida architettura e la capacit di far funzionare insieme tanti piccoli elementi.`,
      image: "/images/passions/music.jpg",
      activities: [
        "Piano",
        "Sax",
        "FL Studio",
        "Creazione di colonne sonore per i miei progetti di gioco",
        "Generi preferiti: alternative rock, dance, soundtrack, pop, rock, metal, italiane d'autore, ballad",
        "Ascolto massivo di cover su YouTube",
      ],
    },
    {
      id: "hobby",
      title: "Hobby",
      icon: "",
      musicNote: "La",
      notePosition: 6,
      color: "#3498db",
      description: "Piccole passioni quotidiane: musica, sport e relax.",
      fullDescription: `Suonare, uscire e ascoltare podcast sono piccoli rituali che rigenerano la mente e la creativit. Amo il mare, le serate con amici e suonare il pianoforte quando trovo tempo.`,
      image: "/images/passions/hobby.jpg",
      activities: [
        "Suonare il piano",
        "Giocare a calcio",
        "Birra",
        "Podcast",
        "Mare (spiaggia)",
      ],
    },
  ];

  // Per-category audio mapping. Place files under public/audio/...
  // Example paths: /audio/music-production.mp3, etc.
  const AUDIO_MAP = {
    "music-production": "/sounds/numb.mp3",
    "game-dev": "/sounds/skyrim.mp3",
    "anime-manga": "/sounds/onepiece.mp3",
    film: "/sounds/harry.mp3",
    projects: "/sounds/tetris.mp3",
    hobby: "/sounds/doremi.mp3",
  };

  // Helpers per posizionare le note correttamente sul pentagramma (chiave di violino)
  // Riferimenti (dal basso verso l'alto):
  // Linee:  E4 (Mi)  G4  B4  D5 (Re)  F5 (Fa)
  // Spazi:  F4 (Fa)  A4  C5 (Do)  E5 (Mi)
  // Indici di passo (mezzi-spazi): 0 = E4 (linea bassa) ... 8 = F5 (linea alta)
  const NOTE_STEPS = {
    Do: 5, // C5 (3 spazio)
    "Do#": 5, // C# (same vertical position as Do)
    Re: 6, // D5 (4 linea)
    Mi: 7, // E5 (4 spazio)
    Fa: 8, // F5 (5 linea)
    "Fa#": 8, // treat F# similar vertical position to Fa (same staff line)
  Sol: 2, // G4 (default)
  "Sol#": 2, // G# (treated same vertical position as Sol)
    La: 3, // A4
    Si: 4, // B4
  };

  const STAFF_HEIGHT = 200; // altezza del pentagramma (in .staff)
  const CONTAINER_HEIGHT = 300; // altezza della staff-container
  const NOTE_SIZE = 80; // diametro della nota (note-head)

  function getNoteStep(note) {
    return NOTE_STEPS[note] ?? 4; // default: centro
  }

  function getNoteTop(note) {
    const step = getNoteStep(note);
    const stepPx = STAFF_HEIGHT / 8; // 8 mezze altezze tra linee
    const staffTop = (CONTAINER_HEIGHT - STAFF_HEIGHT) / 2; // 50px
    // Coordinate verticale del centro della nota rispetto alla staff-container
    const yWithinStaff = STAFF_HEIGHT - step * stepPx;
    const centerY = staffTop + yWithinStaff;
    return centerY; // Torniamo il centro; il contenitore sar centrato via transform
  }

  function getNoteLeft(index, total) {
    // Distribuisce le note come frazioni della larghezza (evita i bordi)
    return ((index + 1) / (total + 1)) * 100;
  }

  function openPassionModal(passion) {
    selectedPassion = passion;
  }

  function closePassionModal() {
    selectedPassion = null;
  }

  // Category / score mapping: each passion exposes a small "partitura" of rich note objects
  // Each note: { id, title, short, description, image }
  // Made reactive to support multilingual content
  $: categoryScores = Object.keys(categoryScoresData).reduce((acc, catKey) => {
    acc[catKey] = categoryScoresData[catKey].map(note => ({
      ...note,
      title: typeof note.title === 'object' ? note.title[$language] || note.title.it : note.title,
      short: typeof note.short === 'object' ? note.short[$language] || note.short.it : note.short,
      description: typeof note.description === 'object' ? note.description[$language] || note.description.it : note.description,
    }));
    return acc;
  }, {});

  // Short labels per category for the mixer buttons
  const CATEGORY_SHORT = {
    "music-production": "MUSIC",
    "game-dev": "GAMES",
    "anime-manga": "ANIME",
    film: "FILM",
    projects: "PROJECTS",
    hobby: "HOBBY",
  };

  // Melody mapping per categoria per definire le altezze della partitura
  // Usa nomi note italiani (Do, Re, Mi, Fa, Sol, La)
  const categoryMelodies = {
    "music-production": ["DO#", "RE", "DO#", "FA#", "LA", "SOL#"],
    "anime-manga": ["LA", "LA", "SOL", "SOL", "MI", "DO", "LA"],
    film: ["SI", "MI", "SOL", "FA#", "MI"],
    "game-dev": ["RE", "DO#", "RE", "RE", "DO#", "RE"],
    projects: ["MI", "SI", "DO", "RE", "DO"],
  hobby: ["DO", "RE", "MI", "FA", "SOL"],
  };

  function normalizeNote(n) {
    if (!n) return null;
    const key = String(n).toLowerCase();
    const map = {
      do: "Do",
      "do#": "Do#",
      re: "Re",
      mi: "Mi",
      fa: "Fa",
      "fa#": "Fa#",
      sol: "Sol",
      "sol#": "Sol#",
      la: "La",
      si: "Si",
    };
    return map[key] || null;
  }

  // Category-specific note step overrides (useful to make 'Mi' low for anime category)
  const categoryNoteOverrides = {
    "anime-manga": {
      Mi: 0, // use E4 (low Mi) instead of the default high Mi
    },
    hobby: {
  Sol: 9, // slightly higher than top line for hobby melodies
    },
  };

  // Configurazione timing per l'animazione per-categoria (gap tra una nota e la successiva in ms)
  // Puoi personalizzare i valori per ogni categoria; se non specificato, verr usato un gap di default.
  const categoryAnimationTimings = {
    "music-production": [120, 120, 120, 120, 120],
    projects: [100, 60, 60, 80, 120],
    "game-dev": [5, 5, 5, 5, 5, 5],
    "anime-manga": [120, 120, 600, 60, 80, 80, 80],
    film: [150, 150, 150, 150, 150],
    hobby: [130, 130, 130, 130, 130],
  };

  const DEFAULT_NOTE_GAP = 120; // ms

  // Riferimento all'ultima timeline per poterla interrompere se l'utente cambia categoria rapidamente
  let currentCascadeTimeline = null;

  // Rimuove eventuali animazioni in corso e ripristina stile base
  function resetNoteAnimationState(container) {
    if (!container) return;
    const heads = container.querySelectorAll(".note-head");
    const glows = container.querySelectorAll(".note-glow");
    heads.forEach((h) => {
      anime.remove(h);
      h.style.transform = "";
    });
    glows.forEach((g) => {
      anime.remove(g);
      g.style.transform = "";
      g.style.opacity = "";
    });
  }

  // Anima in cascata le note della staff corrente: ognuna si ingrandisce leggermente e si illumina, poi torna allo stato iniziale
  // timings: array di gap (ms) tra una nota e la successiva, oppure numero singolo per gap costante
  // animateNotesCascade:
  //  - timings: array di gap (durata di attesa aggiuntiva) tra una nota e la successiva.
  //             Se vuoi un gap uniforme puoi passare un numero singolo.
  //             Ogni nota impiega ~360ms (180 up + 180 down). Il gap viene sommato dopo quei 360ms.
  //             Per avere start times quasi consecutivi riduci il gap; per pi respiro aumentalo.
  function animateNotesCascade(timings) {
    const container = document.querySelector(".notes-container");
    if (!container) return;
    const notes = container.querySelectorAll(".musical-note-passion");
    if (!notes || notes.length === 0) return;

    // reset stato
    resetNoteAnimationState(container);
    if (currentCascadeTimeline) {
      try {
        currentCascadeTimeline.pause();
      } catch {}
      currentCascadeTimeline = null;
    }
    const timeline = anime.timeline({ autoplay: true, easing: "easeOutQuad" });
    currentCascadeTimeline = timeline;
    let offset = 0;

    notes.forEach((note, idx) => {
      const head = note.querySelector(".note-head");
      const glow = note.querySelector(".note-glow");
      const gap = Array.isArray(timings)
        ? (timings[idx] ?? DEFAULT_NOTE_GAP)
        : typeof timings === "number"
          ? timings
          : DEFAULT_NOTE_GAP;

      // fase up (ingrandisce e illumina)
      if (head) {
        timeline.add(
          {
            targets: head,
            scale: [1, 1.08],
            duration: 180,
          },
          offset
        );
      }
      if (glow) {
        timeline.add(
          {
            targets: glow,
            opacity: [0.45, 0.85],
            scale: [1, 1.06],
            duration: 180,
          },
          offset
        );
      }

      // fase down (ritorno allo stato iniziale)
      if (head) {
        timeline.add(
          {
            targets: head,
            scale: 1,
            duration: 180,
          },
          offset + 180
        );
      }
      if (glow) {
        timeline.add(
          {
            targets: glow,
            opacity: [0.85, 0.45],
            scale: 1,
            duration: 180,
          },
          offset + 180
        );
      }

      // aggiorna l'offset per la prossima nota: durata animazione (360ms) + gap scelto
      offset += 360 + gap;
    });
  }

  // Cache of HTMLAudioElement per category
  const audioPlayers = {};
  let playbackProgress = 0; // 0..100
  let playbackDuration = 0; // seconds

  function getAudioFor(id) {
    const src = AUDIO_MAP[id];
    if (!src) return null;
    if (!audioPlayers[id]) {
      const a = new Audio(src);
      a.preload = "auto";
      a.loop = false; // non in loop
      a.addEventListener("ended", () => {
        // clear playing state when finishes naturally
        if (playingCategory === id) {
          playingCategory = null;
        }
        playbackProgress = 0;
      });
      // Wire progress updates once
      // @ts-ignore - attach a flag on the element to avoid duplicate listeners
      a._wired = true;
      a.addEventListener("timeupdate", () => {
        if (!isNaN(a.duration) && a.duration > 0) {
          playbackDuration = a.duration;
          playbackProgress = Math.max(
            0,
            Math.min(100, (a.currentTime / a.duration) * 100)
          );
        }
      });
      a.addEventListener("loadedmetadata", () => {
        if (!isNaN(a.duration) && a.duration > 0) {
          playbackDuration = a.duration;
        }
      });
      audioPlayers[id] = a;
    }
    return audioPlayers[id];
  }

  function stopAllAudio() {
    Object.values(audioPlayers).forEach((a) => {
      try {
        a.pause();
        a.currentTime = 0;
      } catch {}
    });
    playbackProgress = 0;
  }

  let activeCategory = null; // when set, staff shows that category's score

  // play control state for mixer
  let playingCategory = null;

  function togglePlay(id) {
    if (!id) return;

    // If same category is playing, stop it and reset state
    if (playingCategory === id) {
      const current = audioPlayers[id];
      if (current) {
        try {
          current.pause();
          current.currentTime = 0;
        } catch {}
      }
      playingCategory = null;
      // keep previous behavior: also deactivate score view when stopping via button
      activeCategory = null;
      playbackProgress = 0;
      return;
    }

    // Switch playback to the requested category
    stopAllAudio();
    playingCategory = id;
    activeCategory = id;

    const audio = getAudioFor(id);
    if (!audio) {
      console.warn(`Nessun file audio mappato per la categoria: ${id}`);
      return;
    }
    // Apply volume from fader (0..100 -> 0..1)
    const level = Math.min(100, Math.max(0, trackState[id]?.level ?? 80));
    audio.volume = level / 100;
    try {
      playbackProgress = 0;
      playbackDuration = 0;
      audio.currentTime = 0;
      const p = audio.play();
      if (p && typeof p.then === "function") {
        p.catch((err) => console.warn("Audio play failed:", err));
      }
    } catch (err) {
      console.warn("Audio play threw:", err);
    }

    // Avvia l'animazione in cascata delle note dopo il render della staff
    (async () => {
      try {
        await tick();
        const timings = categoryAnimationTimings[id];
        animateNotesCascade(timings);
      } catch {}
    })();
  }

  // per ogni traccia manteniamo uno stato (mute/solo/level)
  let trackState = {};
  passions.forEach((p) => {
    trackState[p.id] = { mute: false, solo: false, level: 80 };
  });

  function getTopForStep(step) {
    // step: 0..8 (same scale used before)
    const stepPx = STAFF_HEIGHT / 8;
    const staffTop = (CONTAINER_HEIGHT - STAFF_HEIGHT) / 2;
    return staffTop + (STAFF_HEIGHT - step * stepPx);
  }

  // Calcola l'opacit visuale di una nota basata sullo stato della traccia (mute/solo/level)
  function getNoteOpacity(note) {
    // determine track id: for passion notes it's the passion id; for score notes it's the activeCategory
    const trackId = note.source === "passion" ? note.id : activeCategory;
    if (!trackId || !trackState[trackId]) return 1;
    const state = trackState[trackId];
    if (state.mute) return 0;
    const base = (state.level ?? 80) / 100;
    const anySolo = Object.values(trackState).some((s) => s.solo);
    // Ensure a minimum visible opacity so notes are not too faint by default
    const MIN_VISIBLE = 0.6;
    if (anySolo) {
      // non-solo tracks remain dim, solo track gets boosted with a minimum
      return state.solo ? Math.max(MIN_VISIBLE, base) : 0.15;
    }
    return Math.max(MIN_VISIBLE, base);
  }

  // Reactive array of notes to render on the staff. Each item: {id?, label, color, x, y}
  $: displayNotes = (function () {
    // Do not render the passion notes by default on page load.
    // Notes will appear only when a category is selected (activeCategory set).
    if (!activeCategory) {
      return [];
    }

    const passion = passions.find((p) => p.id === activeCategory);
    const notes = categoryScores[activeCategory] || [];
    const total = notes.length || 1;
    return notes.map((label, i) => {
      // Usa la melodia se definita per determinare l'altezza della nota, altrimenti distribuisce.
      const melody = categoryMelodies[activeCategory];
      let step;
      let nn = null;
      if (melody && melody.length) {
        nn = normalizeNote(melody[i % melody.length]);
        if (nn) {
          // allow category overrides (e.g., Mi basso for anime-manga)
          const overrides = categoryNoteOverrides[activeCategory] || {};
          const overrideStep = overrides[nn];
          const rawStep =
            typeof overrideStep === "number" ? overrideStep : getNoteStep(nn);
          // clamp 0..8 (step 0 corresponds to low E4)
          step = Math.max(0, Math.min(8, rawStep));
        } else {
          step = Math.max(
            0,
            Math.min(8, Math.round(((i + 1) / (total + 1)) * 8))
          );
        }
      } else {
        step = Math.max(
          0,
          Math.min(8, Math.round(((i + 1) / (total + 1)) * 8))
        );
      }
      return {
        id: `${activeCategory}-${i}`,
        label: label.title || label.short || label,
        color: passion ? passion.color : "#9b59b6",
        x: getNoteLeft(i, total),
        y: getTopForStep(step),
        source: "score",
        musicNote: nn,
        noteMeta: {
          id: `${activeCategory}-${label.id || i}`,
          title: label.title || label,
          short:
            label.short || (label.title ? label.title.split(" ")[0] : label),
          description: label.description || "",
          image: label.image || (passion ? passion.image : null),
        },
      };
    });
  })();

  // Accessibility: handle Enter/Space activation on note elements
  function onNoteKey(e, note) {
    if (e.key === "Enter" || e.key === " ") {
      e.preventDefault();
      if (note.source === "passion")
        openPassionModal(passions.find((p) => p.id === note.id));
    }
  }

  onMount(() => {
    // No entrance animations or ripples for notes: they should appear instantly.
    // Keep audio cleanup on unmount.
    return () => {
      stopAllAudio();
    };
  });
</script>

<div class="passions-container" class:dark-theme={$currentTheme === "dark"}>
  <!-- Minimal Header + Mixer -->
  <section class="header-mixer">
    <div class="container">
      <!-- Minimal Header -->
      <div class="minimal-header">
        <span class="clef-icon"></span>
        <h1 class="page-title">
          {#if $language === 'it'}
            Lo <span class="accent">Spartito</span> delle mie Passioni
          {:else}
            The <span class="accent">Score</span> of my Passions
          {/if}
        </h1>
      </div>

      <!-- Mixer Controls -->
      <div class="mixer-row">
        <!-- Controls Left -->
        <div class="mixer-controls-left">
          <button
            class="btn-play"
            on:click={() => togglePlay(activeCategory)}
            disabled={!activeCategory}
            aria-pressed={!!(
              playingCategory &&
              activeCategory &&
              playingCategory === activeCategory
            )}
          >
            {#if playingCategory && activeCategory && playingCategory === activeCategory}
              
            {:else}
              
            {/if}
          </button>
          <input
            class="progress-bar"
            type="range"
            min="0"
            max="100"
            value={playbackProgress}
            aria-label="Avanzamento traccia"
            disabled
          />
        </div>

        <!-- Center Label -->
        <div class="mixer-center-label">
          <span class="select-track-text">{$language === 'it' ? 'Seleziona la traccia' : 'Select track'}</span>
          <span class="arrow-right" aria-hidden="true">
            <svg
              viewBox="0 0 24 24"
              width="22"
              height="22"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
            >
              <path
                d="M5 12h12"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
              />
              <path
                d="M13 6l6 6-6 6"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </span>
        </div>

        <!-- Category Icons Right (more prominent) -->
        <div class="mixer-categories">
          {#each passions as passion}
            <button
              class="category-button {activeCategory === passion.id
                ? 'active'
                : ''} {playingCategory === passion.id ? 'playing' : ''}"
              on:click={() => togglePlay(passion.id)}
              title={passion.title}
              aria-label={`Seleziona categoria ${passion.title}`}
            >
              <span class="category-emoji">{passion.icon}</span>
              <span class="category-name">{CATEGORY_SHORT[passion.id]}</span>
            </button>
          {/each}
        </div>
      </div>
    </div>
  </section>

  <!-- Musical Staff Section -->
  <section class="musical-staff-section">
    <div class="container-fluid">
      <div class="staff-container">
        <!-- Pentagramma -->
        <div class="staff">
          <div class="staff-line" data-line="1"></div>
          <div class="staff-line" data-line="2"></div>
          <div class="staff-line" data-line="3"></div>
          <div class="staff-line" data-line="4"></div>
          <div class="staff-line" data-line="5"></div>
        </div>

        <!-- Chiave di violino sul pentagramma -->
        <div class="staff-clef" aria-hidden="true"></div>

        <!-- Note musicali (render da displayNotes reattivo) -->
        <div class="notes-container">
          {#each displayNotes as note, i}
            <div
              class="musical-note-passion score-note {note.source === 'score'
                ? 'score-active'
                : ''}"
              style="--note-color: {note.color}; --x: {note.x}%; --y: {note.y}px; --delay: {i *
                0.2}s; --note-opacity: {getNoteOpacity(note)}"
              on:click={() =>
                note.source === "passion"
                  ? openPassionModal(passions.find((p) => p.id === note.id))
                  : null}
              on:keydown={(e) => onNoteKey(e, note)}
              role="button"
              tabindex="0"
              aria-label={`${note.label}`}
              title={note.label}
            >
              <div class="note-stem"></div>
              <div class="note-head">
                {#if note.noteMeta && note.noteMeta.image}
                  <img
                    class="note-image"
                    src={note.noteMeta.image}
                    alt={note.noteMeta.title}
                  />
                {:else}
                  <span class="passion-emoji"
                    >{note.source === "passion"
                      ? passions.find((p) => p.id === note.id).icon
                      : ""}</span
                  >
                {/if}
              </div>
              <div class="note-info">
                <div class="note-name">{note.musicNote ?? note.label}</div>
                <!-- <div class="passion-name">{note.label}</div> -->
              </div>
              <div class="note-glow"></div>

              <!-- Tooltip: black semi-transparent box with image + text -->
              {#if note.noteMeta}
                <div class="note-tooltip" role="group" aria-hidden="true">
                  <div class="note-tooltip-inner">
                    {#if note.noteMeta.image}
                      <div class="tooltip-image">
                        <img
                          src={note.noteMeta.image}
                          alt={note.noteMeta.title}
                        />
                      </div>
                    {/if}
                    <div class="tooltip-text">
                      <div class="tooltip-title">{note.noteMeta.title}</div>
                      {#if note.noteMeta.description}
                        <div class="tooltip-desc">
                          {note.noteMeta.description}
                        </div>
                      {/if}
                    </div>
                  </div>
                </div>
              {/if}
            </div>
          {/each}
        </div>

        <!-- Indicatore di tempo -->
        <div class="time-signature">
          <div class="time-top">4</div>
          <div class="time-bottom">4</div>
        </div>
      </div>
    </div>
  </section>

  <!-- previous category-strip removed in favor of mixer-bar -->
</div>

<style>
  .passions-container {
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    min-height: 100vh;
    overflow-x: hidden;
    font-family: "Georgia", serif;
  }

  .dark-theme.passions-container {
    background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
    color: white;
  }

  /* Header-Mixer Integrated Section */
  .header-mixer {
    padding: 1.5rem 0 0.8rem;
    background: linear-gradient(
      135deg,
      rgba(24, 39, 51, 0.95) 0%,
      rgba(31, 39, 51, 0.98) 35%,
      rgba(20, 24, 29, 1) 100%
    );
    border-bottom: 3px solid rgba(255, 255, 255, 0.05);
    color: #e6eef8;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    position: relative;
  }

  .header-mixer::before {
    content: "";
    position: absolute;
    inset: 0;
    background: radial-gradient(
      ellipse at 30% 20%,
      rgba(255, 107, 107, 0.06),
      transparent 60%
    );
    pointer-events: none;
  }

  /* Minimal Header */
  .minimal-header {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
    margin-bottom: 1rem;
  }

  .clef-icon {
    font-size: 2.2rem;
    color: #ff6b6b;
    filter: drop-shadow(0 2px 8px rgba(255, 107, 107, 0.4));
  }

  .page-title {
    font-size: 1.8rem;
    font-weight: 700;
    margin: 0;
    color: #ffffff;
    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  }

  .page-title .accent {
    background: linear-gradient(135deg, #ff6b6b 0%, #ffa8a8 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  /* Mixer Row */
  .mixer-row {
    display: flex;
    align-items: center;
    gap: 2rem;
    justify-content: space-between;
  }

  /* Controls Left */
  .mixer-controls-left {
    display: flex;
    gap: 0.6rem;
    align-items: center;
    flex: 0 0 auto;
  }

  .btn-play {
    width: 44px;
    height: 36px;
    border-radius: 8px;
    border: none;
    background: linear-gradient(180deg, #2a3a4a, #1b242c);
    color: #e6eef8;
    cursor: pointer;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease;
  }

  .btn-play[aria-pressed="true"] {
    background: linear-gradient(180deg, #1f5fa8, #124b83);
    box-shadow: 0 12px 28px rgba(18, 75, 131, 0.25);
  }

  .progress-bar {
    width: 180px;
    appearance: none;
    height: 6px;
    background: linear-gradient(90deg, #2a3a4a, #22303d);
    outline: none;
    border-radius: 4px;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.4);
    opacity: 0.9;
  }

  .progress-bar::-webkit-slider-thumb {
    appearance: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: linear-gradient(180deg, #cfd9df, #a6b1b8);
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.35);
  }

  .progress-bar::-moz-range-thumb {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: linear-gradient(180deg, #cfd9df, #a6b1b8);
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.35);
  }

  /* Center Label */
  .mixer-center-label {
    flex: 1;
    text-align: center;
    padding: 0 1rem;
  }

  .select-track-text {
    font-size: 0.95rem;
    color: #dee6f5;
    font-weight: 600;
    letter-spacing: 0.6px;
    margin-right: 0.4rem;
  }

  .arrow-right {
    display: inline-flex;
    vertical-align: middle;
    color: #a8c1f0;
    filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.4));
  }

  /* Category Buttons Right (more prominent) */
  .mixer-categories {
    display: flex;
    gap: 0.8rem;
    align-items: center;
    flex: 0 0 auto;
  }

  .category-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70px;
    height: 60px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.06);
    color: #f7fbff;
    border: 1px solid rgba(255, 255, 255, 0.08);
    cursor: pointer;
    transition: all 0.3s ease;
    gap: 0.25rem;
  }

  .category-button:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-3px);
  }

  .category-button.active {
    box-shadow: 0 12px 32px rgba(34, 150, 243, 0.15);
    background: linear-gradient(180deg, #2b3b52, #1b2634);
    border-color: rgba(34, 150, 243, 0.3);
  }

  .category-button.playing {
    outline: 2px solid rgba(34, 150, 243, 0.4);
    background: linear-gradient(180deg, #1f5fa8, #124b83);
  }

  .category-button:focus-visible {
    outline: 3px solid #6aa2ff;
    outline-offset: 2px;
  }

  .category-emoji {
    font-size: 1.8rem;
    line-height: 1;
  }

  .category-name {
    font-size: 8px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: rgba(255, 255, 255, 0.8);
  }

  .category-button.active .category-name,
  .category-button.playing .category-name {
    color: #ffffff;
  }

  /* small visual for notes when showing a category score */
  .score-note .note-head {
    border-width: 3px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.18);
  }
  .score-note.score-active .note-head {
    transform: scale(1.18);
  }

  /* Musical Staff Section */
  .musical-staff-section {
    padding: 100px 0;
    background: linear-gradient(180deg, #ffffff 0%, #f8fbff 100%);
    position: relative;
  }

  .dark-theme .musical-staff-section {
    background: #1e1e1e;
  }

  .staff-container {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
    height: 300px;
  }

  .staff {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    height: 200px;
    transform: translateY(-50%);
  }

  .staff-line {
    position: absolute;
    left: 0;
    right: 0;
    height: 2px;
    background: #2c3e50;
    opacity: 0.6;
  }

  .dark-theme .staff-line {
    background: #ecf0f1;
  }

  .staff-line[data-line="1"] {
    top: 0;
  }
  .staff-line[data-line="2"] {
    top: 25%;
  }
  .staff-line[data-line="3"] {
    top: 50%;
  }
  .staff-line[data-line="4"] {
    top: 75%;
  }
  .staff-line[data-line="5"] {
    bottom: 0;
  }

  .notes-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none; /* le singole note gestiscono i click */
  }

  .musical-note-passion {
    position: absolute;
    left: calc(var(--x) - 40px + 60px); /* spostata un po' pi a destra */
    top: calc(var(--y) - 40px);
    cursor: pointer;
    pointer-events: auto;
    /* appear instantly without animation */
    opacity: var(--note-opacity, 1);
    transform: none;
  }

  .note-head {
    width: 68px;
    height: 68px;
    background: var(--note-color);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    border: 4px solid white;
  }

  .note-image {
    width: 56px;
    height: 56px;
    object-fit: cover;
    border-radius: 50%;
    display: block;
    border: 2px solid rgba(255, 255, 255, 0.85);
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.25);
  }

  .dark-theme .note-head {
    border-color: #1e1e1e;
  }

  .passion-emoji {
    font-size: 2.5rem;
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  }

  .note-stem {
    position: absolute;
    top: -64px;
    left: 63px;
    transform: none;
    width: 4px;
    height: 100px;
    background: #2c3e50;
    z-index: 1;
  }

  .dark-theme .note-stem {
    background: #ecf0f1;
  }

  .note-info {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    margin-top: 1rem;
    opacity: 0;
  }

  .note-name {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--note-color);
    margin-bottom: 0.5rem;
  }

  .passion-name {
    font-size: 0.9rem;
    color: #2c3e50;
    font-weight: 600;
  }

  .dark-theme .passion-name {
    color: #ecf0f1;
  }

  .musical-note-passion:hover .note-info {
    opacity: 1;
  }

  .musical-note-passion:hover .note-head {
    transform: scale(1.2);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
  }

  /* Taglio ledger lines (tagli addizionali) per note fuori dal range - opzionale per estensioni future */
  .ledger-line {
    position: absolute;
    left: -20px;
    right: -20px;
    height: 2px;
    background: currentColor;
  }

  .note-glow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 64px;
    height: 64px;
    background: radial-gradient(
        circle at 50% 40%,
        rgba(255, 255, 255, 0.12),
        transparent 60%
      ),
      radial-gradient(circle, var(--note-color), transparent 60%);
    border-radius: 50%;
    filter: blur(6px);
    opacity: calc(var(--note-opacity, 1) * 0.45);
    z-index: 0;
  }

  /* Tooltip for note metadata (black semi-transparent) */
  .note-tooltip {
    position: absolute;
    bottom: calc(100% + 12px);
    left: 50%;
    transform: translateX(-50%);
    min-width: 220px;
    max-width: 320px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border-radius: 10px;
    padding: 8px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.6);
    opacity: 0;
    pointer-events: none;
    z-index: 40;
  }

  .note-tooltip-inner {
    display: flex;
    gap: 8px;
    align-items: flex-start;
  }

  .tooltip-image img {
    width: 64px;
    height: 64px;
    object-fit: cover;
    border-radius: 6px;
    flex: 0 0 64px;
  }

  .tooltip-text {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  .tooltip-title {
    font-weight: 700;
    font-size: 0.95rem;
    line-height: 1.1;
  }

  .tooltip-short {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.85);
    font-weight: 600;
  }

  .tooltip-desc {
    font-size: 0.78rem;
    color: rgba(255, 255, 255, 0.92);
    margin-top: 3px;
  }

  /* Show tooltip on hover or when the note has keyboard focus */
  .musical-note-passion:hover .note-tooltip,
  .musical-note-passion:focus .note-tooltip,
  .musical-note-passion:focus-within .note-tooltip {
    opacity: 1;
    transform: translateX(-50%);
    pointer-events: auto;
  }

  @media (max-width: 768px) {
    .note-tooltip {
      min-width: 180px;
      max-width: 260px;
      bottom: calc(100% + 8px);
    }
    .tooltip-image img {
      width: 52px;
      height: 52px;
      flex: 0 0 52px;
    }
  }

  .time-signature {
    position: absolute;
    left: 165px; /* spostato un po' pi a destra */
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    color: #2c3e50;
  }

  .dark-theme .time-signature {
    color: #ecf0f1;
  }

  .time-top,
  .time-bottom {
    line-height: 1;
  }

  /* Chiave di violino nel pentagramma */
  .staff-clef {
    position: absolute;
    left: -8px;
    top: 51%;
    transform: translateY(-55%);
    font-size: 305px;
    color: #2c3e50;
    filter: drop-shadow(0 2px 2px rgba(0, 0, 0, 0.15));
    line-height: 1;
  }

  .dark-theme .staff-clef {
    color: #ecf0f1;
  }

  /* Melody Wave Section */
  .melody-wave {
    padding: 100px 0;
    background: #f8f9fa;
  }

  .dark-theme .melody-wave {
    background: #1a1a1a;
  }

  .melody-svg {
    width: 100%;
    height: 200px;
    margin-top: 2rem;
  }

  .melody-path {
    fill: none;
    stroke: #3498db;
    stroke-width: 3;
    opacity: 0;
    animation: drawMelody 3s ease-in-out forwards;
  }

  .melody-path-glow {
    fill: none;
    stroke: #3498db;
    stroke-width: 6;
    opacity: 0.3;
    filter: blur(2px);
    animation: drawMelody 3s ease-in-out forwards;
    animation-delay: 0.5s;
  }

  @keyframes drawMelody {
    to {
      opacity: 1;
      stroke-dasharray: 2000;
      stroke-dashoffset: 0;
    }
  }

  .melody-path,
  .melody-path-glow {
    stroke-dasharray: 2000;
    stroke-dashoffset: 2000;
  }

  .melody-description {
    font-size: 1.2rem;
    color: #7f8c8d;
    margin-bottom: 2rem;
  }

  .dark-theme .melody-description {
    color: #bdc3c7;
  }

  .melody-runner {
    fill: #9b59b6;
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
    animation: runAlong 4s ease-in-out infinite;
  }

  @keyframes runAlong {
    0% {
      transform: translateX(0);
      opacity: 0;
    }
    10% {
      opacity: 1;
    }
    50% {
      transform: translateX(400px);
    }
    90% {
      opacity: 1;
    }
    100% {
      transform: translateX(800px);
      opacity: 0;
    }
  }

  /* Musical Stats */
  .musical-stats {
    padding: 100px 0;
    background: white;
  }

  .dark-theme .musical-stats {
    background: #1e1e1e;
  }

  .stats-staff {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 3rem;
    flex-wrap: wrap;
    gap: 2rem;
  }

  .stat-note {
    text-align: center;
    padding: 2rem;
    background: #f8f9fa;
    border-radius: 20px;
    min-width: 200px;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
  }

  .dark-theme .stat-note {
    background: #2c3e50;
  }

  .stat-note::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(45deg, #3498db, #9b59b6);
  }

  .stat-note:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  }

  .stat-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .stat-note h3 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #2c3e50;
    margin-bottom: 0.5rem;
  }

  .dark-theme .stat-note h3 {
    color: white;
  }

  .stat-note p {
    color: #7f8c8d;
    font-weight: 600;
  }

  .dark-theme .stat-note p {
    color: #bdc3c7;
  }

  /* Mini pentagramma dentro ogni card statistica */
  .mini-staff {
    position: absolute;
    top: 12px;
    left: 12px;
    right: 12px;
    height: 36px;
    background-image: linear-gradient(#dfe6e9, #dfe6e9),
      linear-gradient(#dfe6e9, #dfe6e9), linear-gradient(#dfe6e9, #dfe6e9),
      linear-gradient(#dfe6e9, #dfe6e9), linear-gradient(#dfe6e9, #dfe6e9);
    background-size:
      100% 1px,
      100% 1px,
      100% 1px,
      100% 1px,
      100% 1px;
    background-repeat: no-repeat;
    background-position:
      0 0,
      0 25%,
      0 50%,
      0 75%,
      0 100%;
    opacity: 0.6;
  }

  .dark-theme .mini-staff {
    background-image: linear-gradient(#7f8c8d, #7f8c8d),
      linear-gradient(#7f8c8d, #7f8c8d), linear-gradient(#7f8c8d, #7f8c8d),
      linear-gradient(#7f8c8d, #7f8c8d), linear-gradient(#7f8c8d, #7f8c8d);
  }

  /* Musical Philosophy */
  .musical-philosophy {
    padding: 100px 0;
    background: #f8f9fa;
  }

  .dark-theme .musical-philosophy {
    background: #1a1a1a;
  }

  .philosophy-text {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #7f8c8d;
    margin-bottom: 1.5rem;
  }

  .dark-theme .philosophy-text {
    color: #bdc3c7;
  }

  .sheet-music-visual {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
  }

  .sheet-container {
    position: relative;
    width: 350px;
    height: 250px;
    background: white;
    border-radius: 15px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    border: 1px solid #e1e8ed;
  }

  .dark-theme .sheet-container {
    background: #2c3e50;
    border-color: #34495e;
  }

  .sheet-staff {
    position: absolute;
    top: 50%;
    left: 2rem;
    right: 2rem;
    height: 120px;
    transform: translateY(-50%);
  }

  .sheet-line {
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background: #bdc3c7;
  }

  .dark-theme .sheet-line {
    background: #7f8c8d;
  }

  .sheet-line:nth-child(1) {
    top: 0;
  }
  .sheet-line:nth-child(2) {
    top: 25%;
  }
  .sheet-line:nth-child(3) {
    top: 50%;
  }
  .sheet-line:nth-child(4) {
    top: 75%;
  }
  .sheet-line:nth-child(5) {
    bottom: 0;
  }

  .sheet-note {
    position: absolute;
