<script>
  import { onMount, onDestroy } from "svelte";
  import { currentTheme, language } from "../../store";
  import { translate } from "../../lib/i18n";

  let contactForm = {
    name: '',
    email: '',
    subject: '',
    message: '',
    company: '',
    budget: '',
    timeline: ''
  };

  let isSubmitting = false;
  let submitMessage = '';
  let submitType = ''; // 'success' or 'error'
  let contactMethods = [
    {
      title: 'Email',
      description: '',
      icon: 'fas fa-envelope',
      value: 'giovannifragala@gmail.com',
      action: 'mailto:giovannifragala@gmail.com',
      color: '#e74c3c'
    },
    {
      title: 'Telefono',
      description: '',
      icon: 'fas fa-phone',
      value: '+39 331 447 6871',
      action: 'tel:+393314476871',
      color: '#27ae60'
    },
    {
      title: 'LinkedIn',
      description: '',
      icon: 'fab fa-linkedin',
      value: '/in/giovannifragal',
      action: 'https://www.linkedin.com/in/giovannifragal%C3%A0/',
      color: '#0077b5'
    },
    {
      title: 'GitHub',
      description: '',
      icon: 'fab fa-github',
      value: '/giovannifragala',
      action: 'https://github.com/giovannifragala',
      color: '#333'
    }
  ];
  let faqItems = [
    {
      question: 'Quali tipi di progetti accetti?',
      answer: 'Accetto progetti di sviluppo web e mobile, specializzandomi in applicazioni con architettura a microservizi. Lavoro principalmente con Node.js, Svelte, MongoDB, Docker e MySQL. Sono anche interessato a progetti di game development.',
      isOpen: false
    },
    {
      question: 'Qual  il tuo processo di lavoro?',
      answer: 'Il mio processo inizia con l\'analisi dei requisiti e la progettazione dell\'architettura. Utilizzo metodologie agile, sviluppo iterativo con Docker per la containerizzazione, e mantengo comunicazione costante durante tutto il progetto.',
      isOpen: false
    },
    {
      question: 'Quanto tempo richiede un progetto tipico?',
      answer: 'Dipende dalla complessit: un\'applicazione web semplice richiede 2-4 settimane, progetti con microservizi 1-3 mesi, mentre applicazioni enterprise possono richiedere 3-6 mesi. Fornisco sempre stime dettagliate.',
      isOpen: false
    },
    {
      question: 'Hai esperienza con tecnologie specifiche?',
      answer: 'S, ho esperienza approfondita con Node.js, Javascript, Svelte, Java, Camel, Docker, Git, MongoDB, MySQL, Bootstrap, e React. Ho anche conoscenze di Python, TensorFlow per progetti di IA, e game development.',
      isOpen: false
    },
    {
      question: 'Lavori con team esistenti?',
      answer: 'Assolutamente! Ho esperienza nel lavorare in team, sia come sviluppatore che in ruoli di leadership tecnica. So integrarmi in workflow esistenti e utilizzo strumenti di collaborazione moderni.',
      isOpen: false
    }
  ];

  function toggleFaq(index) {
    faqItems[index].isOpen = !faqItems[index].isOpen;
    faqItems = faqItems; // Trigger reactivity
  }

  async function handleSubmit(event) {
    event.preventDefault();
    
    if (!contactForm.name || !contactForm.email || !contactForm.message) {
      submitMessage = 'Per favore compila tutti i campi obbligatori.';
      submitType = 'error';
      return;
    }

    isSubmitting = true;
    
    try {
      // Simula chiamata API
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // In un'app reale, qui faresti la chiamata al tuo backend
      console.log('Form submitted:', contactForm);
      
      submitMessage = 'Messaggio inviato con successo! Ti risponder entro 24 ore.';
      submitType = 'success';
      
      // Reset form
      contactForm = {
        name: '',
        email: '',
        subject: '',
        message: '',
        company: '',
        budget: '',
        timeline: ''
      };
      
    } catch (error) {
      submitMessage = 'Si  verificato un errore. Riprova o contattami direttamente via email.';
      submitType = 'error';
    } finally {
      isSubmitting = false;
      
      // Clear message after 5 seconds
      setTimeout(() => {
        submitMessage = '';
        submitType = '';
      }, 5000);
    }
  }

  // Star map / canvas setup
  let starCanvas;
  let animationId;
  let stars = [];
  const STAR_COUNT_BASE = 140;
  const starNodePositions = [
    { x: 18, y: 32 },
    { x: 82, y: 26 },
    { x: 24, y: 72 },
    { x: 78, y: 68 }
  ];
  const connections = [
    [0, 1],
    [0, 2],
    [1, 3],
    [2, 3]
  ];

  function initStarfield() {
    if (!starCanvas) return;
    const dpr = window.devicePixelRatio || 1;
    const rect = starCanvas.getBoundingClientRect();
    starCanvas.width = Math.floor(rect.width * dpr);
    starCanvas.height = Math.floor(rect.height * dpr);
    const ctx = starCanvas.getContext('2d');
    ctx.setTransform(dpr, 0, 0, dpr, 0, 0);
    // Generate stars
    const count = Math.floor(STAR_COUNT_BASE * (rect.width / 1200 + rect.height / 1200));
    stars = Array.from({ length: Math.max(100, Math.min(260, count)) }, () => ({
      x: Math.random() * rect.width,
      y: Math.random() * rect.height,
      r: Math.random() * 1.2 + 0.3,
      a: Math.random() * Math.PI * 2,
      s: 0.005 + Math.random() * 0.01
    }));
  }

  function drawStarfield() {
    if (!starCanvas) return;
    const ctx = starCanvas.getContext('2d');
    const { width, height } = starCanvas.getBoundingClientRect();
    ctx.clearRect(0, 0, width, height);
    // Background gradient (very subtle)
    const grad = ctx.createLinearGradient(0, 0, width, height);
    grad.addColorStop(0, '#0b1020');
    grad.addColorStop(1, '#151a30');
    ctx.fillStyle = grad;
    ctx.fillRect(0, 0, width, height);
    // Stars
    for (const star of stars) {
      star.a += star.s;
      const tw = (Math.sin(star.a) + 1) / 2; // 0..1
      ctx.beginPath();
      ctx.arc(star.x, star.y, star.r + tw * 0.6, 0, Math.PI * 2);
      ctx.fillStyle = `rgba(255,255,255,${0.3 + tw * 0.7})`;
      ctx.shadowBlur = 6 + tw * 8;
      ctx.shadowColor = 'rgba(120,180,255,0.6)';
      ctx.fill();
      ctx.shadowBlur = 0;
    }
  }

  function animate() {
    drawStarfield();
    animationId = requestAnimationFrame(animate);
  }

  function startAnimation() {
    stopAnimation();
    const prefersReduced = window.matchMedia('(prefers-reduced-motion: reduce)').matches;
    if (!prefersReduced) {
      animate();
    } else {
      drawStarfield();
    }
  }

  function stopAnimation() {
    if (animationId) cancelAnimationFrame(animationId);
    animationId = null;
  }

  function handleResize() {
    initStarfield();
    startAnimation();
  }

  onMount(() => {
    initStarfield();
    startAnimation();
    window.addEventListener('resize', handleResize);
  });

  onDestroy(() => {
    stopAnimation();
    window.removeEventListener('resize', handleResize);
  });
</script>

<div class="contact-container" class:dark-theme={$currentTheme === 'dark'}>
  <!-- Star Map Hero -->
  <section class="starmap-hero">
    <canvas class="starfield" bind:this={starCanvas} aria-hidden="true"></canvas>

  <!-- constellation lines removed to simplify hero shape -->

    <!-- Centered hero content -->
    <div class="starmap-overlay container text-center">
      <div class="row">
        <div class="col-lg-8 mx-auto">
          <h1 class="hero-title">
            {#if $language === 'it'}
              Lavoriamo <span class="gradient-text">Insieme</span>
            {:else}
              Let's Work <span class="gradient-text">Together</span>
            {/if}
          </h1>
          <p class="hero-subtitle">
            {#if $language === 'it'}
              Sei un'azienda e cerchi uno sviluppatore motivato? Sono aperto a nuove opportunit lavorative: contattami per entrare nel tuo team! Valuto anche proposte per progetti, consulenze e collaborazioni tecniche.
            {:else}
              Are you a company looking for a motivated developer? I'm open to new job opportunities: contact me to join your team! I also consider proposals for projects, consultancy and technical collaborations.
            {/if}
          </p>
          <a href="#contact-methods" class="scroll-down" aria-label={$language === 'it' ? 'Scorri ai metodi di contatto' : 'Scroll to contact methods'}>
            <i class="fas fa-chevron-down" aria-hidden="true"></i>
          </a>
        </div>
      </div>
    </div>
  </section>

  <!-- Contact Methods via interactive orbits -->
  <section id="contact-methods" class="orbits-section">
    <div class="container">
      <div class="row">
        <div class="col-lg-8 mx-auto text-center">
          <h2 class="section-title">{$language === 'it' ? 'Come contattarmi' : 'How to contact me'}</h2>
          <p class="section-description">
            {#if $language === 'it'}
              Scegli il metodo che preferisci per entrare in contatto. Clicca sul pianeta per aprire il link corrispondente.
            {:else}
              Choose your preferred method to get in touch. Click on the planet to open the corresponding link.
            {/if}
          </p>
        </div>
      </div>
      
      <!-- Orbits (interactive solar system) -->
      <div class="orbits-scene mt-5">
        <div class="orbits-container" aria-label="Sistema di contatto orbitale" role="group">
          <!-- central star -->
          <div class="sun" aria-hidden="true">
            <div class="sun-core"></div>
            <div class="sun-glow"></div>
          </div>

          <!-- orbit rings -->
          <div class="orbit-ring ring-1" aria-hidden="true"></div>
          <div class="orbit-ring ring-2" aria-hidden="true"></div>
          <div class="orbit-ring ring-3" aria-hidden="true"></div>
          <div class="orbit-ring ring-4" aria-hidden="true"></div>

          {#each contactMethods as method, i}
            {#key method.title}
              <a
                href={method.action}
                class="planet"
                style="--color: {method.color}; --radius: {i === 0 ? 'calc(var(--cs) * 0.195)' : i === 1 ? 'calc(var(--cs) * 0.275)' : i === 2 ? 'calc(var(--cs) * 0.365)' : 'calc(var(--cs) * 0.455)'}; --startAngle: {i === 0 ? 10 : i === 1 ? 120 : i === 2 ? 220 : 310}deg; --duration: {i === 0 ? '22s' : i === 1 ? '28s' : i === 2 ? '34s' : '40s'};"
                target={method.action.startsWith('http') ? '_blank' : '_self'}
                rel={method.action.startsWith('http') ? 'noopener noreferrer' : undefined}
                aria-label={`${method.title}: ${method.description} (${method.value})`}
              >
                <span class="planet-body" aria-hidden="true">
                  <i class={method.icon}></i>
                </span>
                <span class="planet-label" role="note">
                  <strong>{method.title}</strong>
                  <span class="desc">{method.description}</span>
                  <span class="value">{method.value}</span>
                </span>
              </a>
            {/key}
          {/each}
        </div>
      </div>

      <!-- Fallback list (mobile / reduced-motion) -->
      <div class="orbits-fallback mt-5">
        <div class="row">
          {#each contactMethods as method}
            <div class="col-12 mb-3">
              <a 
                href={method.action} 
                class="fallback-card"
                style="--accent: {method.color}"
                target={method.action.startsWith('http') ? '_blank' : '_self'}
                rel={method.action.startsWith('http') ? 'noopener noreferrer' : undefined}
              >
                <div class="fallback-icon" aria-hidden="true">
                  <i class={method.icon}></i>
                </div>
                <div class="fallback-content">
                  <h4>{method.title}</h4>
                  <span class="value">{method.action}</span>
                </div>
                <i class="fas fa-arrow-right arrow" aria-hidden="true"></i>
              </a>
            </div>
          {/each}
        </div>
      </div>

      <!-- Sezione contatti testuali/linkati -->
      <!-- <section class="contacts-list-section mt-5">
        <div class="container">
          <div class="row">
            <div class="col-lg-10 mx-auto">
              <h3 class="contacts-list-title text-center">Contatti diretti</h3>
              <div class="contacts-grid mt-3">
                {#each contactMethods as method}
                  <a
                    href={method.action}
                    class="contact-card"
                    style="--accent: {method.color}"
                    target={method.action.startsWith('http') ? '_blank' : '_self'}
                    rel={method.action.startsWith('http') ? 'noopener noreferrer' : undefined}
                    aria-label={`${method.title}: ${method.action}`}
                  >
                    <div class="cc-icon" aria-hidden="true"><i class={method.icon}></i></div>
                    <div class="cc-content">
                      <h4 class="cc-title">{method.title}</h4>
                      <div class="cc-value">{method.action}</div>
                    </div>
                    <i class="fas fa-arrow-right cc-arrow" aria-hidden="true"></i>
                  </a>
                {/each}
              </div>
            </div>
          </div>
        </div>
      </section> -->
    </div>
  </section>

  <!-- Contact form removed as requested -->


</div>

<style>
  .contact-container {
    padding-top: 64px;
    background: #0a0d1a;
    min-height: 100vh;
    color: #e6eaff;
  }

  .dark-theme.contact-container {
    background: #070914;
    color: #e6eaff;
  }

  /* Star Map Hero */
  .starmap-hero {
    position: relative;
    min-height: 90vh;
    display: grid;
    place-items: center;
    overflow: hidden;
    isolation: isolate;
    background: radial-gradient(1200px 700px at 50% 20%, rgba(100,120,255,0.15), rgba(0,0,0,0) 60%), linear-gradient(160deg, #0b1020 0%, #0a0d1a 60%, #070914 100%);
  }

  .starfield {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    display: block;
    z-index: 0;
  }

  /* constellation lines removed */

  .starmap-overlay {
    position: relative;
    z-index: 2;
    color: #e6eaff;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }

  .hero-title {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    line-height: 1.2;
  }

  .hero-subtitle {
    font-size: 1.2rem;
    opacity: 0.9;
    margin-bottom: 2.2rem;
    line-height: 1.6;
  }

  .scroll-down {
    display: inline-flex;
    width: 52px;
    height: 52px;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid rgba(140,170,255,0.35);
    color: #e6eaff;
    background: rgba(10,14,30,0.4);
    box-shadow: 0 6px 24px rgba(0,0,0,0.25);
    transition: transform 0.25s ease, background 0.25s ease, box-shadow 0.25s ease;
  }
  .scroll-down:hover { transform: translateY(3px); background: rgba(10,14,30,0.65); text-decoration: none; }
  .scroll-down i { font-size: 1.25rem; }

  .gradient-text {
    background: linear-gradient(45deg, #64b5f6, #9c27b0, #e91e63);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .hero-cta {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
  }

  .btn {
    padding: 15px 30px;
    border-radius: 30px;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;
    border: 2px solid transparent;
    display: inline-flex;
    align-items: center;
  }

  .btn-primary {
    background: linear-gradient(45deg, #5b8cff, #7d5bff);
    color: white;
    border: none;
    box-shadow: 0 6px 24px rgba(100, 140, 255, 0.3);
  }

  .btn-primary:hover {
    transform: translateY(-3px);
    box-shadow: 0 12px 28px rgba(100, 140, 255, 0.45);
    text-decoration: none;
    color: white;
  }

  .btn-outline-light {
    color: white;
    border-color: rgba(255, 255, 255, 0.5);
    background: transparent;
  }

  .btn-outline-light:hover {
    background: rgba(255, 255, 255, 0.08);
    text-decoration: none;
    color: white;
    transform: translateY(-3px);
  }

  /* Star nodes */
  .star-nodes {
    position: absolute;
    inset: 0;
    z-index: 2;
  }

  .star-node {
    position: absolute;
    left: var(--x);
    top: var(--y);
    transform: translate(-50%, -50%);
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #fff;
    transition: transform 0.25s ease, filter 0.25s ease;
    filter: drop-shadow(0 0 6px var(--node-color));
  }

  .star-node:focus-visible {
    outline: 2px solid #9ec1ff;
    outline-offset: 4px;
  }

  .star-core {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 30% 30%, #fff, var(--node-color));
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-size: 10px;
  }

  .star-glow {
    position: absolute;
    inset: -14px;
    border-radius: 50%;
    background: radial-gradient(circle, color-mix(in srgb, var(--node-color) 40%, transparent), transparent 60%);
    opacity: 0.35;
    filter: blur(10px);
  }

  .star-popup {
    position: absolute;
    left: 22px;
    top: -8px;
    min-width: 240px;
    max-width: 300px;
    background: rgba(10, 14, 30, 0.88);
    border: 1px solid rgba(140, 170, 255, 0.25);
    border-left: 3px solid var(--node-color);
    border-radius: 10px;
    padding: 12px 14px;
    box-shadow: 0 10px 30px rgba(0,0,0,0.35);
    transform: translateY(6px);
    opacity: 0;
    pointer-events: none;
    transition: all 0.25s ease;
    color: #e6eaff;
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
  }

  .star-node:hover .star-popup,
  .star-node:focus .star-popup,
  .star-node:focus-visible .star-popup {
    opacity: 1;
    transform: translateY(0);
    pointer-events: auto;
  }

  .star-popup h4 {
    margin: 0 0 6px 0;
    font-size: 1rem;
    color: #fff;
  }

  .star-popup p {
    margin: 0 0 6px 0;
    font-size: 0.9rem;
    color: #c7d1ff;
  }

  .star-popup .method-value {
    color: var(--node-color);
    font-weight: 700;
    font-size: 0.9rem;
  }

  .method-arrow {
    color: var(--node-color);
    float: right;
  }

  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }

  /* old mobile contact cards removed in favor of orbits fallback */

  .contact-form-section {
    padding: 80px 0;
    background: linear-gradient(180deg, rgba(10, 13, 26, 0) 0%, rgba(10, 13, 26, 0.6) 40%, rgba(10, 13, 26, 1) 100%);
  }

  .dark-theme .contact-form-section {
    background: linear-gradient(180deg, rgba(7, 9, 20, 0) 0%, rgba(7, 9, 20, 0.7) 40%, rgba(7, 9, 20, 1) 100%);
  }

  .dock-style .contact-form {
    background: rgba(10, 14, 30, 0.9);
    padding: 2.25rem;
    border-radius: 18px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.35), 0 0 0 1px rgba(140,170,255,0.2) inset;
    border: 1px solid rgba(140,170,255,0.2);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
  }

  .form-label {
    font-weight: 600;
    color: #e6eaff;
    margin-bottom: 0.35rem;
  }

  .form-control {
    border: 1px solid rgba(140,170,255,0.25);
    border-radius: 10px;
    padding: 14px;
    font-size: 1rem;
    transition: all 0.25s ease;
    background: rgba(12, 16, 34, 0.8);
    color: #e6eaff;
  }

  .form-control:focus {
    border-color: #6f9bff;
    box-shadow: 0 0 0 0.2rem rgba(111, 155, 255, 0.25);
    outline: none;
  }

  .form-control::placeholder {
    color: #9fb0ff;
    opacity: 0.6;
  }

  .form-submit {
    text-align: center;
  }

  .submit-message {
    margin-top: 1rem;
    padding: 1rem;
    border-radius: 10px;
    font-weight: 600;
  }

  .submit-message.success {
    background: rgba(50, 90, 60, 0.6);
    color: #b5ffcf;
    border: 1px solid rgba(100, 200, 140, 0.4);
  }

  .submit-message.error {
    background: rgba(90, 40, 44, 0.6);
    color: #ffb5b5;
    border: 1px solid rgba(200, 100, 100, 0.4);
  }

  .faq-section {
    padding: 100px 0;
    background: transparent;
  }

  .dark-theme .faq-section {
    background: transparent;
  }

  .faq-list {
    margin-top: 3rem;
  }

  .faq-item {
    background: rgba(12, 16, 34, 0.85);
    border-radius: 15px;
    margin-bottom: 1rem;
    overflow: hidden;
    transition: all 0.3s ease;
    border: 1px solid rgba(140,170,255,0.18);
  }

  .faq-question {
    width: 100%;
    background: none;
    border: none;
    padding: 1.25rem 1.5rem;
    text-align: left;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.05rem;
    font-weight: 600;
    color: #e6eaff;
    transition: all 0.3s ease;
  }

  .faq-question:hover {
    background: rgba(120, 170, 255, 0.08);
  }

  .faq-question i {
    transition: transform 0.3s ease;
    color: #7aa8ff;
  }

  .faq-item.open .faq-question i {
    transform: rotate(180deg);
  }

  .faq-answer {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
  }

  .faq-item.open .faq-answer {
    max-height: 300px;
  }

  .faq-answer p {
    padding: 0 1.5rem 1.25rem;
    color: #c7d1ff;
    line-height: 1.6;
    margin: 0;
  }

  .final-cta {
    padding: 100px 0;
    background: radial-gradient(800px 400px at 50% -10%, rgba(120,170,255,0.2), rgba(0,0,0,0) 60%), linear-gradient(135deg, #0b1020 0%, #12183a 100%);
    color: white;
    text-align: center;
  }

  .dark-theme .final-cta {
    background: radial-gradient(800px 400px at 50% -10%, rgba(120,170,255,0.2), rgba(0,0,0,0) 60%), linear-gradient(135deg, #0b1020 0%, #12183a 100%);
  }

  .final-cta h2 {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }

  .final-cta p {
    font-size: 1.2rem;
    opacity: 0.9;
