<script>
  import { segment, islands, language } from '../store';
  import { translate } from '../lib/i18n';
  import anime from 'animejs';
  import { get } from 'svelte/store';
  import { onMount } from 'svelte';
  let rootEl;
  let isAnimating = false;
  const bgImage = '/images/grand-line.png';

  onMount(() => {
    // Portal nel body per evitare che un ancestor con transform interferisca con position:fixed
    if (rootEl && rootEl.parentNode !== document.body) {
      document.body.appendChild(rootEl);
    }
    // Fallback: forza position fixed se sovrascritta da altri stili
    if (rootEl) {
      rootEl.style.position = 'fixed';
      rootEl.style.bottom = '16px';
      rootEl.style.right = '16px';
    }
  });

  function handleClick() {
    if (isAnimating) return;
    isAnimating = true;
    const rect = rootEl.getBoundingClientRect();
    // Clona un overlay per l'animazione senza spostare l'originale
    const overlay = document.createElement('div');
    overlay.className = 'mini-map-overlay';
    overlay.setAttribute('aria-hidden', 'true');
    overlay.style.position = 'fixed';
    overlay.style.left = rect.left + 'px';
    overlay.style.top = rect.top + 'px';
    overlay.style.width = rect.width + 'px';
    overlay.style.height = rect.height + 'px';
    overlay.style.borderRadius = '14px';
    overlay.style.backgroundImage = `url(${bgImage})`;
    overlay.style.backgroundSize = 'cover';
    overlay.style.backgroundPosition = 'center';
    overlay.style.zIndex = '9999';
    overlay.style.boxShadow = '0 8px 28px -4px rgba(0,0,0,.55)';
    // Copia le isole dentro l'overlay
    const frag = document.createDocumentFragment();
    islands.forEach(island => {
      const b = document.createElement('div');
      b.className = 'mini-island';
      b.style.left = island.x + '%';
      b.style.top = island.y + '%';
      b.title = island.label;
      b.innerHTML = `<span class='dot'></span>`;
      frag.appendChild(b);
    });
    overlay.appendChild(frag);
    document.body.appendChild(overlay);

    anime({
      targets: overlay,
      left: 0,
      top: 0,
      width: window.innerWidth,
      height: window.innerHeight,
      borderRadius: 0,
      duration: 650,
      easing: 'easeInOutCubic',
      update: () => {
        // potremmo ridimensionare le isole in base al progresso se volessimo
      },
      complete: () => {
        segment.set('home');
        setTimeout(() => {
          // rimuovi overlay dopo cambio pagina per evitare flash
          overlay.remove();
          isAnimating = false;
        }, 30);
      }
    });
  }
</script>

<div class="mini-map" bind:this={rootEl} role="button" aria-label="Apri la mappa" on:click={handleClick}>
  <div class="islands-layer">
    {#each islands as island}
      <div class="mini-island" style="left:{island.x}%;top:{island.y}%" title={island.label}>
        <span class="dot {island.segment === (get(segment)) ? 'active' : ''}"></span>
      </div>
    {/each}
  </div>
  <div class="mini-overlay" aria-hidden="true">{$translate('map.minimap')}</div>
</div>

<style>
  .mini-map {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 140px;
    height: 140px;
    background: #0b2035 url('/images/grand-line.png') center/cover no-repeat;
    border: 1px solid #ffffff22;
    border-radius: 14px;
    box-shadow: 0 4px 18px -2px rgba(0,0,0,.55), 0 0 0 1px #ffffff10 inset;
    cursor: pointer;
    transition: transform .35s cubic-bezier(.22,.99,.43,1), box-shadow .35s;
    z-index: 40;
    overflow: hidden;
    backdrop-filter: blur(2px) saturate(1.2);
  }
  .mini-map:hover {
    transform: scale(1.5) translate(-25px, -25px);
    box-shadow: 0 10px 28px -4px rgba(0,0,0,.65), 0 0 0 1px #ffffff18 inset;
  }
  .mini-map:active { transform: scale(1.45) translate(-25px, -25px); }
  .islands-layer { position: relative; width: 100%; height: 100%; }
  .mini-island {
    position: absolute;
    transform: translate(-50%, -50%);
    width: 16px; height: 16px;
    display: flex; align-items: center; justify-content: center;
  }
  .dot {
    width: 8px; height: 8px;
    border-radius: 50%;
    background: radial-gradient(circle at 30% 30%, #ffe9b3, #d4a757);
    border: 1px solid #5b3d00;
    box-shadow: 0 0 0 2px rgba(0,0,0,.4), 0 0 6px -1px rgba(255,255,255,.55);
    transition: transform .3s;
  }
  .dot.active { outline: 2px solid #fff8; }
  .mini-island:hover .dot { transform: scale(1.5); }

  .mini-map-overlay .mini-island { position: absolute; }
  .mini-map-overlay .mini-island .dot { width: 14px; height: 14px; }
  .mini-map-overlay { transition: filter .3s; }

  /* Overlay iniziale "Mappa" */
  .mini-overlay {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(10,16,28,0.15);
    backdrop-filter: blur(3px) saturate(1.3);
    font-size: 0.8rem;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #fff;
    text-shadow: 0 2px 6px rgba(0,0,0,.55);
    transition: opacity .35s ease;
    pointer-events: none; /* non blocca click/hover */
    border-radius: inherit;
  }
  .mini-map:hover .mini-overlay,
  .mini-map:active .mini-overlay { opacity: 0; }
  .mini-overlay { opacity: 1; }
</style>
