<script>
  import { onMount, onDestroy } from "svelte";
  import { currentTheme, language } from "../../store";
  import { translate } from "../../lib/i18n";

  // Dimensioni del "mondo" marino
  const WORLD_WIDTH = 4500;
  const WORLD_HEIGHT = 2000;
  const SPEED = 210; // px al secondo
  const DIAGONAL_Y_RATIO = 0.4; // quanto inclinare la diagonale verso X (0..1)  ridotto per angolo pi orizzontale
  const IDLE_TIMEOUT = 4000; // ms
  const DISCOVER_DISTANCE = 230; // raggio per attivare oggetto info

  // === CONFIGURAZIONE GRAFICA ===
  // Percorso opzionale texture mare (imposta USE_TEXTURE = true se aggiungi un file valido)
  export const SEA_TEXTURE_PATH = "/images/sea.jpg";
  export const USE_TEXTURE = true; // se true usa immagine invece di gradienti procedurali
  export const SHOW_ALL_OBJECTS_BASE = false; // oggetti invisibili finch non vengono scoperti
  export const OBJECT_BASE_OPACITY = 0.25; // opacit base oggetti non scoperti
  export const CAMERA_SMOOTH = 0.18; // 0 = nessun smoothing, 0.1-0.2 = morbido
  export const BOTTLE_IMAGE_PATH = "/images/bottle.png"; // sostituisci con il path reale dell'immagine bottiglia
  export const SHORE_IMAGE_PATH = "/images/spiaggia.png"; // immagine spiaggia
  // Gli item ora includono direttamente il path dell'immagine

  // Oggetti informativi che galleggiano (titolo / testo / posizione XY nel mondo) - reactive to language
  $: infoItems = [
    {
      id: "portfolio",
      title: $translate('about.portfolio.title'),
      text: $translate('about.portfolio.text'),
      x: 660,
      y: 1580,
      type: "scroll",
      image: "/images/scroll.png",
    },
    {
      id: "aboutme",
      title: $translate('about.whoami.title'),
      text: $translate('about.whoami.text'),
      x: 1250,
      y: 1500,
      type: "about",
      image: "/images/straw-hat.png",
    },
    {
      id: "universita",
      title: $translate('about.education.title'),
      text: $translate('about.education.text'),
      x: 1800,
      y: 1115,
      type: "book",
      image: "/images/zaino.png",
    },
    {
      id: "lavoro",
      title: $translate('about.work.title'),
      text: $translate('about.work.text'),
      x: 2500,
      y: 1060,
      type: "book",
      image: "/images/scheda-madre.png",
    },
    {
      id: "game",
      title: $translate('about.gamedev.title'),
      text: $translate('about.gamedev.text'),
      x: 3050,
      y: 600,
      type: "crate",
      image: "/images/forziere.png",
    },
    {
      id: "pap",
      title: $translate('about.family.title'),
      text: $translate('about.family.text'),
      x: 3650,
      y: 580,
      type: "buoy",
      image: "/images/biberon.png",
    },
    {
      id: "obiettivi",
      title: $translate('about.goals.title'),
      text: $translate('about.goals.text'),
      x: 4100,
      y: 200,
      type: "buoy",
      image: "/images/bussola.png",
    },
  ];

  let bottleX = 100; // coordinate logiche "camera" - pi a sinistra
  let bottleY = WORLD_HEIGHT - 150; // pi in basso: avvia vicino al fondo del mondo
  const START_IN_CORNER = true; // avvio con vista ancorata in basso-sinistra
  let hasMoved = false; // diventa true al primo input effettivo
  let discovered = new Set(); // mantenuto se in futuro servir "collezionati"
  let visibleCards = new Set(); // set dinamico aggiornato ogni frame (vicinanza)
  // removed reachedShore mechanics
  let showIdleHint = false;
  let lastMove = Date.now();
  let raf; // loop id
  let idleInterval; // intervallo hint
  let pressed = {
    ArrowUp: false,
    ArrowDown: false,
    ArrowLeft: false,
    ArrowRight: false,
  };

  function updateIdleHint() {
    showIdleHint = Date.now() - lastMove > IDLE_TIMEOUT;
  }

  function handleKey(e) {
    if (e.key in pressed) {
      pressed[e.key] = e.type === "keydown";
      if (e.type === "keydown") {
        lastMove = Date.now();
        showIdleHint = false;
      }
      e.preventDefault();
    }
  }

  function gameLoop(ts) {
    const dt = 1 / 60; // fisso per semplicit
    let vx = 0,
      vy = 0;
    const forward = pressed.ArrowUp || pressed.ArrowRight; // avanti (obliquo su-destra ma pi verso destra)
    const backward = pressed.ArrowDown || pressed.ArrowLeft; // indietro (obliquo gi-sinistra ma pi verso sinistra)
    if (forward && !backward) {
      vx = 1;
      vy = -DIAGONAL_Y_RATIO;
    } else if (backward && !forward) {
      vx = -1;
      vy = DIAGONAL_Y_RATIO;
    }
    // normalizza il vettore per mantenere la stessa velocit indipendentemente dall'inclinazione
    const len = Math.hypot(vx, vy);
    if (len > 0) {
      vx /= len;
      vy /= len;
    }

    const newX = bottleX + vx * SPEED * dt;
    const newY = bottleY + vy * SPEED * dt;

    // Bordo mondo e aggiornamento reattivo
    const oldX = bottleX;
    const oldY = bottleY;
    bottleX = Math.max(0, Math.min(WORLD_WIDTH, newX));
    bottleY = Math.max(0, Math.min(WORLD_HEIGHT, newY));

    if (oldX !== bottleX || oldY !== bottleY) {
      hasMoved = true;
    }

    // Vicinanza card (compare quando entro nel raggio, scompare quando esco)
    const nextVisible = new Set();
    for (const item of infoItems) {
      const dx = item.x - bottleX;
      const dy = item.y - bottleY;
      if (Math.hypot(dx, dy) < DISCOVER_DISTANCE) {
        nextVisible.add(item.id);
        if (!discovered.has(item.id)) {
          // registra come scoperto (ma la card potr comunque sparire quando ti allontani)
          discovered = new Set([...discovered, item.id]);
        }
      }
    }
    visibleCards = nextVisible; // assegno nuovo set per trigger reattivit

    // removed shore reach detection

    updateIdleHint();
    raf = requestAnimationFrame(gameLoop);
  }

  // Posizione camera smussata
  let camX = bottleX;
  let camY = bottleY;

  // Reactive statement per aggiornare camera quando bottleX/Y cambiano
  $: {
    if (CAMERA_SMOOTH > 0) {
      camX += (bottleX - camX) * CAMERA_SMOOTH;
      camY += (bottleY - camY) * CAMERA_SMOOTH;
    } else {
      camX = bottleX;
      camY = bottleY;
    }
  }

  // Variabili viewport e trasformazioni
  let viewportW = typeof window !== "undefined" ? window.innerWidth : 1920;
  let viewportH = typeof window !== "undefined" ? window.innerHeight : 1080;
  let tx = 0,
    ty = 0; // trasformazioni mondo
  let worldTransformValue = "translate(0px,0px)";

  function updateViewport() {
    viewportW = window.innerWidth;
    viewportH = window.innerHeight;
  }

  // Reactive: calcola transform mondo + posizione schermo bottiglia
  $: {
    const centerX = viewportW / 2;
    const centerY = viewportH / 2;
    const minTx = viewportW - WORLD_WIDTH;
    const minTy = viewportH - WORLD_HEIGHT;

    if (START_IN_CORNER && !hasMoved) {
      // ancorato a basso-sinistra prima di qualsiasi movimento
      tx = 0; // bordo sinistro
      ty = minTy; // bordo inferiore (world bottom visibile)
    } else {
      let desiredTx = centerX - bottleX;
      let desiredTy = centerY - bottleY;
      tx = Math.min(0, Math.max(desiredTx, minTx));
      ty = Math.min(0, Math.max(desiredTy, minTy));
    }
    worldTransformValue = `translate(${tx}px, ${ty}px)`;
  }

  onMount(() => {
    window.addEventListener("keydown", handleKey);
    window.addEventListener("keyup", handleKey);
    window.addEventListener("resize", updateViewport);
    raf = requestAnimationFrame(gameLoop);
    idleInterval = setInterval(updateIdleHint, 500);
  });

  onDestroy(() => {
    window.removeEventListener("keydown", handleKey);
    window.removeEventListener("keyup", handleKey);
    window.removeEventListener("resize", updateViewport);
    cancelAnimationFrame(raf);
    if (idleInterval) clearInterval(idleInterval);
  });
</script>

<div
  class="sea-experience"
  class:dark-theme={$currentTheme === "dark"}
  style="--ghost-opacity:{OBJECT_BASE_OPACITY};"
>
  <div class="viewport-overlay">
    {#if showIdleHint}
      <div class="idle-hint">
        {$translate('about.idleHint')}
      </div>
    {/if}
  </div>
  <div
    class="world {USE_TEXTURE ? 'with-texture' : ''}"
    style="width:{WORLD_WIDTH}px; height:{WORLD_HEIGHT}px; transform:{worldTransformValue}; --sea-tex:{USE_TEXTURE
      ? `url(${SEA_TEXTURE_PATH})`
      : 'none'};"
  >
    <div class="layer depth"></div>
    <div class="layer currents"></div>
    <div class="layer particles">
      {#each Array(40) as _, i}
        <span
          style="--x:{Math.random() * 100}%; --y:{Math.random() *
            100}%; --d:{6 + Math.random() * 10}s; --s:{0.4 +
            Math.random() * 0.8};"
        ></span>
      {/each}
    </div>
    <img class="shore-img" src={SHORE_IMAGE_PATH} alt="Shore" />
    <div
      class="bottle-fixed in-world"
      style="left:{bottleX}px; top:{bottleY}px;"
    >
      <div class="bottle-entity">
        <img class="bottle-img" src={BOTTLE_IMAGE_PATH} alt="Bottle" />
        <div class="wake"></div>
      </div>
    </div>
    {#each infoItems as item}
      <div
        class="info-object type-{item.type} {item.id === 'obiettivi'
          ? 'layout-right'
          : ''}"
        style="left:{item.x}px; top:{item.y}px;"
      >
        <img
          class="float-shape"
          src={item.image}
          alt={item.title}
          loading="lazy"
        />
        <div class="content {visibleCards.has(item.id) ? 'show' : ''}">
          <h4>{item.title}</h4>
          <p>{item.text}</p>
        </div>
      </div>
    {/each}
  </div>
  <div class="mobile-controls">
    <button
      on:touchstart={() => (pressed.ArrowUp = true)}
      on:touchend={() => (pressed.ArrowUp = false)}></button
    >
    <div class="mid">
      <button
        on:touchstart={() => (pressed.ArrowLeft = true)}
        on:touchend={() => (pressed.ArrowLeft = false)}></button
      >
      <button
        on:touchstart={() => (pressed.ArrowDown = true)}
        on:touchend={() => (pressed.ArrowDown = false)}></button
      >
      <button
        on:touchstart={() => (pressed.ArrowRight = true)}
        on:touchend={() => (pressed.ArrowRight = false)}></button
      >
    </div>
  </div>
</div>

<style>
  .sea-experience {
    --sea-top: #0d6d8f;
    --sea-mid: #0b5773;
    --sea-deep: #083c52;
    --accent: #6bd5ff;
    --accent2: #30b0ff;
    --sand: #f6e4b4;
    --sand-edge: #d0b680;
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: linear-gradient(
      to bottom,
      #9ad9ff,
      #6bc3f1 25%,
      #2083ad 60%,
      #0d4f6d
    );
    font-family:
      system-ui,
      -apple-system,
      "Segoe UI",
      Roboto,
      sans-serif;
  }
  .dark-theme.sea-experience {
    background: linear-gradient(
      to bottom,
      #072331,
      #063043 25%,
      #072f40 60%,
      #041e29
    );
  }
  .viewport-overlay {
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 50;
  }
  .bottle-fixed {
    position: absolute;
    transform: translate(-50%, -50%);
    width: 160px;
    height: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    z-index: 20;
  }
  .idle-hint {
    margin-top: 2rem;
    background: rgba(0, 0, 0, 0.55);
    color: #fff;
    padding: 0.8rem 1.2rem;
    border-radius: 12px;
    font-size: 0.9rem;
    letter-spacing: 0.5px;
    animation: pulse 1.8s ease-in-out infinite;
    backdrop-filter: blur(4px);
  }
  @keyframes pulse {
    0%,
    100% {
      transform: translateY(0);
      opacity: 0.9;
    }
    50% {
      transform: translateY(4px);
      opacity: 0.6;
    }
  }
  /* removed shore panel styles */

  .world {
    position: absolute;
    top: 0;
    left: 0;
    will-change: transform;
  }
  .world:before {
    content: "";
    position: absolute;
    inset: 0;
    background: var(--sea-tex);
    background-size: 1200px 1200px;
    opacity: 0;
    mix-blend-mode: overlay;
    pointer-events: none;
  }
  .world.with-texture:before {
    opacity: 0.35;
  }
  .layer.depth {
    position: absolute;
    inset: 0;
    background: radial-gradient(
        circle at 40% 60%,
        rgba(255, 255, 255, 0.08),
        transparent 60%
      ),
      linear-gradient(to bottom, #0f6a8c, var(--sea-mid) 55%, var(--sea-deep));
    mix-blend-mode: overlay;
  }
  .layer.currents {
    position: absolute;
    inset: 0;
    background: repeating-linear-gradient(
      115deg,
      rgba(255, 255, 255, 0.06) 0 140px,
      transparent 140px 260px
    );
    opacity: 0.25;
    animation: curr 18s linear infinite;
  }
  @keyframes curr {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-600px);
    }
  }
  .layer.particles span {
    position: absolute;
    left: var(--x);
    top: var(--y);
    width: 6px;
    height: 6px;
    background: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    animation: rise var(--d) linear infinite;
    transform: scale(var(--s));
    opacity: 0.5;
  }
  @keyframes rise {
    to {
      transform: translateY(-160px) scale(var(--s));
      opacity: 0;
    }
  }

  .shore-img {
    position: absolute;
    right: 0;
    top: 0;
    width: 800px;
    height: auto;
    max-height: 520px;
    object-fit: contain;
    filter: drop-shadow(-10px 18px 24px rgba(0, 0, 0, 0.35));
    pointer-events: none;
    -webkit-mask-image: radial-gradient(
      circle at 65% 40%,
      #000 55%,
      rgba(0, 0, 0, 0) 92%
    );
    mask-image: radial-gradient(
      circle at 65% 40%,
      #000 55%,
      rgba(0, 0, 0, 0) 92%
    );
  }
  .dark-theme .shore-img {
    filter: drop-shadow(-10px 18px 28px rgba(0, 0, 0, 0.55));
  }

  .bottle-entity {
    position: absolute;
    width: 120px;
    height: 120px;
    transform: translate(-50%, -50%);
    pointer-events: none;
    animation: float 4.5s ease-in-out infinite;
  }
  @keyframes float {
    0%,
    100% {
      transform: translateY(-6px) rotate(-4deg);
    }
    50% {
      transform: translateY(5px) rotate(3deg);
    }
  }
  .bottle-img {
    width: 100%;
    height: 18vh;
    object-fit: contain;
    image-rendering: auto;
    filter: drop-shadow(0 6px 16px rgba(0, 0, 0, 0.35));
  }
  .wake {
    position: absolute;
    bottom: 10px;
    left: 50%;
    width: 120px;
    height: 50px;
    transform: translateX(-50%);
    background: radial-gradient(
      circle at 50% 80%,
      rgba(255, 255, 255, 0.55),
      rgba(255, 255, 255, 0) 70%
    );
    filter: blur(8px);
    opacity: 0.55;
    animation: wake 3.5s linear infinite;
  }
  @keyframes wake {
    from {
      transform: translateX(-50%) scale(0.9);
    }
    to {
      transform: translateX(-50%) scale(1.05);
    }
  }

  /* Re-centered the whole info-object; we'll shift only the card content left */
  .info-object {
    position: absolute;
    transform: translate(-50%, -50%) scale(0.9);
    width: 210px;
    pointer-events: none;
    z-index: 50;
  }
  .info-object .float-shape {
    width: 108px;
    height: 108px;
    margin: 0 auto 6px;
    filter: drop-shadow(0 7px 16px rgba(0, 0, 0, 0.28));
    animation: bob 5.2s ease-in-out infinite;
    position: relative;
    object-fit: contain;
  }
  @keyframes bob {
    0%,
    100% {
      transform: translateY(-10px) rotate(-4deg);
    }
    50% {
      transform: translateY(6px) rotate(3deg);
    }
  }
  .info-object .content {
    position: absolute;
    left: 50%;
    bottom: 100%;
    transform: translate(-80%, -12px) translateY(12px) scale(0.92);
    background: rgba(255, 255, 255, 0.92);
    backdrop-filter: blur(8px);
    padding: 0.7rem 0.75rem 0.75rem;
    border-radius: 16px;
    box-shadow: 0 8px 24px -8px rgba(0, 0, 0, 0.4);
    width: 230px;
    opacity: 0;
    pointer-events: none;
    transition:
      opacity 0.35s ease,
      transform 0.35s ease;
    z-index: 65;
  }
  .info-object .content.show {
    opacity: 1;
    transform: translate(-80%, -12px) translateY(0) scale(1);
  }
  /* Layout speciale per la bussola (card a destra e un po' pi in basso) */
  .info-object.layout-right .content {
    bottom: auto;
    left: 100%;
    top: 55%;
    transform: translate(14px, -45%) translateY(12px) scale(0.92);
  }
  .info-object.layout-right .content.show {
    transform: translate(14px, -45%) translateY(0) scale(1);
  }
  .dark-theme .info-object .content {
    background: rgba(20, 32, 40, 0.82);
    color: #def6ff;
  }
  .info-object h4 {
    margin: 0 0 0.4rem;
    font-size: 1.15rem;
    background: linear-gradient(90deg, var(--accent2), var(--accent));
    -webkit-background-clip: text;
    color: transparent;
    letter-spacing: 0.55px;
  }
  .info-object p {
    margin: 0;
    font-size: 0.92rem;
    line-height: 1.45;
    font-weight: 500;
  }
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  /* Tipi: ora gestiti via immagini -> gli stili specifici non servono */

  /* Mobile controls */
  .mobile-controls {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    display: none;
    gap: 0.6rem;
    z-index: 60;
  }
  .mobile-controls button {
    background: rgba(255, 255, 255, 0.2);
    border: 2px solid rgba(255, 255, 255, 0.5);
    color: #fff;
    width: 54px;
    height: 54px;
    border-radius: 12px;
    font-size: 1.1rem;
    font-weight: 600;
    backdrop-filter: blur(6px);
  }
  .mobile-controls .mid {
    display: flex;
    gap: 0.6rem;
  }
  @media (max-width: 860px) {
    .mobile-controls {
      display: flex;
      flex-direction: column;
    }
  }

  /* Dark tweaks */
  .dark-theme .info-object h4 {
    filter: brightness(1.1);
  }
  .dark-theme .shore-cta {
    color: #fff;
  }
</style>
