<script>
  import { onMount } from 'svelte';
  import { currentTheme } from '../../store';
  import { translate } from '../../lib/i18n';
  import { language } from '../../store'; // to access $language in template
  import { experiencesData, educationData, certificationsData, skillsData } from '../../data/cvData';
  import { cvPdfIt, cvPdfEn } from '../../data/cvPdf';

  // Dati di base
  // PDF del CV in Base64 (senza prefisso data URI). Sostituisci la stringa segnaposto con il contenuto reale.
  // Esempio: const cvPdfBase64 = 'JVBERi0xLjcKJc...' (molto lungo)
  const cvPdfBase64 = ''; // TODO: incolla qui il base64 del PDF

  const personalInfo = {
    name: 'Giovanni Fragal',
    title: 'Software Engineer',
    email: 'giovannifragala@gmail.com',
    phone: '+39 3314476871',
    location: 'Catania, Italy  Remote',
    website: 'https://(portfolio-link)', // TODO: inserire URL reale portfolio
    linkedin: 'https://www.linkedin.com/in/giovannifragal',
    github: ''
  };

  const summary = `Sono uno sviluppatore Full Stack con ~6 anni di esperienza, specializzato nella progettazione e realizzazione di architetture software scalabili (microservizi, API gateway, integrazioni). Early adopter degli strumenti di Intelligenza Artificiale (Copilot, Claude Code, GPT, agent mode) che utilizzo quotidianamente per migliorare flussi di lavoro e diffondere nuove pratiche nel team. Fuori dal lavoro sviluppo videogiochi indie e produco musica: attivit che alimentano creativit, disciplina e problem solving.`;


  // Use imported multilingual data
  const experiences = experiencesData;
  const education = educationData;
  const certifications = certificationsData;
  const skills = skillsData;

  const languages = [
    { name: 'Italiano', level: 'Madrelingua' },
    { name: 'Inglese', level: 'Intermedio (B1+)' }
  ];

  const projects = [
    {
      name: 'Home  Utility Tools',
      description: 'Web app di utility familiari & lavoro: gestione spese, lista spesa, file sharing temporaneo, gestione keywords. Architettura containerizzata autohosted.',
      role: 'Full-Stack Developer',
      team: 'Indie / personale',
      duration: 'Evoluzione continua',
      technologies: ['Node.js','MongoDB','Redis','Docker Swarm','Svelte','Sapper','Bootstrap4'],
      achievements: ['Deploy stabile selfhosted','Utilizzo quotidiano > 2 anni']
    },
    {
      name: 'Pirates Dawn: The Hidden Tide',
      description: 'Indie mobile game pubblicato (Google Play)  design, gameplay loop, backend servizi base, UI e pubblicazione.',
      role: 'Game Designer / Dev',
      team: 'Indie solo',
      duration: 'Release Apr 2025',
      technologies: ['Svelte','Ionic','Android Studio','Node.js'],
      achievements: ['Pubblicazione store','Pipeline build & release automatizzata']
    },
    {
      name: 'Roguelite RPG & Card Combat (WIP)',
      description: 'Due giochi in sviluppo: uno roguelite con progressione & skill tree; altro con card-based combat & progression systems.',
      role: 'Game Developer',
      team: 'Indie / personale',
      duration: 'Work in progress',
      technologies: ['Python','TensorFlow','Svelte','Node.js'],
      achievements: ['Prototipi giocabili','Studio pattern progressione e bilanciamento']
    }
  ];
  // Utilit: converte base64 in Blob in modo sicuro anche per file grandi
  function base64ToBlob(b64Data, contentType = 'application/pdf', sliceSize = 1024) {
    const clean = b64Data.replace(/^data:application\/pdf;base64,/, '').trim();
    const byteChars = atob(clean);
    const bytesLength = byteChars.length;
    const slicesCount = Math.ceil(bytesLength / sliceSize);
    const byteArrays = new Array(slicesCount);
    for (let sliceIndex = 0; sliceIndex < slicesCount; sliceIndex++) {
      const begin = sliceIndex * sliceSize;
      const end = Math.min(begin + sliceSize, bytesLength);
      const bytes = new Array(end - begin);
      for (let offset = begin, i = 0; offset < end; offset++, i++) {
        bytes[i] = byteChars.charCodeAt(offset);
      }
      byteArrays[sliceIndex] = new Uint8Array(bytes);
    }
    return new Blob(byteArrays, { type: contentType });
  }

  function downloadCV() {
    try {
      const selected = $language === 'en' ? cvPdfEn : cvPdfIt;
      const blob = base64ToBlob(selected, 'application/pdf');
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = $language === 'en' ? 'Giovanni_Fragala_CV_EN.pdf' : 'Giovanni_Fragala_CV_IT.pdf';
      document.body.appendChild(a);
      a.click();
      setTimeout(() => {
        URL.revokeObjectURL(url);
        a.remove();
      }, 150);
    } catch (err) {
      console.error('Errore download PDF', err);
      alert($language === 'en' ? 'Error generating PDF. Check base64 string.' : 'Errore durante la generazione del PDF. Controlla la stringa base64.');
    }
  }

  // Animazioni progressive
  onMount(() => {
    const progress = document.querySelectorAll('[data-progress]');
    const io = new IntersectionObserver(entries => {
      entries.forEach(e => {
        if (e.isIntersecting) {
          const el = e.target;
          requestAnimationFrame(() => {
            el.style.setProperty('--value', el.getAttribute('data-progress') + '%');
          });
          io.unobserve(el);
        }
      });
    }, { threshold: 0.4 });
    progress.forEach(p => io.observe(p));
  });

  // Derivati rapidi
  const totalYears = (() => {
    const start = new Date('2019-07-01');
    const now = new Date();
    return ( (now - start) / (1000*60*60*24*365) ).toFixed(1);
  })();

  // DASHBOARD WIDGET SYSTEM -------------------------------------------------
  let widgets = [
    { id: 'profile', key: 'cv.profile', type: 'profile' },
    { id: 'summary', key: 'cv.intro', type: 'summary' },
    { id: 'experience', key: 'cv.experience', type: 'experience', span: 2 },
    { id: 'projects', key: 'cv.projects', type: 'projects' },
    { id: 'education', key: 'cv.education', type: 'education' },
    { id: 'skills', key: 'cv.skills', type: 'skills', span: 2 },
    { id: 'extra', key: 'cv.extra', type: 'extra' }
  ];

  // Icon & label metadata for widgets (fa-* icons)
  const widgetMeta = {
    profile: { icon: 'id-card', key: 'cv.profile' },
    summary: { icon: 'info-circle', key: 'cv.intro' },
    experience: { icon: 'briefcase', key: 'cv.experience' },
    projects: { icon: 'folder-open', key: 'cv.projects' },
    education: { icon: 'graduation-cap', key: 'cv.education' },
    skills: { icon: 'tools', key: 'cv.skills' },
    extra: { icon: 'plus-circle', key: 'cv.extra' }
  };

  // Layout masonry: non richiede calcolo manuale (gap eliminati)
  function noop(){}
  const applyGridSpans = noop;

  // UI State (densit rimossa su richiesta)

  // Evidenzia alcune parole chiave nel summary per maggiore leggibilit
  const KEYWORDS = ['backend','microservizi','scalabili','API','AI','intelligenza','docker','node','cloud','game','database'];
  function escapeRegex(str){ return str.replace(/[.*+?^${}()|[\]\\]/g,'\\$&'); }
  function highlight(text){
    const pattern = new RegExp('('+KEYWORDS.map(escapeRegex).join('|')+')','gi');
    return text.replace(pattern, m => `<em>${m}</em>`);
  }
  // Summary translation
  // Use auto-subscribed store function
  $: summaryTranslated = $translate ? $translate('cv.summary') : summary;
</script>

<div class="cv-page" class:dark={$currentTheme === 'dark'}>
  <!-- Top Bar compatta -->
  <header class="top-bar">
    <div class="tb-inner">
      <div class="tb-left">
        <div class="avatar small main">
          <span>{personalInfo.name.split(' ').map(p=>p[0]).join('')}</span>
        </div>
        <div class="id-block">
          <h1>{personalInfo.name}</h1>
          <p class="role">{personalInfo.title}</p>
          <div class="chips">
            <span class="chip"> {personalInfo.location.split(' (')[0]}</span>
            <span class="chip"> {totalYears} {$translate('cv.chip.exp')}</span>
            <span class="chip"> {projects.length} {$translate('cv.chip.projects')}</span>
          </div>
        </div>
      </div>
      <div class="tb-right">
        <button class="btn-primary" on:click={downloadCV}>
          <i class="fas fa-file-download"></i>
          <span>{$translate('cv.download')}</span>
        </button>
  <!-- Toggle densit rimosso -->
        <div class="mini-contacts">
          <a href={'mailto:'+personalInfo.email} title={$translate('contact.email')}><i class="fas fa-envelope"></i></a>
          <a href={'tel:'+personalInfo.phone} title={$translate('cv.phone')}><i class="fas fa-phone"></i></a>
        </div>
      </div>
    </div>
  </header>

  <!-- DASHBOARD -->
  <main class="dashboard-wrapper">
    <div class="dashboard-grid">
      {#each widgets as w (w.id)}
  <div class="widget-card" data-id={w.id} class:wide={w.span===2}>
          <div class="widget-head">
            <span class="icon"><i class={`fas fa-${widgetMeta[w.type].icon}`}></i></span>
            <span class="title">{$translate(widgetMeta[w.type].key)}</span>
            <span class="wh-line"></span>
          </div>
          <div class="widget-body">
            {#if w.type === 'profile'}
              <div class="profile-full">
                <div class="pf-top">
                  <div class="avatar tiny"><span>{personalInfo.name.split(' ').map(p=>p[0]).join('')}</span></div>
                  <div class="who">
                    <h4>{personalInfo.name}</h4>
                    <p class="role">{personalInfo.title}</p>
                  </div>
                </div>
                <p class="intro">{summaryTranslated.split('. ').slice(0,2).join('. ') + '.'}</p>
                <ul class="kv">
                  <li><span>{$translate('cv.experience')}</span><strong>{totalYears} {$translate('cv.years')}</strong></li>
                  <li><span>{$translate('cv.projects')}</span><strong>{projects.length}</strong></li>
                  <li><span>{$translate('cv.education')}</span><strong>{education.length} {$translate('cv.degrees')}</strong></li>
                </ul>
                <div class="links-inline">
                  <a href={'mailto:'+personalInfo.email}><i class="fas fa-envelope"></i> {$translate('contact.email')}</a>
                  <a href={'tel:'+personalInfo.phone}><i class="fas fa-phone"></i> {$translate('cv.phone')}</a>
                </div>
              </div>
            {:else if w.type === 'summary'}
              <div class="text-block">
                <!-- highlight() injects <em> tags; extra invisible sample ensures selector not flagged as unused -->
                <p>{@html highlight(summaryTranslated)}</p>
                <span aria-hidden="true" style="display:none"><em>_</em></span>
              </div>
            {:else if w.type === 'experience'}
              <ol class="timeline-mini rich">
                {#each experiences as exp}
                  <li class="t-item">
                    <div class="head">
                      <strong>{exp.title[$language]}</strong>
                      <span class="company">{exp.company}</span>
                    </div>
                    <div class="meta">{exp.period[$language] || exp.period}  {exp.location[$language]}</div>
                    <ul class="points">
                      {#each exp.description[$language].slice(0,2) as d}
                        <li>{d}</li>
                      {/each}
                    </ul>
                    <div class="stack">
                      {#each exp.technologies.slice(0,6) as t}
                        <span class="tag small alt">{t}</span>
                      {/each}
                    </div>
                  </li>
                {/each}
              </ol>
            {:else if w.type === 'projects'}
              <div class="projects-mini">
                {#each projects as p}
                  <div class="p-row">
                    <div class="p-head">
                      <strong>{p.name}</strong>
                      <span class="badge">{p.duration}</span>
                    </div>
                    <p class="p-desc">{p.description}</p>
                    <div class="stack">
                      {#each p.technologies.slice(0,5) as t}
                        <span class="tag tiny">{t}</span>
                      {/each}
                    </div>
                  </div>
                {/each}
              </div>
            {:else if w.type === 'education'}
              <div class="edu-mini">
                {#each education as edu}
                  <div class="e-row">
                    <strong>{edu.degree[$language]}</strong>
                    <span class="period">{typeof edu.period === 'string' ? edu.period : edu.period[$language]}</span>
                    <span class="school">{typeof edu.school === 'string' ? edu.school : edu.school[$language]}</span>
                    <p class="e-thesis">{edu.thesis[$language]}</p>
                  </div>
                {/each}
              </div>
            {:else if w.type === 'skills'}
              <div class="skills-mini">
                {#each Object.entries(skills) as [cat, list]}
                  <div class="scat">
                    <h5>{cat}</h5>
                    {#each list as s}
                      <div class="s-row" data-progress={s.level}>
                        <span class="nm">{s.name}</span>
                        <div class="bar"><span style="--value:{s.level}%"></span></div>
                        <span class="val">{s.level}%</span>
                      </div>
                    {/each}
                  </div>
                {/each}
              </div>
            {:else if w.type === 'extra'}
              <div class="extra-mini new">
                <div class="certs">
                  <h5>{$translate('cv.certifications')}</h5>
                  {#each certifications as c}
                    <div class="cert-line">
                      <strong>{c.name[$language]}</strong>
                      <span class="issuer">{c.issuer}</span>
                      <span class="focus">{typeof c.id === 'string' ? c.id : c.id[$language]}</span>
                    </div>
                  {/each}
                </div>
                <div class="langs">
                  <h5>{$translate('cv.languages')}</h5>
                  {#each languages as l}
                    <div class="lang-line">
                      <span>{$translate(l.nameKey)}</span>
                      <span class="lvl">{$translate(l.levelKey)}</span>
                    </div>
                  {/each}
                </div>
              </div>
            {/if}
          </div>
        </div>
      {/each}
    </div>
  </main>
</div>

<style>
  @import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600;700&family=Inter:wght@400;500;600;700&display=swap');

  :root {
    --font-base:16px;
    --font-scale-1:0.72rem;
    --font-scale-2:0.78rem;
    --font-scale-3:0.85rem;
    --font-scale-4:0.95rem;
    --font-scale-5:1.15rem;
    --line-base:1.55;
    --bg-base:#fcfbf8;
    --bg-alt:#f5f3ef;
    --bg-soft:#ebe7e2;
    --panel:#ffffff;
    --panel-border:#e4dfd6;
    --panel-border-strong:#d1c9bd;
  --text:#1f1e1c;
  --text-dim:#58544f;
    --accent1:#6366f1; /* indigo */
    --accent2:#ec4899; /* pink */
    --accent3:#10b981; /* emerald */
    --accent4:#f59e0b; /* amber */
    --grad:linear-gradient(90deg,var(--accent1),var(--accent2));
    --grad-soft:linear-gradient(90deg,rgba(99,102,241,.15),rgba(236,72,153,.15));
    --accent1-soft:rgba(99,102,241,.14);
    --accent2-soft:rgba(236,72,153,.15);
    --radius-lg:22px; --radius-md:14px; --radius-sm:8px;
    --shadow:0 4px 18px -6px rgba(0,0,0,.16);
    --shadow-hover:0 10px 34px -10px rgba(0,0,0,.25);
    --anim:cubic-bezier(.4,0,.2,1);
    --focus-ring:0 0 0 2px #fff,0 0 0 4px var(--accent1);
  }

  /* Dark mode variables override */
  .cv-page.dark {
    --bg-base:#121316;
    --bg-alt:#181a1e;
    --bg-soft:#202328;
    --panel:#1e2126;
    --panel-border:#2c3138;
    --panel-border-strong:#383f47;
    --text:#f2f4f6;
    --text-dim:#9aa2ad;
    --accent1:#818cf8;
    --accent2:#f472b6;
    --accent3:#34d399;
    --accent4:#fbbf24;
    --accent1-soft:rgba(129,140,248,.18);
    --accent2-soft:rgba(244,114,182,.20);
    --grad:linear-gradient(90deg,var(--accent1),var(--accent2));
    --grad-soft:linear-gradient(90deg,rgba(129,140,248,.20),rgba(244,114,182,.18));
    --shadow:0 4px 24px -6px rgba(0,0,0,.55);
    --shadow-hover:0 12px 42px -12px rgba(0,0,0,.65);
  }

  .cv-page { min-height:100vh; font-family:'Inter',system-ui,sans-serif; background:radial-gradient(circle at 18% 20%,var(--bg-alt),var(--bg-base) 55%); color:var(--text); padding-top:0; font-size:var(--font-base); line-height:var(--line-base); }
  /* stile compatto rimosso */

  /* TOP BAR */
  .top-bar { position:sticky; top:0; z-index:40; background:rgba(255,255,255,.92); backdrop-filter:blur(18px) saturate(180%); border-bottom:1px solid var(--panel-border); box-shadow:var(--shadow); }
  .tb-inner { max-width:1420px; margin:0 auto; padding:1rem 2.4rem; display:flex; align-items:center; justify-content:space-between; gap:2rem; }
  .tb-left { display:flex; align-items:center; gap:1.25rem; min-width:0; }
  .avatar.main { width:78px; height:78px; }
  .avatar { background:var(--grad); color:#fff; display:flex; align-items:center; justify-content:center; font-weight:600; letter-spacing:.5px; border-radius:22px; font-size:1.35rem; position:relative; overflow:hidden; box-shadow:var(--shadow); background-size:160% 160%; background-position:35% 55%; transition:.7s var(--anim), transform .45s var(--anim); }
  .avatar:hover { background-position:70% 40%; transform:translateY(-3px); box-shadow:var(--shadow-hover); }
  .id-block h1 { margin:0; font-size:1.65rem; background:var(--grad); -webkit-background-clip:text; background-clip:text; color:transparent; line-height:1.1; font-weight:700; letter-spacing:.5px; }
  .id-block .role { margin:.3rem 0 .75rem; font-size:.7rem; letter-spacing:1.1px; text-transform:uppercase; color:var(--accent3); font-weight:700; }
  .chips { display:flex; flex-wrap:wrap; gap:.5rem; }
  .chip { font-size:.68rem; letter-spacing:.4px; padding:.5rem .75rem .45rem; background:var(--panel); border:1px solid var(--panel-border); border-radius:11px; color:var(--text-dim); box-shadow:0 2px 4px -2px rgba(0,0,0,.06); }
  .tb-right { display:flex; align-items:center; gap:.9rem; }
  .btn-primary { display:inline-flex; align-items:center; gap:.55rem; font-size:.72rem; letter-spacing:1px; text-transform:uppercase; border:none; cursor:pointer; font-weight:600; padding:.95rem 1.35rem .9rem; background:var(--grad); color:#fff; border-radius:16px; box-shadow:var(--shadow); transition:.5s var(--anim); position:relative; }
  .btn-primary:hover { box-shadow:var(--shadow-hover); transform:translateY(-3px); }
  .btn-primary:active { transform:translateY(-1px); }
  .btn-primary:focus-visible { outline:0; box-shadow:var(--focus-ring); }
  .btn-ghost { display:inline-flex; align-items:center; gap:.4rem; font-size:.65rem; letter-spacing:.8px; text-transform:uppercase; font-weight:600; border:1px solid var(--panel-border); background:var(--panel); color:var(--accent1); padding:.8rem 1rem .75rem; border-radius:14px; cursor:pointer; transition:.4s; }
  .btn-ghost:hover { background:var(--accent1-soft); border-color:var(--accent1); }
  .mini-contacts { display:flex; gap:.45rem; }
  .mini-contacts a { width:36px; height:36px; display:flex; align-items:center; justify-content:center; border:1px solid var(--panel-border); border-radius:12px; color:var(--accent1); background:var(--panel); font-size:.8rem; transition:.35s; box-shadow:0 2px 4px -2px rgba(0,0,0,.06); }
  .mini-contacts a:hover { background:var(--accent1-soft); border-color:var(--accent1); }

  /* DASHBOARD */
  .dashboard-wrapper { max-width:1420px; margin:0 auto 4rem; padding:1rem 2.4rem 0; }
  .dashboard-grid { column-width:350px; column-gap:0; }
  @media (max-width:900px){ .dashboard-grid { column-width:300px; } }
  @media (max-width:640px){ .dashboard-grid { column-width:100%; } }
  .widget-card { background:var(--panel); border:1px solid var(--panel-border); display:inline-block; width:100%; box-shadow:var(--shadow); transition:.45s var(--anim); margin:0; vertical-align:top; break-inside:avoid; }
  .widget-card + .widget-card { margin-top:0; }
  /* .widget-card.wide mantenuto per compatibilit pregressa */
  .widget-card:hover { border-color:var(--accent1); box-shadow:var(--shadow-hover); }
  .widget-head { display:flex; align-items:center; gap:.65rem; padding:1rem 1.25rem .95rem; border-bottom:1px solid var(--panel-border); background:linear-gradient(90deg,var(--panel),var(--panel) 70%); position:relative; }
  .widget-head .wh-line { flex:1; height:3px; border-radius:2px; background:var(--grad-soft); margin-left:.5rem; }
  .widget-head .icon { width:30px; height:30px; display:flex; align-items:center; justify-content:center; background:var(--accent1-soft); color:var(--accent1); border:1px solid var(--accent1); border-radius:11px; font-size:.8rem; box-shadow:0 2px 4px -2px rgba(0,0,0,.12); }
  .widget-head .title { font-size:.72rem; letter-spacing:1px; text-transform:uppercase; font-weight:700; color:var(--accent1); }
  .widget-body { flex:1; padding:1.3rem 1.45rem 1.5rem; font-size:var(--font-scale-4); line-height:1.6; display:flex; flex-direction:column; gap:1.1rem; }
  .cv-page.dark .widget-card { box-shadow:0 4px 18px -6px rgba(0,0,0,.7); }
  .cv-page.dark .widget-card:hover { box-shadow:0 10px 34px -10px rgba(0,0,0,.8); }

  /* PROFILE FULL */
  .profile-full { display:flex; flex-direction:column; gap:.9rem; }
  .pf-top { display:flex; gap:.75rem; align-items:center; }
  .avatar.tiny { width:52px; height:52px; font-size:1.05rem; border-radius:16px; }
  .profile-full h4 { margin:0 0 .15rem; font-size:1.05rem; background:var(--grad); -webkit-background-clip:text; background-clip:text; color:transparent; font-weight:700; }
  .profile-full .role { margin:0; font-size:.66rem; letter-spacing:.9px; text-transform:uppercase; color:var(--accent2); font-weight:600; }
  .intro { margin:0; font-size:var(--font-scale-3); line-height:1.6; color:var(--text-dim); }
  .kv { list-style:none; margin:0; padding:0; display:grid; grid-template-columns:repeat(auto-fit,minmax(120px,1fr)); gap:.4rem; }
  .kv li { background:var(--panel); border:1px solid var(--panel-border); border-radius:12px; padding:.6rem .75rem .55rem; display:flex; flex-direction:column; gap:.2rem; box-shadow:0 2px 4px -2px rgba(0,0,0,.06); }
  .kv li span { font-size:.58rem; text-transform:uppercase; letter-spacing:.55px; color:var(--text-dim); }
  .kv li strong { font-size:.9rem; font-weight:600; color:var(--accent1); }
  .links-inline { display:flex; flex-wrap:wrap; gap:.55rem; }
  .links-inline a { font-size:.6rem; letter-spacing:.5px; background:var(--accent1-soft); color:var(--accent1); border:1px solid var(--accent1); padding:.45rem .65rem .4rem; border-radius:999px; text-decoration:none; display:inline-flex; gap:.4rem; align-items:center; font-weight:500; }
  .links-inline a:hover { background:var(--accent2-soft); color:var(--accent2); border-color:var(--accent2); }

  /* TEXT BLOCK */
