<script>
  import { onMount } from "svelte";
  import { currentTheme } from "../../store";

  let heroRef;
  let isVisible = false;

  onMount(() => {
    isVisible = true;
    
    // Animazioni per l'hero section
    const hero = document.querySelector('.hero-section');
    if (hero) {
      const elements = hero.querySelectorAll('.animate-on-load');
      elements.forEach((el, index) => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(30px)';
        setTimeout(() => {
          el.style.transition = 'all 0.8s ease';
          el.style.opacity = '1';
          el.style.transform = 'translateY(0)';
        }, index * 200);
      });
    }

    // Parallax effect
    const handleScroll = () => {
      const scrolled = window.pageYOffset;
      const parallax = document.querySelector('.parallax-bg');
      if (parallax) {
        parallax.style.transform = `translateY(${scrolled * 0.5}px)`;
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  function scrollToSection(sectionId) {
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  }
</script>

<div class="home-container" class:dark-theme={$currentTheme === 'dark'}>
  <!-- Hero Section -->
  <section class="hero-section" bind:this={heroRef}>
    <div class="parallax-bg"></div>
    <div class="hero-content">
      <div class="container">
        <div class="row align-items-center min-vh-100">
          <div class="col-lg-6">            <div class="hero-text">
              <h1 class="hero-title bg-primary animate-on-load">
                Ciao, sono 
                <span class="gradient-text">Giovanni Fragal</span>
              </h1>
              <h2 class="hero-subtitle animate-on-load">
                Software Engineer & Game Developer
              </h2>
              <p class="hero-description animate-on-load">
                Sviluppatore Software con laurea in Ingegneria Informatica, specializzato 
                nella progettazione di applicazioni web e microservizi. Appassionato di 
                tecnologie innovative e game development.
              </p>
              <div class="hero-buttons animate-on-load">
                <button class="btn btn-primary btn-lg mr-3" on:click={() => scrollToSection('about-preview')}>
                  <i class="fas fa-user mr-2"></i>
                  Scopri di pi
                </button>
                <button class="btn btn-outline-primary btn-lg">
                  <i class="fas fa-download mr-2"></i>
                  Scarica CV
                </button>
              </div>              <div class="social-links animate-on-load">
                <a href="mailto:giovannifragala@gmail.com" class="social-link">
                  <i class="fas fa-envelope"></i>
                </a>
                <a href="tel:3314476871" class="social-link">
                  <i class="fas fa-phone"></i>
                </a>
                <a href="https://github.com/giovannifragala" target="_blank" rel="noopener noreferrer" class="social-link">
                  <i class="fab fa-github"></i>
                </a>
                <a href="https://linkedin.com/in/giovannifragala" target="_blank" rel="noopener noreferrer" class="social-link">
                  <i class="fab fa-linkedin"></i>
                </a>
              </div>
            </div>
          </div>
          <div class="col-lg-6">
            <div class="hero-image animate-on-load">              <div class="floating-elements">
                <div class="floating-element" style="--delay: 0s">
                  <i class="fab fa-js-square"></i>
                </div>
                <div class="floating-element" style="--delay: 1s">
                  <i class="fab fa-node-js"></i>
                </div>
                <div class="floating-element" style="--delay: 2s">
                  <i class="fab fa-docker"></i>
                </div>
                <div class="floating-element" style="--delay: 0.5s">
                  <i class="fas fa-leaf"></i>
                </div>
                <div class="floating-element" style="--delay: 1.5s">
                  <i class="fas fa-database"></i>
                </div>
              </div>
              <div class="profile-container">
                <div class="profile-image">
                  <img src="/images/profile.jpg" alt="Ross" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="scroll-indicator">
      <div class="scroll-mouse">
        <div class="scroll-wheel"></div>
      </div>
      <span>Scorri per esplorare</span>
    </div>
  </section>

  <!-- Quick About Section -->
  <section id="about-preview" class="about-preview-section">
    <div class="container">
      <div class="row">        <div class="col-lg-8 mx-auto text-center">
          <h2 class="section-title">La mia passione</h2>
          <p class="section-description">
            Sviluppatore Software con esperienza in applicazioni web e microservizi, 
            con un focus particolare su Node.js, Svelte, Docker e MongoDB. 
            Appassionato di game development e tecnologie innovative.
          </p>
        </div>
      </div>
      <div class="row mt-5">        <div class="col-md-4 mb-4">
          <div class="feature-card">
            <div class="feature-icon">
              <i class="fas fa-project-diagram"></i>
            </div>
            <h4>Microservizi</h4>
            <p>Progetto architetture scalabili con approccio microservizi e Docker.</p>
          </div>
        </div>
        <div class="col-md-4 mb-4">
          <div class="feature-card">
            <div class="feature-icon">
              <i class="fas fa-gamepad"></i>
            </div>
            <h4>Game Development</h4>
            <p>Sviluppo giochi innovativi e esperienze interattive coinvolgenti.</p>
          </div>
        </div>
        <div class="col-md-4 mb-4">
          <div class="feature-card">
            <div class="feature-icon">
              <i class="fas fa-database"></i>
            </div>
            <h4>Database Design</h4>
            <p>Progettazione di database efficienti con MongoDB e MySQL.</p>
          </div>
        </div>
      </div>
    </div>
  </section>

  <!-- Latest Projects Preview -->
  <section class="projects-preview-section">
    <div class="container">
      <div class="row">
        <div class="col-lg-8 mx-auto text-center">
          <h2 class="section-title">Progetti in evidenza</h2>
          <p class="section-description">
            Alcuni dei miei lavori pi recenti e significativi
          </p>
        </div>
      </div>      <div class="row mt-5">
        <div class="col-lg-4 mb-4">
          <div class="project-card">
            <div class="project-image">
              <img src="/images/project1.jpg" alt="Color Swipe Challenge" />
              <div class="project-overlay">
                <button class="btn btn-light">
                  <i class="fas fa-eye mr-2"></i>
                  Visualizza
                </button>
              </div>
            </div>
            <div class="project-content">
              <h4>Color Swipe Challenge</h4>
              <p>Puzzle game iper-casuale pubblicato su Google Play Store.</p>
              <div class="project-tags">
                <span class="tag">Game Dev</span>
                <span class="tag">Mobile</span>
                <span class="tag">Unity</span>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-4 mb-4">
          <div class="project-card">
            <div class="project-image">
              <img src="/images/project2.jpg" alt="Home Utility App" />
              <div class="project-overlay">
                <button class="btn btn-light">
                  <i class="fas fa-eye mr-2"></i>
                  Visualizza
                </button>
              </div>
            </div>
            <div class="project-content">
              <h4>Home Utility App</h4>
              <p>Web app per utility familiari con cache e gestione keywords.</p>
              <div class="project-tags">
                <span class="tag">Node.js</span>
                <span class="tag">MongoDB</span>
                <span class="tag">Svelte</span>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-4 mb-4">
          <div class="project-card">
            <div class="project-image">
              <img src="/images/project3.jpg" alt="PvP Game" />
              <div class="project-overlay">
                <button class="btn btn-light">
                  <i class="fas fa-eye mr-2"></i>
                  Visualizza
                </button>
              </div>
            </div>
            <div class="project-content">
              <h4>Gioco PvP Mobile</h4>
              <p>Gioco di ruolo PvP per mobile con IA avanzata (WIP).</p>
              <div class="project-tags">
                <span class="tag">Python</span>
                <span class="tag">GTGE</span>
                <span class="tag">TensorFlow</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</div>

<style>
  .home-container {
    min-height: 100vh;
    overflow-x: hidden;
  }

  .hero-section {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    background: var(--gradient, linear-gradient(135deg, #667eea 0%, #764ba2 100%));
    overflow: hidden;
    transition: background 0.5s ease;
  }

  .dark-theme .hero-section {
    background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  }

  .parallax-bg {
    position: absolute;
    top: -20%;
    left: 0;
    width: 100%;
    height: 120%;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><polygon fill="rgba(255,255,255,0.1)" points="0,0 1000,300 1000,1000 0,700"/></svg>');
    background-size: cover;
    will-change: transform;
  }

  .hero-content {
    position: relative;
    z-index: 2;
    width: 100%;
  }

  .hero-title {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    line-height: 1.2;
    color: var(--text-primary);
  }

  .hero-subtitle {
    font-size: 1.5rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.9);
    margin-bottom: 1.5rem;
  }

  .hero-description {
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 2rem;
    line-height: 1.6;
  }

  .gradient-text {
    background: linear-gradient(45deg, #ff6b6b, #feca57);
    -webkit-background-clip: text;
    /* -webkit-text-fill-color: transparent; */
    background-clip: text;
  }

  .hero-buttons {
    margin-bottom: 2rem;
  }

  .btn {
    padding: 12px 30px;
    border-radius: 30px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: all 0.3s ease;
    border: 2px solid transparent;
  }

  .btn-primary {
    background: linear-gradient(45deg, #ff6b6b, #feca57);
    border: none;
    color: white;
  }

  .btn-primary:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 25px rgba(255, 107, 107, 0.3);
  }

  .btn-outline-primary {
    color: white;
    border-color: white;
    background: transparent;
  }

  .btn-outline-primary:hover {
    background: white;
    color: #667eea;
    transform: translateY(-3px);
  }

  .social-links {
    display: flex;
    gap: 1rem;
  }

  .social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    color: white;
    font-size: 1.2rem;
    transition: all 0.3s ease;
    text-decoration: none;
  }

  .social-link:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateY(-3px);
    color: white;
    text-decoration: none;
  }

  .hero-image {
    position: relative;
    height: 500px;
  }

  .floating-elements {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .floating-element {
    position: absolute;
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.7);
    animation: float 6s ease-in-out infinite;
    animation-delay: var(--delay);
  }

  .floating-element:nth-child(1) { top: 10%; left: 20%; }
  .floating-element:nth-child(2) { top: 20%; right: 10%; }
  .floating-element:nth-child(3) { bottom: 30%; left: 10%; }
  .floating-element:nth-child(4) { bottom: 10%; right: 30%; }
  .floating-element:nth-child(5) { top: 50%; left: 50%; }

  @keyframes float {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-20px); }
  }

  .profile-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .profile-image {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid rgba(255, 255, 255, 0.3);
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  }

  .profile-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .scroll-indicator {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    color: rgba(255, 255, 255, 0.7);
    animation: bounce 2s infinite;
  }

  .scroll-mouse {
    width: 24px;
    height: 40px;
    border: 2px solid rgba(255, 255, 255, 0.7);
    border-radius: 12px;
    margin: 0 auto 10px;
    position: relative;
  }

  .scroll-wheel {
    width: 4px;
    height: 8px;
    background: rgba(255, 255, 255, 0.7);
    border-radius: 2px;
    position: absolute;
    top: 6px;
    left: 50%;
    transform: translateX(-50%);
    animation: scroll-wheel 2s infinite;
  }

  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateX(-50%) translateY(0); }
    40% { transform: translateX(-50%) translateY(-10px); }
    60% { transform: translateX(-50%) translateY(-5px); }
  }

  @keyframes scroll-wheel {
    0% { top: 6px; opacity: 1; }
    100% { top: 20px; opacity: 0; }
  }

  /* Sections */
  .about-preview-section,
  .projects-preview-section {
    padding: 100px 0;
    background: white;
  }

  .dark-theme .about-preview-section,
  .dark-theme .projects-preview-section {
    background: #1e1e1e;
    color: white;
  }

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #2c3e50;
  }

  .dark-theme .section-title {
    color: white;
  }

  .section-description {
    font-size: 1.1rem;
    color: #7f8c8d;
    line-height: 1.6;
  }

  .dark-theme .section-description {
    color: #bdc3c7;
  }

  .feature-card {
    text-align: center;
    padding: 2rem;
    border-radius: 15px;
    background: #f8f9fa;
    transition: all 0.3s ease;
    height: 100%;
  }

  .dark-theme .feature-card {
    background: #2c3e50;
  }

  .feature-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  }

  .feature-icon {
    width: 80px;
    height: 80px;
    background: linear-gradient(45deg, #3498db, #9b59b6);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1.5rem;
    font-size: 2rem;
    color: white;
  }

  .project-card {
    background: white;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }

  .dark-theme .project-card {
    background: #2c3e50;
  }

  .project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  }

  .project-image {
    position: relative;
    height: 200px;
    overflow: hidden;
  }

  .project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }

  .project-card:hover .project-image img {
    transform: scale(1.1);
  }

  .project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(52, 152, 219, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  .project-card:hover .project-overlay {
    opacity: 1;
  }

  .project-content {
    padding: 1.5rem;
  }

  .project-content h4 {
    margin-bottom: 0.5rem;
    color: #2c3e50;
  }

  .dark-theme .project-content h4 {
    color: white;
  }

  .project-content p {
    color: #7f8c8d;
    margin-bottom: 1rem;
  }

  .dark-theme .project-content p {
    color: #bdc3c7;
  }

  .project-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .tag {
    background: #ecf0f1;
    color: #2c3e50;
    padding: 0.25rem 0.75rem;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: 600;
  }

  .dark-theme .tag {
    background: #34495e;
    color: white;
  }

  @media (max-width: 768px) {
    .hero-title {
      font-size: 2.5rem;
    }
    
    .hero-subtitle {
      font-size: 1.2rem;
    }
    
    .hero-buttons {
      flex-direction: column;
      gap: 1rem;
    }
    
    .hero-buttons .btn {
      width: 100%;
    }
    
    .profile-image {
      width: 200px;
      height: 200px;
    }
  }
</style>
