import App from './App.svelte';

const app = new App({
	target: document.body,
	props: {
		name: 'world'
	}
});

export default app;

// Global click/pointer/touch coordinate logger
// Usage: window.logClickCoords.start() to enable (also enabled by default)
//        window.logClickCoords.stop() to disable
//        window.logClickCoords.log(event) to manually log an event
;(function attachGlobalClickLogger(){
	const state = { enabled: false };

	function getPageFromClient(clientX, clientY){
		return {
			pageX: clientX + (window.pageXOffset || document.documentElement.scrollLeft || 0),
			pageY: clientY + (window.pageYOffset || document.documentElement.scrollTop || 0)
		};
	}

	function extractFromEvent(e){
		// normalize touch/pointer/click
		let type = e.type || 'event';
		let primary = null;
		if (e.touches && e.touches.length) primary = e.touches[0];
		else if (e.changedTouches && e.changedTouches.length) primary = e.changedTouches[0];
		else primary = e;

		const screenX = primary.screenX ?? null;
		const screenY = primary.screenY ?? null;
		const clientX = primary.clientX ?? null;
		const clientY = primary.clientY ?? null;
		const page = getPageFromClient(clientX || 0, clientY || 0);
		const pageX = primary.pageX ?? page.pageX;
		const pageY = primary.pageY ?? page.pageY;
		const offsetX = primary.offsetX ?? (primary.layerX ?? null);
		const offsetY = primary.offsetY ?? (primary.layerY ?? null);
		const movementX = primary.movementX ?? null;
		const movementY = primary.movementY ?? null;

		const target = e.target || (primary.target || null);
		let rect = null;
		let inTargetX = null;
		let inTargetY = null;
		let percentInTargetX = null;
		let percentInTargetY = null;
		try{
			if (target && target.getBoundingClientRect && clientX != null && clientY != null){
				rect = target.getBoundingClientRect();
				inTargetX = clientX - rect.left;
				inTargetY = clientY - rect.top;
				percentInTargetX = rect.width ? (inTargetX / rect.width) * 100 : null;
				percentInTargetY = rect.height ? (inTargetY / rect.height) * 100 : null;
			}
		}catch(err){ /* ignore */ }

		const viewportWidth = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
		const viewportHeight = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);

		const percentInViewportX = (clientX != null && viewportWidth) ? (clientX / viewportWidth) * 100 : null;
		const percentInViewportY = (clientY != null && viewportHeight) ? (clientY / viewportHeight) * 100 : null;

		return {
			eventType: type,
			timestamp: Date.now(),
			screenX, screenY,
			clientX, clientY,
			pageX, pageY,
			offsetX, offsetY,
			movementX, movementY,
			inTargetX, inTargetY,
			percentInTargetX, percentInTargetY,
			percentInViewportX, percentInViewportY,
			viewportWidth, viewportHeight,
			scrollX: window.pageXOffset || document.documentElement.scrollLeft || 0,
			scrollY: window.pageYOffset || document.documentElement.scrollTop || 0,
			targetTag: target && target.tagName ? target.tagName.toLowerCase() : null,
			targetId: target && target.id ? target.id : null,
			targetClasses: target && target.className ? target.className : null
		};
	}

	function prettyLog(coords){
		const label = `[coords] ${coords.eventType} @ ${new Date(coords.timestamp).toISOString()}`;
		try{
			console.groupCollapsed(label);
			console.log('Screen:', { x: coords.screenX, y: coords.screenY });
			console.log('Client (viewport):', { x: coords.clientX, y: coords.clientY });
			console.log('Page (document):', { x: coords.pageX, y: coords.pageY });
			console.log('Offset/Layer:', { x: coords.offsetX, y: coords.offsetY });
			console.log('Movement:', { x: coords.movementX, y: coords.movementY });
			console.log('In target element (px):', { x: coords.inTargetX, y: coords.inTargetY });
			console.log('In target element (%):', { x: coords.percentInTargetX, y: coords.percentInTargetY });
			console.log('In viewport (%):', { x: coords.percentInViewportX, y: coords.percentInViewportY });
			console.log('Viewport size:', { width: coords.viewportWidth, height: coords.viewportHeight });
			console.log('Scroll offsets:', { x: coords.scrollX, y: coords.scrollY });
			console.log('Target element:', { tag: coords.targetTag, id: coords.targetId, classes: coords.targetClasses });
			console.groupEnd();
		}catch(err){
			// fallback
			console.log(label, coords);
		}
	}

	function handler(e){
		const coords = extractFromEvent(e);
		prettyLog(coords);
	}

	// Public API
	const api = {
		start(){
			if (state.enabled) return;
			state.enabled = true;
			// Listen for multiple pointer types
			window.addEventListener('click', handler, { passive: true });
			window.addEventListener('pointerdown', handler, { passive: true });
			window.addEventListener('pointerup', handler, { passive: true });
			window.addEventListener('touchstart', handler, { passive: true });
			window.addEventListener('touchend', handler, { passive: true });
			// expose single-event logger
			window.logClickCoords = api;
		},
		stop(){
			if (!state.enabled) return;
			state.enabled = false;
			window.removeEventListener('click', handler);
			window.removeEventListener('pointerdown', handler);
			window.removeEventListener('pointerup', handler);
			window.removeEventListener('touchstart', handler);
			window.removeEventListener('touchend', handler);
		},
		log(e){
			// manually log an event object
			try{
				const coords = extractFromEvent(e || window.event || {});
				prettyLog(coords);
			}catch(err){ console.error('log error', err); }
		},
		isActive(){ return !!state.enabled }
	};

	// Enable by default
	api.start();

	// attach to window if not already
	if (!window.logClickCoords) window.logClickCoords = api;
})();