<script>
  import TreeNode from './TreeNode.svelte';
  export let skill;
  export let selected;
  export let selectSkill;
  // No external progress: use the level declared on each skill object

  // Reactive intensity based on level/max (0 -> locked style; >0 scales up to current max)
  $: lvl = (skill && skill.level) || 0;
  $: max = (skill && skill.max) || 1;
  $: t = Math.min(1, Math.max(0, max ? lvl / max : 0));
  // Start a bit visible when unlocked (min ~0.3), ramp to 1.0 at max level
  $: intensity = lvl === 0 ? 0 : 0.3 + 0.7 * t;
  // Precompute CSS variable string (kept <= current visual maxima)
  $: styleVars = `--int:${intensity};`+
    `--glow-a1:${(0.35 * intensity).toFixed(3)};`+
    `--glow-a2:${(0.50 * intensity).toFixed(3)};`+
    `--border-a:${(0.80 * intensity).toFixed(3)};`+
    `--shadow-a:${(0.15 * intensity).toFixed(3)};`+
    `--line-a-top:${(0.80 * intensity).toFixed(3)};`+
    `--line-a-bot:${(0.40 * intensity).toFixed(3)};`+
    `--label-op:${(0.6 + 0.4 * intensity).toFixed(3)};`+
    `--icon-op:${(0.8 + 0.2 * intensity).toFixed(3)};`;
</script>

<div class="tree-branch">
  <div class="skill-node-wrapper" style={lvl > 0 ? styleVars : undefined}>
  <div class="skill-name" class:unlocked={(skill.level || 0) > 0}>{skill.name}</div>
    <div 
      class="node-core {(skill.level || 0) > 0 ? 'unlocked' : 'locked'} {selected === skill.id ? 'selected' : ''}"
      tabindex="0" role="button"
      on:click={() => selectSkill(skill)}
      on:keydown={(e) => (e.key === 'Enter' || e.key === ' ') && selectSkill(skill)}
    >
      <i class="fas {skill.icon}"></i>
      <!-- Small level progress bar and ratio inside the node -->
      <div class="node-progress" aria-hidden="true">
        <div class="progress-bar">
          <div class="fill" style="--progress:{Math.min(1, ((skill.level||0) / (skill.max || 1)))}"></div>
        </div>
        <span class="ratio">{skill.level||0}/{skill.max}</span>
      </div>
    </div>

    {#if skill.children && skill.children.length > 0}
      <div class="connection-line"></div>
      <div class="children-container">
        {#each skill.children as child}
          <TreeNode skill={child} selected={selected} selectSkill={selectSkill} />
        {/each}
      </div>
    {/if}
  </div>
</div>

<style>
  .tree-branch {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .skill-node-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .node-core {
    width: 76px;
    height: 76px;
    border-radius: 8px;
    /* background: linear-gradient(135deg, rgba(255, 215, 0, 0.15), rgba(255, 215, 0, 0.25)); */
    border: 2px solid rgba(148, 163, 184, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 12px rgba(255, 215, 0, 0.25);
    position: relative;
    transition: transform 0.25s ease;
  }

  .node-core:hover { transform: scale(1.05); }

  .node-core.unlocked {
    background: linear-gradient(
      135deg,
      rgba(255, 215, 0, var(--glow-a1, 0.35)),
      rgba(255, 215, 0, var(--glow-a2, 0.5))
    );
    border-color: rgba(255, 215, 0, var(--border-a, 0.8));
  }

  .node-core.selected {
    outline: none;
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.45), 0 0 18px rgba(255, 215, 0, 0.35);
    border-color: #3b82f6;
  }

  .node-core i {
    font-size: 28px;
    color: #e5e7eb;
    filter: drop-shadow(0 0 4px rgba(0,0,0,0.3));
  }

  .node-core.unlocked i {
    color: #ffd700;
    opacity: var(--icon-op, 1);
    /* filter: drop-shadow(0 0 6px rgba(255, 215, 0, 0.6)); */
  }

  /* Compact level progress bar inside the node */
  .node-core .node-progress {
    position: absolute;
    bottom: 3px; /* a bit more space from the icon above */
    left: 6px;
    right: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    pointer-events: none;
  }

  .node-core .progress-bar {
    width: 28px; /* even narrower bar */
    height: 3px; /* slimmer */
    background: rgba(148, 163, 184, 0.35);
    border-radius: 2px;
    overflow: hidden;
  }

  .node-core .progress-bar .fill {
    height: 100%;
    width: calc(var(--progress, 0) * 100%);
    background: linear-gradient(90deg, rgba(255, 215, 0, 0.8), #ffd700);
    border-radius: 2px;
    transition: width 0.3s ease;
  }

  .node-core .ratio {
    font-size: 9px; /* smaller numeretto */
    font-weight: 700;
    color: #cbd5e1;
  }

  .node-core.unlocked .ratio {
    color: #f8fafc;
    text-shadow: 0 0 4px rgba(0,0,0,0.3);
  }

  .skill-name {
    font-size: 12px;
    font-weight: 600;
    color: #cbd5e1;
    text-align: center;
    margin-bottom: 6px;
  }

  .skill-name.unlocked {
    color: #f1f5f9;
    opacity: var(--label-op, 1);
  }

  .connection-line {
    width: 3px;
    height: 40px;
    background: rgba(148, 163, 184, 0.4);
    margin: 15px 0;
  }

  .node-core.unlocked + .connection-line {
    background: linear-gradient(
      180deg,
      rgba(255, 215, 0, var(--line-a-top, 0.8)),
      rgba(255, 215, 0, var(--line-a-bot, 0.4))
    );
  }

  .children-container {
    display: flex;
    gap: 40px;
    align-items: flex-start;
    position: relative;
  }

  .children-container::before {
    content: '';
    position: absolute;
    top: -15px;
    left: 50%;
    right: 50%;
    height: 3px;
    background: rgba(148, 163, 184, 0.4);
    transform: translateX(-50%);
    width: calc(100% - 40px);
  }
</style>
