<script>
  import { onMount, tick } from 'svelte';
  import TreeNode from './TreeNode.svelte';
  import categories from '../../data/skillsCategories.js';
  import { language } from '../../store';
  import { translate } from '../../lib/i18n';

  /* =============================================
    ULTRA REDESIGN: Skill Tree stile Astral Chain / Cyberpunk
    - Design scuro elegante con nodi circolari
    - Connessioni gialle brillanti
    - Layout verticale ramificato con icone
    - Effetti glow e animazioni fluide
  ============================================== */

  // Levels are declared in the external data file (skill.level)

  let currentCat = categories[0].id;
  let selected = null;
  let svg; let treeEl;
  let active; // reactive current category object
  // Autoscale roots row to keep them side-by-side by shrinking when needed
  let scalerEl; let rootRowEl; let scale = 1; let contentH = 0;

  // Reactive active category; ensures template updates when currentCat changes
  $: active = categories.find(c=>c.id===currentCat);
  function skillById(id){ for(const c of categories){ const f = c.skills.find(s=>s.id===id); if(f) return f;} }
  function rank(id){ const s = skillById(id); return (s && (s.level || 0)) || 0; }
  function isUnlocked(skill){ return !skill.req || skill.req.every(r=> rank(r) > 0); }
  function isMaxed(skill){ return rank(skill.id) >= (skill.max || 0); }

  // Build skill tree hierarchy (iterative to avoid recursion limits)
  function buildSkillTree(skills) {
    // Create a map of skill ID to skill object with children array
    const map = new Map();
    skills.forEach(s => {
      map.set(s.id, { ...s, children: [] });
    });
    // Populate children arrays based on req dependencies
    skills.forEach(s => {
      if (s.req) {
        s.req.forEach(parentId => {
          const parent = map.get(parentId);
          if (parent) {
            parent.children.push(map.get(s.id));
          }
        });
      }
    });
    // Return roots (skills without req)
    return Array.from(map.values()).filter(s => !s.req || s.req.length === 0);
  }

  $: skillTree = active ? buildSkillTree(active.skills) : [];

  // Totals for header: number of unlocked skills (level>0) and total skills
  $: totalSkills = categories.reduce((acc, c) => acc + (c.skills ? c.skills.length : 0), 0);
  $: unlockedSkills = categories.reduce(
    (acc, c) => acc + (c.skills ? c.skills.filter(s => (s.level || 0) > 0).length : 0),
    0
  );

  function selectSkill(skill){
    selected = skill.id;
  }

  async function drawLines(){ 
    // Grid layout doesn't need SVG connections
    return;
  }

  function changeCategory(newCatId) {
    currentCat = newCatId;
    selected = null;
    // Force redraw after category change
    tick().then(() => { drawLines(); updateScale(); });
  }

  function updateScale(){
    if(!scalerEl || !rootRowEl) return;
    // Natural dimensions
    const availableW = scalerEl.clientWidth || 0;
    const contentW = rootRowEl.scrollWidth || 0;
    contentH = rootRowEl.offsetHeight || 0;
    if(availableW === 0 || contentW === 0){ scale = 1; return; }
    const desired = availableW / contentW;
    const minScale = 0.55; // don't go too tiny
    // When scaling down, keep a tiny headroom to prevent right-edge clipping
    const headroom = 0.03;
    if (desired < 1) {
      scale = Math.min(1, Math.max(minScale, desired - headroom));
    } else {
      scale = 1;
    }
  }

  function handleResize(){
    updateScale();
  }

  onMount(()=>{ 
    drawLines(); 
    updateScale();
    window.addEventListener('resize', drawLines);
    window.addEventListener('resize', handleResize);
    return ()=>{
      window.removeEventListener('resize', drawLines);
      window.removeEventListener('resize', handleResize);
    }
  });
  // Redraw lines when category changes or after DOM updates
  $: if (active) { tick().then(()=> { drawLines(); updateScale(); }); }
  $: selectedSkill = selected ? skillById(selected) : null;

  function handleKey(e){
    if(e.key === 'Escape' && selected){ selected = null; }
  }

  // Levels are static in categories now; no randomize/reset logic.

  let background = "images/skill-tree-background6.png"

</script>

<svelte:window on:keydown={handleKey} />

<div class="astral-skill-tree">
<!-- <div class="astral-skill-tree" style="background-image: url({background}); background-size: cover; background-position: center; "> -->
  <!-- Header con logo e progress globale -->
  <header class="header">
    <div class="brand">
      <i class="fas fa-atom"></i>
      <h1>{$translate('skills.title')}</h1>
    </div>
    <div class="global-stats">
      <div class="stat">
        <span class="label">{$language === 'it' ? 'Skill Totali' : 'Total Skills'}</span>
        <span class="value">{unlockedSkills} / {totalSkills}</span>
      </div>
      <div class="stat">
        <span class="label">{$language === 'it' ? 'Livello' : 'Level'}</span>
        <span class="value">{Math.floor(categories.flatMap(c=>c.skills).reduce((a,s)=>a+(s.level||0),0)/5) + 1}</span>
      </div>
    </div>
  </header>

  <div class="main-container" class:has-detail={!!selectedSkill}>
    <!-- Sidebar categorie -->
    <aside class="categories">
      {#each categories as cat}
        <div 
          class="category {cat.id===currentCat?'active':''}" 
          style="--cat-color:{cat.color}; --cat-accent:{cat.accent}"
          tabindex="0" role="button"
          on:click={() => changeCategory(cat.id)}
          on:keydown={(e)=> (e.key==='Enter'||e.key===' ') && changeCategory(cat.id)}
        >
          <div class="cat-icon">
            <i class="fas {cat.icon}"></i>
          </div>
          <div class="cat-info">
            <h3>{cat.name}</h3>
            <div class="progress-ring">
              <div class="ring-fill" style="--progress:{cat.skills.reduce((a,s)=>a+(s.level||0),0)/cat.skills.reduce((a,s)=>a+s.max,0)}"></div>
              <span class="progress-text">{cat.skills.reduce((a,s)=>a+(s.level||0),0)}/{cat.skills.reduce((a,s)=>a+s.max,0)}</span>
            </div>
          </div>
        </div>
      {/each}
      
    </aside>

    <!-- Area skill tree -->
    <main class="tree-area">
      <div class="tree-bg"></div>
      {#if active}
        <div class="skill-constellation" bind:this={treeEl}>
          <h2 class="tree-title">{active.name}</h2>
          
          <div class="roots-scaler" bind:this={scalerEl} style="--h:{contentH * scale}px">
            <div class="roots-row {scale < 1 ? 'scaled' : ''}" bind:this={rootRowEl} style="--scale:{scale}">
              <div class="skill-tree-container">
        {#each skillTree as skill}
                  <div class="root-tree">
          <TreeNode skill={skill} selected={selected} selectSkill={selectSkill} />
                  </div>
                {/each}
              </div>
            </div>
          </div>
        </div>
      {/if}
    </main>

    {#if selectedSkill}
      <div 
        class="detail-backdrop" 
        role="button" tabindex="0" aria-label="Close details"
        on:click={() => (selected = null)}
        on:keydown={(e) => (e.key === 'Enter' || e.key === ' ') && (selected = null)}
      ></div>
    {/if}

    <!-- Pannello dettagli -->
    <aside class="details-panel">
      {#if selectedSkill}
        <div class="skill-details" style="--accent:{active?.accent}">
          <div class="detail-header">
            <div class="skill-icon">
              <i class="fas {selectedSkill.icon}"></i>
            </div>
            <div class="skill-title">
              <h3>{selectedSkill.name}</h3>
              <div class="skill-level">
                {$language === 'it' ? 'Livello Attuale' : 'Current Level'}: {selectedSkill.level || 0} / {selectedSkill.max}
              </div>
            </div>
          </div>
          
          <div class="level-descriptions">
            <h4>{$language === 'it' ? 'Progressione Skill' : 'Skill Progression'}</h4>
            {#each selectedSkill.desc as desc, index}
              {@const levelNumber = index + 1}
              {@const isActiveLevel = (selectedSkill.level || 0) === levelNumber}
              {@const displayText = typeof desc === 'object' ? (desc[$language] || desc.en || desc.it) : desc}
              <p class="level-line">
                <span class="level-label">{$language === 'it' ? 'Livello' : 'Level'} {levelNumber}:</span>
                {#if isActiveLevel}
                  <strong>{displayText}</strong>
                {:else}
                  {displayText}
                {/if}
              </p>
            {/each}
          </div>

          {#if selectedSkill.req}
            <div class="requirements">
              <h4>{$language === 'it' ? 'Prerequisiti' : 'Prerequisites'}</h4>
              <div class="req-list">
                {#each selectedSkill.req as reqId}
                  {@const reqSkill = skillById(reqId)}
                  {#if reqSkill}
                    <div class="req-item {(reqSkill.level||0) > 0 ? 'completed' : 'pending'}">
                      <i class="fas {reqSkill.icon}"></i>
                      <span>{reqSkill.name}</span>
                      <div class="req-status">
                        {#if (reqSkill.level||0) > 0}
                          <i class="fas fa-check"></i>
                        {:else}
                          <i class="fas fa-lock"></i>
                        {/if}
                      </div>
                    </div>
                  {/if}
                {/each}
              </div>
            </div>
          {/if}
        </div>
  {/if}
    </aside>
  </div>
</div>
<style>

  .astral-skill-tree {
    min-height: 100vh;
    background: 
      radial-gradient(ellipse at top, rgba(59, 130, 246, 0.15) 0%, transparent 70%),
      radial-gradient(ellipse at bottom, rgba(255, 215, 0, 0.1) 0%, transparent 70%),
      linear-gradient(180deg, #1e293b 0%, #334155 50%, #475569 100%);
    color: #f8fafc;
  /* Use monospace font for all texts within this component */
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow-x: hidden;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 40px;
    background: rgba(30, 41, 59, 0.9);
    border-bottom: 1px solid rgba(255, 215, 0, 0.3);
    backdrop-filter: blur(10px);
  }

  .brand {
    display: flex;
    align-items: center;
    gap: 15px;
    font-family: monospace;
  }


  .brand i {
    font-size: 32px;
    color: #ffd700;
    filter: drop-shadow(0 0 10px rgba(255, 215, 0, 0.6));
    animation: pulse 3s ease-in-out infinite;
  }

  .brand h1 {
    margin: 0;
    font-size: 28px;
    font-weight: 300;
    color: #ffd700;
    text-shadow: 0 0 20px rgba(255, 215, 0, 0.4);
    letter-spacing: 2px;
  }

  .global-stats {
    display: flex;
    gap: 30px;
  }

  .stat {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }

  .stat .label {
    font-size: 12px;
    color: #a0a0b0;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .stat .value {
    font-size: 18px;
    font-weight: bold;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
  }

  .main-container {
    display: grid;
    grid-template-columns: 280px 1fr;
    flex: 1;
    overflow: hidden;
  }

  .main-container.has-detail {
    grid-template-columns: 280px 1fr 320px;
  }

  .categories {
    background: rgba(30, 41, 59, 0.8);
    border-right: 1px solid rgba(59, 130, 246, 0.3);
    padding: 30px 20px;
    overflow-y: auto;
    backdrop-filter: blur(5px);
  }

  .category {
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 18px 20px;
    margin-bottom: 15px;
    background: rgba(51, 65, 85, 0.6);
    border: 1px solid rgba(148, 163, 184, 0.2);
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
  }

  .category::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.2), transparent);
    transition: left 0.6s;
  }

  .category:hover::before,
  .category.active::before {
    left: 100%;
  }

  .category:hover,
  .category.active {
    background: rgba(59, 130, 246, 0.2);
    border-color: rgba(59, 130, 246, 0.5);
    transform: translateX(5px);
    box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
  }

  .cat-icon {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(59, 130, 246, 0.4), rgba(59, 130, 246, 0.1));
    border: 2px solid rgba(59, 130, 246, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }

  .cat-icon i {
    font-size: 18px;
    color: #3b82f6;
    filter: drop-shadow(0 0 5px rgba(59, 130, 246, 0.6));
  }

  .cat-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .cat-info h3 {
    margin: 0;
    font-size: 16px;
    color: #f1f5f9;
    font-weight: 500;
  }

  .progress-ring {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .ring-fill {
    width: 30px;
    height: 6px;
    background: rgba(148, 163, 184, 0.3);
    border-radius: 3px;
    position: relative;
    overflow: hidden;
  }

  .ring-fill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: calc(var(--progress, 0) * 100%);
    background: linear-gradient(90deg, rgba(255, 215, 0, 0.8), #ffd700);
    border-radius: 3px;
    transition: width 0.6s ease;
  }

  .progress-text {
    font-size: 11px;
    color: #cbd5e1;
    font-weight: 600;
  }

  /* reset/randomize controls removed */

  .tree-area {
    position: relative;
    overflow: hidden;
    background: rgba(51, 65, 85, 0.3);
  }

  .tree-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
      radial-gradient(circle at 20% 30%, rgba(255, 215, 0, 0.08) 0%, transparent 40%),
      radial-gradient(circle at 80% 70%, rgba(59, 130, 246, 0.08) 0%, transparent 40%),
      radial-gradient(circle at 50% 50%, rgba(34, 197, 94, 0.05) 0%, transparent 60%);
  }

  .skill-constellation {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 60px 40px; /* extra top padding for better spacing */
    overflow: auto;
  }

  .tree-title {
    text-align: center;
    margin: 0 0 50px;
    font-size: 32px;
    font-weight: 300;
    color: #ffd700;
    text-shadow: 0 0 30px rgba(255, 215, 0, 0.4);
    letter-spacing: 3px;
  }

  .connections {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
  }

  .connection {
    stroke-width: 3;
    fill: none;
    filter: url(#glow);
  }

  .connection.active {
    stroke: #ffd700;
    opacity: 0.9;
    animation: flow 2s ease-in-out infinite alternate;
  }

  .connection.inactive {
    stroke: #444;
    stroke-dasharray: 5,5;
    opacity: 0.3;
  }

  .skills-grid {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }

  /* Auto-scale wrapper to keep roots in one row by shrinking when necessary */
  .roots-scaler {
    width: 100%;
    overflow: hidden;
  /* reserve space based on scaled height to prevent overlap */
  min-height: var(--h, auto);
  }

  .roots-row {
  display: inline-block; /* shrink-to-fit content width for measuring */
    transform: scale(var(--scale, 1));
    transform-origin: top center;
  white-space: nowrap; /* keep roots on one row before scaling */
  }

  .roots-row.scaled {
    /* When scaled, anchor to the left and add a tiny left nudge for safety */
    transform-origin: top left;
  /* Nudge left a bit to ensure rightmost content isn't clipped by scroll rounding */
  transform: translateX(-8px) scale(var(--scale, 1));
  }

  .skill-tree-container {
  display: flex;
  flex-direction: row; /* place root trees next to each other */
  align-items: flex-start; /* align tops */
  justify-content: center; /* center the group */
  gap: 40px; /* horizontal space between trees */
  padding: 20px;
    flex-wrap: wrap; /* allow wrapping when too many trees */
  }

  /* When scaling down, reduce fixed gaps so visuals remain compact */
  .roots-row[style*="--scale:"] .skill-tree-container { gap: calc(40px * var(--scale, 1)); padding: calc(20px * var(--scale, 1)); }
  .roots-row .skill-tree-container { flex-wrap: nowrap; }

  /* Each root tree wraps its subtree and sizes to its content */
  .root-tree {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: max-content;
  }

  /* Responsive tweaks for tighter layouts */
  @media (max-width: 1200px) {
    .skill-tree-container { gap: 32px; }
  }

  @media (max-width: 992px) {
    .skill-tree-container { gap: 24px; padding: 16px; }
  }

  @media (max-width: 768px) {
    .skill-tree-container { gap: 20px; padding: 12px; }
  }

  /* Plain text level descriptions */
  .level-descriptions { margin-bottom: 20px; }
  .level-descriptions h4 { margin: 0 0 10px; font-weight: 600; font-size: 14px; color: #e2e8f0; }
  .level-line { margin: 6px 0; color: #cbd5e1; font-size: 14px; }
  .level-label { display: inline-block; width: 86px; color: #94a3b8; font-weight: 600; }
  .level-line strong { color: #ffd700; font-weight: 600; }

  .skill-row {
    display: flex;
    align-items: center;
    gap: 30px;
    padding: 20px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid rgba(148, 163, 184, 0.2);
    border-radius: 12px;
    transition: all 0.3s ease;
  }

  .skill-row:hover {
    background: rgba(30, 41, 59, 0.8);
    border-color: rgba(255, 215, 0, 0.3);
  }

  .skill-info {
    flex: 0 0 200px;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .level-status {
    font-size: 12px;
    padding: 2px 8px;
    border-radius: 4px;
    margin-left: 10px;
  }

  .level-status.unlocked {
    background: rgba(34, 197, 94, 0.2);
    color: #22c55e;
    border: 1px solid rgba(34, 197, 94, 0.3);
  }

  .level-status.locked {
    background: rgba(239, 68, 68, 0.2);
    color: #ef4444;
    border: 1px solid rgba(239, 68, 68, 0.3);
  }

  .skill-node {
    position: absolute;
    transform: translate(-50%, -50%);
    left: var(--x);
    top: var(--y);
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    filter: drop-shadow(0 4px 15px rgba(0, 0, 0, 0.3));
  }

  .skill-node:hover {
    transform: translate(-50%, -50%) scale(1.1);
    z-index: 10;
  }

  .skill-node.selected {
    transform: translate(-50%, -50%) scale(1.15);
    z-index: 15;
  }

  .node-ring {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 3px solid rgba(148, 163, 184, 0.4);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s;
    pointer-events: none; /* avoid odd focus/hover effects */
  }

  .skill-node.unlocked .node-ring {
    border-color: rgba(255, 215, 0, 0.8);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.4);
  }

  .skill-node.maxed .node-ring {
    border-color: #ffd700;
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.6);
    animation: pulse 2s ease-in-out infinite;
  }

  .skill-node.selected .node-ring {
    border-color: #3b82f6;
    box-shadow: 0 0 25px rgba(59, 130, 246, 0.7);
  }

  .node-core {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: radial-gradient(circle at 30% 30%, rgba(71, 85, 105, 0.9), rgba(51, 65, 85, 0.95));
    border: 2px solid rgba(148, 163, 184, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
  }

  .skill-node.locked .node-core {
    background: radial-gradient(circle, rgba(55, 65, 81, 0.8), rgba(31, 41, 55, 0.9));
    opacity: 0.6;
  }

  .skill-node.unlocked .node-core {
    background: radial-gradient(circle at 30% 30%, rgba(255, 215, 0, 0.3), rgba(59, 130, 246, 0.2));
    border-color: rgba(255, 215, 0, 0.5);
  }

  .skill-node.maxed .node-core {
    background: radial-gradient(circle at 30% 30%, rgba(255, 215, 0, 0.5), rgba(59, 130, 246, 0.3));
    border-color: rgba(255, 215, 0, 0.8);
  }

  .node-core i {
    font-size: 24px;
    color: #94a3b8;
    transition: all 0.3s;
  }

  .skill-node.unlocked .node-core i {
    color: #ffd700;
    filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.5));
  }

  .skill-node.maxed .node-core i {
    color: #ffd700;
    filter: drop-shadow(0 0 10px rgba(255, 215, 0, 0.7));
  }

  .node-progress {
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
  }

  .progress-bar {
    width: 40px;
    height: 4px;
    background: rgba(148, 163, 184, 0.3);
    border-radius: 2px;
    overflow: hidden;
  }

  .progress-bar .fill {
    height: 100%;
    width: calc(var(--progress, 0) * 100%);
    background: linear-gradient(90deg, rgba(255, 215, 0, 0.8), #ffd700);
    border-radius: 2px;
    transition: width 0.5s ease;
  }

  .skill-node.maxed .progress-bar .fill {
    background: linear-gradient(90deg, #ffd700, #ffed4e);
  }

  .rank-text {
    font-size: 10px;
    color: #cbd5e1;
    font-weight: 600;
  }

  .skill-node.unlocked .rank-text {
    color: #ffd700;
  }

  .skill-node.maxed .rank-text {
