<script>
  import { onMount, onDestroy, tick } from "svelte";
  import { currentTheme, language } from "../../store";
  import { translate } from "../../lib/i18n";
  import { projectsData } from "../../data/projects";

  // Get current language value for reactive updates
  $: currentLang = $language;

  // Transform projects data based on current language
  $: projects = projectsData.map(p => ({
    ...p,
    title: typeof p.title === 'object' ? p.title[currentLang] : p.title,
    description: typeof p.description === 'object' ? p.description[currentLang] : p.description,
    longDescription: typeof p.longDescription === 'object' ? p.longDescription[currentLang] : p.longDescription,
  }));

  let searchTerm = "";
  let selectedCategory = "all";
  let selectedStatus = "all";
  let selectedProject = null; // per la modale dettagli

  $: categories = [
    { id: "all", label: $translate('projects.filter.all') },
    { id: "webapp", label: $translate('projects.filter.webapp') },
    { id: "giochi", label: $translate('projects.filter.games') },
    { id: "prototipi", label: $translate('projects.filter.prototypes') },
    { id: "altro", label: $translate('projects.filter.other') },
  ];

  $: statuses = [
    { id: "all", label: $translate('projects.status.all') },
    { id: "Completato", label: $translate('projects.status.completed') },
    { id: "In Sviluppo", label: $translate('projects.status.inProgress') },
    { id: "Prototipo", label: $translate('projects.status.prototype') },
  ];

  let modalEl;
  let prevFocus;
  let scrollY = 0;
  function lockScroll() {
    // preferisco nascondere lo scroll della radice per evitare jump/offset
    try {
      document.documentElement.style.overflow = "hidden";
    } catch (e) {}
  }
  function unlockScroll() {
    try {
      document.documentElement.style.overflow = "";
    } catch (e) {}
  }
  async function openProject(p) {
    prevFocus = document.activeElement;
    selectedProject = p;
    await tick();
    lockScroll();
    // centro gi con fixed, focus modale
    if (modalEl) {
      modalEl.focus();
    }
    window.addEventListener("keydown", handleKey);
  }
  function closeProject() {
    selectedProject = null;
    unlockScroll();
    window.removeEventListener("keydown", handleKey);
    if (prevFocus && prevFocus.focus) prevFocus.focus();
  }
  function handleKey(e) {
    if (e.key === "Escape") closeProject();
    if (e.key === "Tab" && modalEl) {
      // focus trap minimale
      const focusables = modalEl.querySelectorAll(
        'a[href], button, textarea, input, select, [tabindex]:not([tabindex="-1"])'
      );
      if (!focusables.length) return;
      const first = focusables[0];
      const last = focusables[focusables.length - 1];
      if (e.shiftKey && document.activeElement === first) {
        e.preventDefault();
        last.focus();
      } else if (!e.shiftKey && document.activeElement === last) {
        e.preventDefault();
        first.focus();
      }
    }
  }

  $: filteredProjects = projects.filter((p) => {
    const cat =
      selectedCategory === "all" || p.labCategory === selectedCategory;
    const st = selectedStatus === "all" || p.status === selectedStatus;
    const term = searchTerm.trim().toLowerCase();
    const match =
      !term ||
      p.title.toLowerCase().includes(term) ||
      p.description.toLowerCase().includes(term) ||
      p.technologies.some((t) => t.toLowerCase().includes(term));
    return cat && st && match;
  });

  $: featured = projects.filter((p) => p.featured);

  // Animazioni ingresso
  onMount(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((e) => {
          if (e.isIntersecting) {
            e.target.classList.add("in-view");
            observer.unobserve(e.target);
          }
        });
      },
      { threshold: 0.15 }
    );
    document
      .querySelectorAll(".lab-card")
      .forEach((el) => observer.observe(el));
  });
</script>

<div class="lab-wrapper" class:dark={$currentTheme === "dark"}>
  <!-- HERO / LAB INTRO -->
  <section class="lab-hero">
    <div class="hero-inner">
      <div class="hero-badge"><i class="fas fa-microscope"></i> LAB</div>
      <h1 class="lab-title">{$translate('projects.title')}</h1>
      <p class="lab-subtitle">
        {$translate('projects.subtitle')}
      </p>
      <div class="hero-meta">
        <span
          ><i class="fas fa-flask"></i>
          {projects.length} {currentLang === 'it' ? 'prototipi & build' : 'prototypes & builds'}</span
        >
        <span><i class="fas fa-layer-group"></i> {currentLang === 'it' ? 'Stack eterogeneo' : 'Heterogeneous stack'}</span>
        <span><i class="fas fa-bolt"></i> {currentLang === 'it' ? 'Iterazione continua' : 'Continuous iteration'}</span>
      </div>
    </div>
    <div class="hero-pattern" aria-hidden="true"></div>
  </section>

  <!-- FEATURED STRIP -->
  {#if featured.length}
    <section class="featured-strip">
      <div class="featured-chips">
        {#each featured as f}
          <div class="featured-chip" title="In evidenza">
            <i class="fas fa-star" aria-hidden="true"></i>
            {f.title}
          </div>
        {/each}
      </div>
    </section>
  {/if}

  <!-- FILTERS -->
  <section class="lab-filters">
    <div class="filters-bar">
      <div class="filter-group categories">
        {#each categories as c}
          <button
            type="button"
            class="panel-btn"
            class:active={selectedCategory === c.id}
            on:click={() => (selectedCategory = c.id)}
            aria-pressed={selectedCategory === c.id}
          >
            <i class="fas fa-circle-notch" aria-hidden="true"></i>
            {c.label}
          </button>
        {/each}
      </div>
      <div class="filter-group status">
        <select
          bind:value={selectedStatus}
          class="status-select"
          aria-label="Filtro stato"
        >
          {#each statuses as s}
            <option value={s.id}>{s.label}</option>
          {/each}
        </select>
      </div>
      <div class="filter-group search">
        <div class="search-shell">
          <i class="fas fa-search"></i>
          <input placeholder={$translate('projects.search')} bind:value={searchTerm} />
        </div>
      </div>
    </div>
  </section>

  <!-- GRID -->
  <section class="lab-grid">
    {#if filteredProjects.length === 0}
      <div class="empty-state">
        <i class="fas fa-search"></i>
        <h3>{currentLang === 'it' ? 'Nessun risultato' : 'No results'}</h3>
        <p>{currentLang === 'it' ? 'Regola filtri o ricerca per visualizzare altre postazioni.' : 'Adjust filters or search to view other items.'}</p>
      </div>
    {:else}
      <div class="pegboard" role="list" aria-label={$translate('projects.list.label')}>
        <div class="cards-grid">
          {#each filteredProjects as project}
            <div
              class="lab-card with-hooks {project.labCategory}"
              role="listitem"
              tabindex="0"
              aria-label={`Apri dettagli progetto ${project.title}`}
              on:click={() => openProject(project)}
              on:keydown={(e) =>
                (e.key === "Enter" || e.key === " ") && openProject(project)}
            >
              <div class="card-content">
                <header class="card-head">
                  <div class="card-title-group">
                    <span
                      class="status-chip status-{project.status
                        .replace(/\s/g, '')
                        .toLowerCase()}">{project.status}</span
                    >
                    <h3>{project.title}</h3>
                  </div>
                  <span class="year-chip"
                    ><i class="fas fa-calendar" aria-hidden="true"></i>
                    {project.year}</span
                  >
                </header>
                <p class="card-desc">{project.description}</p>
                <div class="card-meta">
                  <span class="meta-chip category"
                    ><i class="fas fa-flask"></i> {$translate(`projects.filter.${project.labCategory}`)}</span
                  >
                  <span class="meta-chip stack"
                    ><i class="fas fa-layer-group"></i>
                    {project.technologies.length} {$translate('projects.techCount')}</span
                  >
                </div>
                <div class="card-tags">
                  {#each project.technologies.slice(0, 4) as tech}
                    <span class="chip tech">{tech}</span>
                  {/each}
                  {#if project.technologies.length > 4}
                    <span class="chip tech more"
                      >+{project.technologies.length - 4}</span
                    >
                  {/if}
                </div>
                <div class="card-actions">
                  <button
                    class="mini-link details"
                    aria-label={$translate('projects.details')}
                    on:click|stopPropagation={() => openProject(project)}
                    ><i class="fas fa-eye"></i></button
                  >
                  {#if project.liveUrl}
                    <a
                      class="mini-link"
                      href={project.liveUrl}
                      target="_blank"
                      aria-label={$translate('projects.live')}
                      on:click|stopPropagation
                      ><i class="fas fa-external-link-alt"></i></a
                    >
                  {/if}
                  {#if project.githubUrl}
                    <a
                      class="mini-link"
                      href={project.githubUrl}
                      target="_blank"
                      aria-label={$translate('projects.github')}
                      on:click|stopPropagation><i class="fab fa-github"></i></a
                    >
                  {/if}
                </div>
              </div>
            </div>
          {/each}
        </div>
      </div>
    {/if}
  </section>

  <!-- MODAL DETTAGLI PROGETTO -->
  {#if selectedProject}
    <div class="project-modal-backdrop" on:click={closeProject}>
      <div
        class="project-modal"
        bind:this={modalEl}
        tabindex="-1"
        role="dialog"
        aria-modal="true"
        aria-label={selectedProject.title}
        on:click|stopPropagation
      >
        <button class="modal-close" aria-label={$translate('common.close')} on:click={closeProject}
          ><i class="fas fa-times"></i></button
        >
        <div class="modal-header">
          <div>
            <h2>{selectedProject.title}</h2>
            <div class="modal-meta">
              <span class="m-badge"
                ><i class="fas fa-calendar"></i> {selectedProject.year}</span
              ><span class="m-badge status"
                ><i class="fas fa-circle"></i> {selectedProject.status}</span
              >
            </div>
          </div>
        </div>
        <p class="modal-long">{selectedProject.longDescription}</p>
        <div class="modal-stack">
          {#each selectedProject.technologies as t}
            <span class="chip tech big">{t}</span>
          {/each}
        </div>
        <div class="modal-links">
          {#if selectedProject.liveUrl}
            <a
              href={selectedProject.liveUrl}
              target="_blank"
              class="modal-btn primary"
              ><i class="fas fa-external-link-alt"></i> {$translate('projects.live')}</a
            >
          {/if}
          {#if selectedProject.githubUrl}
            <a
              href={selectedProject.githubUrl}
              target="_blank"
              class="modal-btn"><i class="fab fa-github"></i> {$translate('projects.github')}</a
            >
          {/if}
        </div>
      </div>
    </div>
  {/if}
</div>

<style>
  @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@600;700;800&family=Lato:wght@400;500;700&family=Source+Code+Pro:wght@400;600&display=swap");

  :global(body) {
    --lab-bg: #f2f2ed;
    --lab-bg-alt: #e7e4dc;
    --lab-card: #ffffff;
    --lab-border: #d9d6cc;
    --lab-text: #1f2730;
    --lab-muted: #5a646f;
    --lab-accent: #d19a00;
    --lab-accent-soft: #f5ce62;
    --lab-accent-alt: #ffb347;
    --lab-shadow: 0 4px 12px -2px rgba(0, 0, 0, 0.06),
      0 2px 4px -1px rgba(0, 0, 0, 0.04);
    --lab-radius: 18px;
    --lab-radius-sm: 10px;
    --lab-gradient: linear-gradient(
      120deg,
      var(--lab-accent) 0%,
      var(--lab-accent-alt) 100%
    );
    font-family: "Lato", system-ui, sans-serif;
  }
  .dark :global(body),
  .dark {
    /* scoping if wrapper toggles */
  }
  .lab-wrapper.dark {
    --lab-bg: #121417;
    --lab-bg-alt: #1a1e23;
    --lab-card: #1e242b;
    --lab-border: #2c343c;
    --lab-text: #f4f6f7;
    --lab-muted: #98a1aa;
    --lab-shadow: 0 4px 14px -2px rgba(0, 0, 0, 0.55),
      0 2px 6px -1px rgba(0, 0, 0, 0.35);
  }

  .lab-wrapper {
    background: var(--lab-bg);
    color: var(--lab-text);
    min-height: 100vh;
    overflow-x: hidden;
  }
  .lab-hero {
    position: relative;
    padding: 23px 1.5rem 44px;
    text-align: center;
    isolation: isolate;
  }
  .lab-title {
    font-family: "Poppins", sans-serif;
    font-size: clamp(2.6rem, 5vw, 4.2rem);
    font-weight: 800;
    letter-spacing: 0.5px;
    background: var(--lab-gradient);
    -webkit-background-clip: text;
    color: transparent;
    margin: 0 0 1rem;
  }
  .lab-subtitle {
    font-size: 1.15rem;
    max-width: 720px;
    margin: 0 auto 1.3rem;
    color: var(--lab-muted);
  }
  .hero-badge {
    display: inline-flex;
    gap: 0.4rem;
    align-items: center;
    background: var(--lab-card);
    padding: 0.55rem 0.9rem;
    border: 1px solid var(--lab-border);
    border-radius: 999px;
    font-size: 0.75rem;
    font-weight: 600;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    box-shadow: var(--lab-shadow);
  }
  .hero-meta {
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 0.75rem;
    font-size: 0.85rem;
    color: var(--lab-muted);
  }
  .hero-meta span {
    display: inline-flex;
    align-items: center;
    gap: 0.4rem;
  }

  .hero-pattern {
    position: absolute;
    inset: 0;
    background: radial-gradient(
        circle at 15% 25%,
        rgba(255, 191, 0, 0.12),
        transparent 60%
      ),
      radial-gradient(
        circle at 80% 55%,
        rgba(255, 166, 0, 0.13),
        transparent 65%
      ),
      repeating-linear-gradient(
        45deg,
        rgba(120, 120, 120, 0.05) 0 12px,
        transparent 12px 24px
      );
    mask: linear-gradient(to bottom, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.2));
    z-index: -1;
  }
  .lab-wrapper.dark .hero-pattern {
    background: radial-gradient(
        circle at 15% 25%,
        rgba(255, 191, 0, 0.18),
        transparent 60%
      ),
      radial-gradient(
        circle at 80% 55%,
        rgba(255, 166, 0, 0.18),
        transparent 65%
      ),
      repeating-linear-gradient(
        45deg,
        rgba(255, 255, 255, 0.04) 0 12px,
        transparent 12px 24px
      );
  }

  .featured-strip {
    padding: 0.65rem 1rem;
    background: var(--lab-bg-alt);
    border-top: 1px solid var(--lab-border);
    border-bottom: 1px solid var(--lab-border);
  }
  .featured-chips {
    display: flex;
    gap: 0.65rem;
    flex-wrap: wrap;
    justify-content: center;
  }
  .featured-chip {
    background: var(--lab-card);
    border: 1px solid var(--lab-border);
    padding: 0.4rem 0.75rem;
    border-radius: 30px;
    font-size: 0.65rem;
    letter-spacing: 0.6px;
    display: inline-flex;
    align-items: center;
    gap: 0.4rem;
    font-weight: 600;
    color: var(--lab-muted);
  }
  .featured-chip i {
    color: var(--lab-accent);
  }

  .lab-filters {
    padding: 40px clamp(0.8rem, 2vw, 2rem) 20px;
  }
  .filters-bar {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: minmax(0, 1fr);
    align-items: start;
  }
  @media (min-width: 900px) {
    .filters-bar {
      grid-template-columns: 1fr auto auto auto;
    }
  }
  .filter-group.categories {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
  }
  .panel-btn {
    border: 1px solid var(--lab-border);
    background: var(--lab-card);
    padding: 0.55rem 0.95rem;
    font-size: 0.78rem;
    font-weight: 600;
    letter-spacing: 0.4px;
    border-radius: 12px;
    cursor: pointer;
    display: inline-flex;
    gap: 0.45rem;
    align-items: center;
    transition: 0.25s;
    box-shadow: var(--lab-shadow);
    color: var(--lab-muted);
  }
  .panel-btn:hover {
    background: var(--lab-bg-alt);
    color: var(--lab-text);
  }
  .panel-btn.active {
    background: var(--lab-accent);
    color: #222;
    border-color: var(--lab-accent);
    box-shadow:
      0 0 0 1px rgba(0, 0, 0, 0.04),
      0 4px 10px -4px rgba(0, 0, 0, 0.2);
  }
  .panel-btn.active i {
    color: #222;
  }

  .status-select {
    background: var(--lab-card);
    border: 1px solid var(--lab-border);
    border-radius: var(--lab-radius-sm);
    padding: 0.65rem 0.9rem;
    font-size: 0.75rem;
    font-weight: 600;
    letter-spacing: 0.5px;
    cursor: pointer;
    box-shadow: var(--lab-shadow);
    color: var(--lab-text);
  }
  .search-shell {
    position: relative;
    background: var(--lab-card);
    border: 1px solid var(--lab-border);
    padding: 0.55rem 0.9rem 0.55rem 2.1rem;
    border-radius: var(--lab-radius-sm);
    display: flex;
    align-items: center;
    gap: 0.5rem;
    box-shadow: var(--lab-shadow);
  }
  .search-shell i {
    position: absolute;
    left: 0.9rem;
    opacity: 0.55;
  }
  .search-shell input {
    border: none;
    outline: none;
    background: transparent;
    width: 100%;
    font-size: 0.85rem;
    font-weight: 500;
    color: var(--lab-text);
  }
  /* rimosso toggle viewmode */

  .lab-grid {
    padding: 8px clamp(0.6rem, 2vw, 1.6rem) 70px;
  }
  .cards-grid {
    --cols: 1;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(var(--cols), minmax(0, 1fr));
  }
  @media (min-width: 520px) {
    .cards-grid {
      --cols: 2;
    }
  }
  @media (min-width: 760px) {
    .cards-grid {
      --cols: 3;
    }
  }
  @media (min-width: 1024px) {
    .cards-grid {
      --cols: 4;
    }
  }
  @media (min-width: 1400px) {
    .cards-grid {
      --cols: 5;
    }
  }

  /* PEGBOARD (pannello attrezzi) */
  .pegboard {
    position: relative;
    padding: clamp(0.8rem, 1.8vw, 1.8rem);
    border: 1px solid var(--lab-border);
    border-radius: 26px;
    background:
      radial-gradient(
          circle at 10px 10px,
          rgba(0, 0, 0, 0.12) 2px,
          transparent 2.2px
        )
        0 0/48px 48px,
      linear-gradient(165deg, var(--lab-bg-alt), var(--lab-bg) 65%);
    box-shadow:
      inset 0 1px 2px rgba(255, 255, 255, 0.4),
      inset 0 -2px 4px rgba(0, 0, 0, 0.08);
    overflow: visible;
  }
  .lab-wrapper.dark .pegboard {
    background:
      radial-gradient(
          circle at 10px 10px,
          rgba(255, 255, 255, 0.07) 2px,
          transparent 2.4px
        )
        0 0/48px 48px,
      linear-gradient(155deg, #232a30, #171c21 65%);
    box-shadow:
      inset 0 1px 2px rgba(255, 255, 255, 0.06),
      inset 0 -2px 4px rgba(0, 0, 0, 0.55);
  }
  .pegboard:before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    pointer-events: none;
    background: linear-gradient(
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0) 50%
    );
    mix-blend-mode: overlay;
    opacity: 0.4;
  }
  .lab-wrapper.dark .pegboard:before {
    opacity: 0.2;
  }
  .pegboard .cards-grid {
    position: relative;
  }

  /* Hook / gancio stilizzato per ogni card */
  .lab-card.with-hooks {
    margin-top: 22px;
  }
  .lab-card.with-hooks:before {
    content: "";
    position: absolute;
    top: -22px;
    left: 50%;
    transform: translateX(-50%);
    width: 48px;
    height: 18px;
    background: linear-gradient(#e2dfd8, #cfcac2);
    border: 1px solid var(--lab-border);
    border-radius: 6px;
    box-shadow:
      0 2px 5px -2px rgba(0, 0, 0, 0.25),
      inset 0 1px 2px rgba(255, 255, 255, 0.65);
    display: block;
  }
  .lab-wrapper.dark .lab-card.with-hooks:before {
    background: linear-gradient(#414a52, #2d343a);
    box-shadow:
      0 2px 5px -2px rgba(0, 0, 0, 0.55),
      inset 0 1px 2px rgba(255, 255, 255, 0.07);
  }
  .lab-card.with-hooks:after {
    content: "";
    position: absolute;
    top: -32px;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 12px;
    background: linear-gradient(var(--lab-border), #b4b0a9);
    border-radius: 2px;
  }
  .lab-wrapper.dark .lab-card.with-hooks:after {
    background: linear-gradient(#545d65, #30383f);
  }
  .lab-card.with-hooks:before {
    background-image: radial-gradient(
        circle at 14px 8px,
        rgba(0, 0, 0, 0.28) 2.4px,
        transparent 2.5px
      ),
      radial-gradient(
        circle at 34px 8px,
        rgba(0, 0, 0, 0.28) 2.4px,
        transparent 2.5px
      ),
      linear-gradient(#e2dfd8, #cfcac2);
  }
  .lab-wrapper.dark .lab-card.with-hooks:before {
    background-image: radial-gradient(
        circle at 14px 8px,
        rgba(0, 0, 0, 0.55) 2.4px,
        transparent 2.5px
      ),
      radial-gradient(
        circle at 34px 8px,
        rgba(0, 0, 0, 0.55) 2.4px,
        transparent 2.5px
      ),
      linear-gradient(#414a52, #2d343a);
  }

  .lab-card {
    position: relative;
    cursor: pointer;
    background: var(--lab-card);
    border: 1px solid var(--lab-border);
    border-radius: 12px;
    box-shadow: var(--lab-shadow);
    transition: 0.2s ease;
    overflow: hidden;
  }
  .lab-card:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 22px -12px rgba(0, 0, 0, 0.35);
  }
  .lab-card.in-view {
    animation: pop-in 0.9s ease;
  }
  @keyframes pop-in {
    0% {
      opacity: 0;
      transform: translateY(40px);
    }
    70% {
