<script>
	import { segment, currentTheme, isLoading } from "./store";
	import { onMount } from "svelte";
	
	// Components
	import UpBar from "./components/UpBar.svelte";
	import Home from "./components/views/Home.svelte"; // legacy home (potremmo rimuoverla se non serve)
	import MapHome from "./components/views/MapHome.svelte"; // nuova home mappa
	import MiniMap from "./components/MiniMap.svelte";
	import About from "./components/views/About.svelte";
	import Projects from "./components/views/Projects.svelte";
	import Blog from "./components/views/Blog.svelte";
	import CV from "./components/views/CV.svelte";
	import Passions from "./components/views/Passions.svelte";
	import Contact from "./components/views/Contact.svelte";
	import Skills from "./components/views/Skills.svelte";

	onMount(async () => {
		// Initialize theme
		const savedTheme = localStorage.getItem('theme') || 'aurora';
		currentTheme.set(savedTheme);
		document.body.setAttribute('data-theme', savedTheme);
		
		// Set loading to false after initialization
		setTimeout(() => {
			isLoading.set(false);
		}, 500);
	});

	// Reactive theme update
	$: if ($currentTheme) {
		document.body.setAttribute('data-theme', $currentTheme);
		localStorage.setItem('theme', $currentTheme);
	}
</script>

<div id="maincontainer" class="theme-{$currentTheme}">
	{#if $isLoading}
		<div class="loading-screen">
			<div class="loading-spinner">
				<i class="fas fa-code fa-spin"></i>
			</div>
			<h2>Loading Portfolio...</h2>
		</div>
	{:else}
		<!-- <UpBar /> -->
		
		<main class="main-content">
			{#if $segment === 'home'}
				<MapHome />
			{:else if $segment === 'about'}
				<About />
			{:else if $segment === 'projects'}
				<Projects />
			{:else if $segment === 'blog'}
				<Blog />
			{:else if $segment === 'cv'}
				<CV />
			{:else if $segment === 'passions'}
				<Passions />
			{:else if $segment === 'skills'}
				<Skills />
			{:else if $segment === 'contact'}
				<Contact />
			{:else}
				<!-- Fallback to home if segment not recognized -->
				<Home />
			{/if}
			{#if $segment !== 'home'}
				<MiniMap />
			{/if}
		</main>
	{/if}
</div>

