<script>
  import { segment, islands, language } from "../../store";
  import { onMount } from "svelte";
  import { get } from "svelte/store";
  import { translate } from "../../lib/i18n";
  import anime from "animejs";
  let container;
  let canvas;
  let ctx;
  let activeCanvas;
  let activeCtx;
  let hoverAnim = null;
  let clickAnim = null;
  let hoverFrom = null;
  let hoverTo = null;
  let routeHistory = [];
  let resizeObserver;
  // mantiene l'isola di origine per le prossime rotte (ultima visitata)
  let originSegment = null;

  // Manteniamo il calcolo per garantire il posizionamento coerente delle isole sull'immagine cover
  let imgNatural = { width: 0, height: 0 };
  let coverGeom = { scale: 1, offsetX: 0, offsetY: 0, drawW: 0, drawH: 0 };

  function computeCoverGeometry() {
    if (!container || !imgNatural.width || !imgNatural.height) return;
    const rect = container.getBoundingClientRect();
    const iw = imgNatural.width;
    const ih = imgNatural.height;
    const scale = Math.max(rect.width / iw, rect.height / ih);
    const drawW = iw * scale;
    const drawH = ih * scale;
    const offsetX = (rect.width - drawW) / 2;
    const offsetY = (rect.height - drawH) / 2;
    coverGeom = { scale, offsetX, offsetY, drawW, drawH };
  }

  function imagePercentToPx(xPct, yPct) {
    const { offsetX, offsetY, drawW, drawH } = coverGeom;
    const x = offsetX + (xPct / 100) * drawW;
    const y = offsetY + (yPct / 100) * drawH;
    return { x, y };
  }

  function getIslandStyle(island) {
    if (!imgNatural.width) return `left:${island.x}%;top:${island.y}%;`;
    const p = imagePercentToPx(island.x, island.y);
    return `left:${p.x}px;top:${p.y}px;`;
  }

  // Disegna tutte le rotte (curve) tra ogni coppia di isole
  function drawAllRoutes() {
    if (!canvas || !container) return;
    const rect = container.getBoundingClientRect();
    canvas.width = rect.width;
    canvas.height = rect.height;
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    // linee pi spesse per essere visibili sulla mappa
    ctx.lineWidth = 3.5;
    ctx.lineCap = "round";
    ctx.lineJoin = "round";

    const islandRadius = 36; // piccolo offset per non toccare il centro dell'isola
    const verticalLift = 20; // solleva punti di partenza/arrivo per allineare meglio le curve

    for (let i = 0; i < islands.length; i++) {
      for (let j = 0; j < islands.length; j++) {
        if (i === j) continue;
        const from = islands[i];
        const to = islands[j];
        const fpx = imagePercentToPx(from.x, from.y);
        const tpx = imagePercentToPx(to.x, to.y);
        const fx = fpx.x,
          fy = fpx.y;
        const tx = tpx.x,
          ty = tpx.y;

        // vettore e distanza
        const dx = tx - fx;
        const dy = ty - fy;
        const dist = Math.hypot(dx, dy) || 1;
        const ux = dx / dist;
        const uy = dy / dist;

        // start / end punti leggermente fuori dall'isola
        let sx = fx + ux * islandRadius;
        let sy = fy + uy * islandRadius;
        let ex = tx - ux * islandRadius;
        let ey = ty - uy * islandRadius;
        // solleva i punti di partenza/arrivo di qualche pixel
        sy -= verticalLift;
        ey -= verticalLift;

        // punto medio e offset per la curvatura (perpendicolare)
        const mx = (sx + ex) / 2;
        const my = (sy + ey) / 2;
        const px = -uy; // perpendicolare normalizzato
        const py = ux;
        // alterniamo direzione in modo deterministico per ridurre sovrapposizioni
        const sign = (i * islands.length + j) % 2 === 0 ? 1 : -1;
        const offset = Math.min(220, dist * 0.35) * sign;
        const cx = mx + px * offset;
        const cy = my + py * offset;

        // colore marrone scuro molto attenuato per lo sfondo delle rotte
        const alpha = 0.06;
        ctx.strokeStyle = `rgba(74,43,0,${alpha})`; // #4a2b00

        ctx.beginPath();
        ctx.moveTo(sx, sy);
        ctx.quadraticCurveTo(cx, cy, ex, ey);
        ctx.stroke();
      }
    }
  }

  function handleIslandClick(island) {
  // anima la rotta dall'ultima isola visitata (originSegment) se presente, altrimenti dal segmento corrente
  const fromSegment = originSegment || get(segment);
    const toSegment = island.segment;

    // interrompi hover se presente
    if (hoverAnim) {
      hoverAnim.pause();
      hoverAnim = null;
      hoverFrom = hoverTo = null;
      clearActive();
    }

    const fromIsland = islands.find((i) => i.segment === fromSegment);
    if (!fromIsland || fromSegment === toSegment) {
      // Se non c' isola di partenza o siamo gi sulla stessa isola, naviga direttamente
      segment.set(toSegment);
      saveToHistory(toSegment);
      originSegment = toSegment;
      return;
    }

    // esegui animazione della singola rotta
    startRouteAnimation(fromIsland, island).then(() => {
      // una volta completata, imposta il segmento e salva nella history
      segment.set(toSegment);
      saveToHistory(toSegment);
  originSegment = toSegment;
      // disegna la history statica
      drawHistoryRoutes();
    });
  }

  function saveToHistory(seg) {
    routeHistory.push(seg);
    // mantenere ordine e rimuovere duplicati consecutivi
    routeHistory = routeHistory.filter(
      (v, i) => i === 0 || v !== routeHistory[i - 1]
    );
    localStorage.setItem("routeHistory", JSON.stringify(routeHistory));
    // persisti ultimo segmento visitato per ripristinare lo stato all reload
    try {
      localStorage.setItem("lastSegment", seg);
    } catch (e) {}
  }

  function loadHistory() {
    try {
      const raw = localStorage.getItem("routeHistory");
      if (raw) routeHistory = JSON.parse(raw);
    } catch (e) {
      routeHistory = [];
    }
    if (!routeHistory || !routeHistory.length) routeHistory = [get(segment)];
    // ripristina solo origine senza cambiare il segmento (per non navigare via da questa pagina)
    try {
      const lastSeg = localStorage.getItem("lastSegment");
      if (lastSeg && islands.find(i => i.segment === lastSeg)) {
        originSegment = lastSeg;
        const lastInHistory = routeHistory[routeHistory.length - 1];
        if (lastInHistory !== lastSeg) {
          routeHistory.push(lastSeg);
          localStorage.setItem("routeHistory", JSON.stringify(routeHistory));
        }
      } else {
        originSegment = routeHistory[routeHistory.length - 1];
      }
    } catch (e) {
      originSegment = routeHistory[routeHistory.length - 1];
    }
  }

  function clearActive() {
    if (!activeCtx || !activeCanvas) return;
  activeCtx.clearRect(0, 0, activeCanvas.width, activeCanvas.height);
  // ridisegna la history se vogliamo che resti visibile dopo l'uscita dall'hover
  drawHistoryRoutes(false);
  }

  function drawDashedRoute(fromIsland, toIsland, alpha = 0.9) {
    if (!activeCtx || !imgNatural.width) return;
    const rect = container.getBoundingClientRect();
    activeCanvas.width = rect.width;
    activeCanvas.height = rect.height;
  activeCtx.clearRect(0, 0, activeCanvas.width, activeCanvas.height);
  // mantieni le vecchie rotte visibili
  drawHistoryRoutes(false);
    activeCtx.lineWidth = 3.5;
    activeCtx.lineCap = "round";
    activeCtx.lineJoin = "round";
    activeCtx.setLineDash([10, 8]);
    activeCtx.strokeStyle = `rgba(74,43,0,${alpha})`;

    const fpx = imagePercentToPx(fromIsland.x, fromIsland.y);
    const tpx = imagePercentToPx(toIsland.x, toIsland.y);
    const fx = fpx.x,
      fy = fpx.y,
      tx = tpx.x,
      ty = tpx.y;
    const dx = tx - fx;
    const dy = ty - fy;
    const dist = Math.hypot(dx, dy) || 1;
    const ux = dx / dist;
    const uy = dy / dist;
    const islandRadius = 36;
    const verticalLift = 20;
    let sx = fx + ux * islandRadius;
    let sy = fy + uy * islandRadius;
    let ex = tx - ux * islandRadius;
    let ey = ty - uy * islandRadius;
    sy -= verticalLift;
    ey -= verticalLift;
    const mx = (sx + ex) / 2;
    const my = (sy + ey) / 2;
    const px = -uy;
    const py = ux;
    const sign = 1;
    const offset = Math.min(220, dist * 0.35) * sign;
    const cx = mx + px * offset;
    const cy = my + py * offset;

    activeCtx.beginPath();
    activeCtx.moveTo(sx, sy);
    activeCtx.quadraticCurveTo(cx, cy, ex, ey);
    activeCtx.stroke();
  }

  function sampleQuad(p0, p1, p2, t) {
    const x = (1 - t) * (1 - t) * p0.x + 2 * (1 - t) * t * p1.x + t * t * p2.x;
    const y = (1 - t) * (1 - t) * p0.y + 2 * (1 - t) * t * p1.y + t * t * p2.y;
    return { x, y };
  }

  function drawPartialRoute(fromIsland, toIsland, progress) {
    if (!activeCtx || !imgNatural.width) return;
    const rect = container.getBoundingClientRect();
    activeCanvas.width = rect.width;
    activeCanvas.height = rect.height;
  activeCtx.clearRect(0, 0, activeCanvas.width, activeCanvas.height);
  // mantieni history
  drawHistoryRoutes(false);
    activeCtx.lineWidth = 4;
    activeCtx.lineCap = "round";
    activeCtx.lineJoin = "round";
    activeCtx.setLineDash([]);
    activeCtx.strokeStyle = `rgba(74,43,0,1)`;

    const fpx = imagePercentToPx(fromIsland.x, fromIsland.y);
    const tpx = imagePercentToPx(toIsland.x, toIsland.y);
    const fx = fpx.x,
      fy = fpx.y,
      tx = tpx.x,
      ty = tpx.y;
    const dx = tx - fx;
    const dy = ty - fy;
    const dist = Math.hypot(dx, dy) || 1;
    const ux = dx / dist;
    const uy = dy / dist;
    const islandRadius = 36;
    const verticalLift = 20;
    let sx = fx + ux * islandRadius;
    let sy = fy + uy * islandRadius;
    let ex = tx - ux * islandRadius;
    let ey = ty - uy * islandRadius;
    sy -= verticalLift;
    ey -= verticalLift;
    const mx = (sx + ex) / 2;
    const my = (sy + ey) / 2;
    const px = -uy;
    const py = ux;
    const sign = 1;
    const offset = Math.min(220, dist * 0.35) * sign;
    const cx = mx + px * offset;
    const cy = my + py * offset;

    // sample curve up to progress
    const p0 = { x: sx, y: sy },
      p1 = { x: cx, y: cy },
      p2 = { x: ex, y: ey };
    const steps = Math.max(12, Math.ceil(dist / 6));
    activeCtx.beginPath();
    const endT = progress;
    const stepT = endT / steps;
    let first = true;
    for (let s = 0; s <= steps; s++) {
      const t = Math.min(endT, s * stepT);
      const pt = sampleQuad(p0, p1, p2, t);
      if (first) {
        activeCtx.moveTo(pt.x, pt.y);
        first = false;
      } else activeCtx.lineTo(pt.x, pt.y);
    }
    activeCtx.stroke();
  }

  async function startRouteAnimation(fromIsland, toIsland) {
    return new Promise((resolve) => {
      if (clickAnim) clickAnim.pause();
      const animObj = { t: 0 };
      // Timeline anime v3: prima disegno progressivo la rotta, pronto per eventuali step futuri
      clickAnim = anime
        .timeline({
          targets: animObj,
          autoplay: true,
        })
        .add({
          t: 1,
          duration: 1000,
          easing: "easeInOutSine",
          update: () => {
            drawPartialRoute(fromIsland, toIsland, animObj.t);
          },
          complete: () => {
            clickAnim = null;
            resolve();
          },
        });
    });
  }

  function drawHistoryRoutes(clear = true) {
    if (!activeCtx || !imgNatural.width) return;
    const rect = container.getBoundingClientRect();
    activeCanvas.width = rect.width;
    activeCanvas.height = rect.height;
    if (clear) activeCtx.clearRect(0, 0, activeCanvas.width, activeCanvas.height);
    if (!routeHistory || routeHistory.length < 2) return;
    activeCtx.lineWidth = 3.5;
    activeCtx.lineCap = "round";
    activeCtx.lineJoin = "round";
    activeCtx.setLineDash([]);
    activeCtx.strokeStyle = `rgba(74,43,0,0.95)`;
    for (let i = 0; i < routeHistory.length - 1; i++) {
      const fromSeg = routeHistory[i];
      const toSeg = routeHistory[i + 1];
      const fromIsland = islands.find((it) => it.segment === fromSeg);
      const toIsland = islands.find((it) => it.segment === toSeg);
      if (!fromIsland || !toIsland) continue;
      // compute curve points
      const fpx = imagePercentToPx(fromIsland.x, fromIsland.y);
      const tpx = imagePercentToPx(toIsland.x, toIsland.y);
      const fx = fpx.x,
        fy = fpx.y,
        tx = tpx.x,
        ty = tpx.y;
      const dx = tx - fx;
      const dy = ty - fy;
      const dist = Math.hypot(dx, dy) || 1;
      const ux = dx / dist;
      const uy = dy / dist;
      const islandRadius = 36;
      const verticalLift = 20;
      let sx = fx + ux * islandRadius;
      let sy = fy + uy * islandRadius;
      let ex = tx - ux * islandRadius;
      let ey = ty - uy * islandRadius;
      sy -= verticalLift;
      ey -= verticalLift;
      const mx = (sx + ex) / 2;
      const my = (sy + ey) / 2;
      const px = -uy;
      const py = ux;
      const offset = Math.min(220, dist * 0.35);
      const cx = mx + px * offset;
      const cy = my + py * offset;
      activeCtx.beginPath();
      activeCtx.moveTo(sx, sy);
      activeCtx.quadraticCurveTo(cx, cy, ex, ey);
      activeCtx.stroke();
    }
  }

  onMount(() => {
    // canvas per le rotte
    canvas = document.createElement("canvas");
    canvas.className = "route-canvas";
    canvas.setAttribute("aria-hidden", "true");
    canvas.style.pointerEvents = "none";
    canvas.style.position = "absolute";
    container.appendChild(canvas);
    ctx = canvas.getContext("2d");

    // canvas attivo per hover / animazioni
    activeCanvas = document.createElement("canvas");
    activeCanvas.className = "active-canvas";
    activeCanvas.setAttribute("aria-hidden", "true");
    activeCanvas.style.pointerEvents = "none";
    activeCanvas.style.position = "absolute";
    container.appendChild(activeCanvas);
    activeCtx = activeCanvas.getContext("2d");

    const img = new Image();
    img.src = "/images/grand-line.png";
    img.onload = () => {
      imgNatural.width = img.naturalWidth;
      imgNatural.height = img.naturalHeight;
      computeCoverGeometry();
      drawAllRoutes();
      loadHistory();
      drawHistoryRoutes();
    };

    resizeObserver = new ResizeObserver(() => {
      computeCoverGeometry();
      drawAllRoutes();
      drawHistoryRoutes();
    });
    resizeObserver.observe(container);
  });

  $: {
    computeCoverGeometry();
    drawAllRoutes();
    drawHistoryRoutes();
  }
</script>

<div class="map-wrapper" bind:this={container}>
  <h1 class="map-title">{$translate('map.title')}</h1>
  <div class="map-bg">
    <!-- Placeholder background; l'utente sostituir con immagine mappa One Piece -->
    <div class="placeholder-text">
      Inserisci qui la mappa One Piece come background CSS
    </div>
  </div>

  {#each islands as island}
    <button
      class="island {island.segment === (originSegment || $segment) ? 'active' : ''}"
      style={getIslandStyle(island)}
      on:click={() => handleIslandClick(island)}
      on:mouseenter={() => {
        const fromSeg = originSegment || get(segment);
        const fromIsland = islands.find((i) => i.segment === fromSeg);
        if (!fromIsland) return;
        hoverFrom = fromIsland;
        hoverTo = island;
        drawDashedRoute(fromIsland, island, 0.9);
        // start blink animation
        if (hoverAnim) hoverAnim.pause();
        const animObj = { a: 0.25 };
        hoverAnim = anime({
          targets: animObj,
            a: [0.25, 0.95],
            duration: 700,
            direction: "alternate",
            loop: true,
            easing: "linear",
            update: () => {
              if (hoverFrom && hoverTo) {
                drawDashedRoute(hoverFrom, hoverTo, animObj.a);
              }
            },
        });
      }}
      on:mouseleave={() => {
        if (hoverAnim) {
          hoverAnim.pause();
          hoverAnim = null;
        }
        hoverFrom = hoverTo = null;
        clearActive();
      }}
      aria-label={island.label}
    >
      <span class="icon-wrapper"><i class="fas {island.icon}"></i></span>
      <span class="label">{island.label}</span>
    </button>
  {/each}

  <section class="intro-panel" aria-labelledby="site-title">
    <div class="panel-header">
      <div>
        <h1 id="site-title">Giovanni Fragal</h1>
        <h2 class="subtitle">{$translate('about.role')}</h2>
      </div>
      <div class="lang-selector">
        <button 
          class="lang-btn" 
          class:active={$language === 'it'}
          on:click={() => language.set('it')}
          aria-label="Italiano"
        >
          
        </button>
        <button 
          class="lang-btn" 
          class:active={$language === 'en'}
          on:click={() => language.set('en')}
          aria-label="English"
        >
          
        </button>
      </div>
    </div>
    <p class="bio">{$translate('map.welcome')}</p>
    <p>
      {$translate('map.intro')}
    </p>
    <div class="mini-info">
      <div class="row">
        <span class="label">{$translate('map.location')}</span><span>Catania, IT</span>
      </div>
      <div class="row">
        <span class="label">{$translate('map.experience')}</span><span>{$translate('map.experience.value')}</span>
      </div>
      <div class="row">
        <span class="label">{$translate('map.stack')}</span><span>Node  Svelte  Docker</span>
      </div>
      <div class="row">
        <span class="label">{$translate('map.availability')}</span><span
          >{$translate('map.availability.value')}</span
        >
      </div>
      <div class="socials" aria-label="social links">
        <a href="https://github.com/giovannifragala" class="icon-link" aria-label="GitHub"
          ><i class="fab fa-github"></i></a
        >
        <a href="https://www.linkedin.com/in/giovannifragal%C3%A0/" class="icon-link" aria-label="LinkedIn"
          ><i class="fab fa-linkedin"></i></a
        >
        <a href="mailto:giovannifragala@gmail.com" class="icon-link" aria-label="Email"
          ><i class="fas fa-envelope"></i></a
        >
      </div>
    </div>
  </section>
</div>

<style>
  .map-title {
    position: fixed;
    right: 20px;
    top: 16px;
    z-index: 10;
    color: #fff;
    font-size: 2rem;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
    font-weight: 600;;
  }
  .map-wrapper {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: #0b2035;
  }
  .map-bg {
    position: absolute;
    inset: 0;
    background-image: url("/images/grand-line.png");
    filter: saturate(1.1) brightness(0.9);
    background-size: cover;
  }
  .placeholder-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff5;
    font-size: 1rem;
    font-family: monospace;
  }
  .route-canvas {
    position: absolute;
    inset: 0;
    z-index: 1;
  }
  .active-canvas {
    position: absolute;
    inset: 0;
    z-index: 3;
    pointer-events: none;
  }
  /* Rimossa canvas rotta */
  .island {
    position: absolute;
    transform: translate(-50%, -50%);
    z-index: 5;
    background: radial-gradient(circle at 30% 30%, #ffe9b3, #d4a757);
    border: 2px solid #5b3d00;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.3s;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
  }
  .island .icon-wrapper {
    font-size: 1.1rem;
    color: #4a2b00;
  }
  .island .label {
    font-size: 0.65rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    margin-top: 2px;
    color: #3a2400;
    font-weight: 700;
  }
  .island:hover {
    transform: translate(-50%, -50%) scale(1.1);
    z-index: 5;
  }
  .island.active {
    outline: 3px solid #fff8;
    box-shadow:
      0 0 0 3px #ffc10755,
      0 4px 15px rgba(0, 0, 0, 0.6);
    animation: pulse 1.2s infinite;
  }
  @keyframes pulse {
    0%,
    100% {
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.6);
    }
    50% {
      box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
    }
  }
  .intro-panel {
    position: absolute;
    top: 16px;
    left: 16px;
    max-width: 28vw;
    background: rgba(15, 23, 42, 0.78);
    padding: 18px 20px 16px;
    border: 1px solid #ffffff22;
    border-radius: 14px;
    backdrop-filter: blur(8px) saturate(1.2);
    color: #eaf6ff;
    font-size: 0.85rem;
    line-height: 1.35;
  }
  .panel-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 12px;
    margin-bottom: 8px;
  }
  .lang-selector {
    display: flex;
    gap: 6px;
    flex-shrink: 0;
  }
  .lang-btn {
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 8px;
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    cursor: pointer;
    transition: all 0.25s ease;
    padding: 0;
  }
  .lang-btn:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: scale(1.05);
  }
  .lang-btn.active {
    background: rgba(255, 193, 7, 0.25);
    border-color: rgba(255, 193, 7, 0.5);
    box-shadow: 0 0 12px rgba(255, 193, 7, 0.3);
  }
  .intro-panel h1 {
    margin: 0 0 4px;
    font-size: 1.35rem;
    letter-spacing: 0.5px;
    background: linear-gradient(120deg, #ffd089, #ff9a4f 40%, #ffce6a);
    -webkit-background-clip: text;
    color: transparent;
    font-weight: 700;
  }
  .intro-panel .subtitle {
    margin: 0 0 10px;
    font-size: 0.8rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 600;
    color: #ffe2b7;
  }
  .intro-panel .bio {
    margin: 0 0 12px;
  }
  .quick-links {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
  }
  .mini-info {
    margin-top: 12px;
    background: rgba(255, 255, 255, 0.03);
    padding: 10px;
